.lk-widjet.--list-clients
  .widjet-list-orders__link
    padding-left: 21px
  .widjet-list-orders__item 
    margin-bottom: 16px
    padding-bottom: 16px
    &:last-child
      margin-bottom: 0
      padding-bottom: 0

.widjet-dcol-list
  margin: 0
  padding: 0
  list-style: none

  &__item
    display: block
    position: relative
    margin-bottom: 12px
    padding-bottom: 12px
    border-bottom: 1px solid #F8F8F8
    &:last-child
      padding-bottom: 0
      margin-bottom: 0
      border-bottom: 0
  &__link
    display: flex
    justify-content: space-between
    text-decoration: none!important
    +h
      .widjet-dcol-list__title
        color: #FFAE00
      .widjet-dcol-list__product-top
        color: #FFAE00

  &__left
    display: flex
    flex-direction: column
  &__title
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 5px
    +tr(.25s)
  &__number
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.04em
    color: #B4BACA
  &__right
    display: flex
    align-items: center
  &__product
    display: flex
    flex-direction: column
    align-items: flex-end
    margin-right: 15px
  &__product-top
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 5px
    +tr(.25s)
  &__product-number
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #B4BACA
    &.--red
      color: #FF6A6A
  &__icon
    color: #FFAE00
    font-size: 30px
  &__button
    background: #F8F9FA
    border-radius: 50%
    color: #FFAE00
    width: 30px
    height: 30px
    font-size: 13px
    display: flex
    align-items: center
    justify-content: center
    font-size: 19px

.lk-page__products
  border-top: 1px solid #E1E3EA
  margin-top: 30px
  padding-top: 30px

  .product-card__box
    min-height: 0

.lk-manager-index
  .widjet-dcol-list__left
    +r(568)
      min-width: 95px
  .widjet-dcol-list__product-top
    +r(568)
      text-align: right