.upload-unit
  display: flex
  align-items: center
  position: relative
  &__label
    display: flex
    align-items: center
    padding-top: 10px
    padding-bottom: 10px
    cursor: pointer
    +h
      .upload-unit__icon-down
        background-color: #628EFF
        color: #fff
  &__input
    position: absolute
    visibility: hidden
    opacity: 0
    pointer-events: none
  &__icon
    display: inline-block
    position: relative
  &__icon-file
    display: flex
    justify-content: center
    align-items: center
    width: 55px
    height: 55px
    background: rgba(204, 210, 227, 0.5)
    border-radius: 8px
    font-size: 30px
    color: #fff
    [class*='ico_']
      text-align: center
  &__status
    display: flex
    justify-content: center
    align-items: center
    width: 35px
    height: 35px
    background: #FFFFFF
    border-radius: 8px
    box-shadow: 0px 2px 8px 0 rgba(204, 210, 227, 0.6)
    position: absolute
    right: -9px
    bottom: -9px
    z-index: 2
    color: #628EFF
    +tr(0.3s)
    &.success
      background: #73CD9B
      color: #fff
      font-size: 20px
    &.cancel
      background: #fff
      color: #FA8A96
  &__trash
    display: flex
    justify-content: center
    align-items: center
    width: 35px
    height: 35px
    background: #FFFFFF
    border-radius: 8px
    box-shadow: 0px 2px 8px 0 rgba(204, 210, 227, 0.6)
    position: absolute
    right: -9px
    bottom: -9px
    z-index: 2
    color: #FF6A6A
    cursor: pointer
    +tr(0.3s)
    +h
      background-color: #FF6A6A
      color: #fff
  &__info
    display: block
    position: relative
    padding-left: 20px
  &__title
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    +line(1)
  &__size
    font-style: normal
    font-weight: 500
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #A3AED0
    margin-top: 6px

.upload-unit.--small
  width: 100%
  background: #F8F9FA
  height: 45px
  padding: 0 15px
  margin-bottom: 3px
  .upload-unit__label
    width: 100%
    padding: 0
  .upload-unit__info
    width: 100%
    display: flex
    justify-content: space-between
    align-items: center
    padding: 0
    background: #F8F9FA
    border-radius: 6px
  .upload-unit__title
    max-width: calc(100% - 35px)
  .upload-unit__trash
    width: 25px
    height: 25px
    position: static
    background: none
    box-shadow: none
    color: #FF6A6A
    font-size: 12px
    opacity: 0.5
    margin-left: 10px
    +h
      opacity: 1

.upload-file-block
  display: block
  position: relative
  &.--full
    border-top: 1px solid #E1E3EA
    padding-top: 30px
    .upload-file-block__box
      margin-bottom: 30px
    .upload-file-block__btn
      padding-right: 100px
      +r(767)
        padding-right: 0
        margin-bottom: 20px
        justify-content: flex-start
    .upload-file-block__label-content
      +ico()
        display: flex
        justify-content: center
        align-items: center
        font-size: 18px
        margin-right: 15px
        width: 35px
        height: 35px
        border-radius: 6px
        color: #fff
        background-color: $accent
  &__box
    position: relative
    width: 100%
    margin-top: 15px
    &:empty
      margin-top: 0
  &__form
    display: flex
    align-items: flex-start
    +r(767)
      display: block
  &__textarea
    flex-grow: 1
  &__btn
    display: flex
    justify-content: center
    align-items: center
    position: relative
    height: 45px
    border: 1.5px dashed #FFAE00
    border-radius: 30px
  &__label
    display: flex
    justify-content: center
    align-items: center
    cursor: pointer
  &__input
    display: none
  &__label-content
    display: flex
    justify-content: center
    align-items: center
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B

    +ico()
      display: flex
      justify-content: center
      align-items: center
      font-size: 18px
      margin-right: 15px
      color: $accent
      +tr(0.25s)

  +h
    .upload-file-block__btn
      border: 1px dashed $accent

  &__bottom
    margin-top: 30px
    text-align: right
    +r(767)
      margin-top: 20px
      text-align: center
    +r(413)
      .button
        width: 100%

.file-upload
  .file-upload-text
    display: block
    +line(1)
    max-width: 540px