.button-modal-all
  display: flex
  justify-content: center
  align-items: center
  position: fixed
  left: 10px
  bottom: 10px
  z-index: 9
  width: 50px
  height: 50px
  padding: 0
  min-width: 50px
  border: 1px solid $accent
  background-color: $accent
  color: #fff
  +ico()
    margin: 0!important
    font-size: 16px

  +r(767)
    width: 40px
    height: 40px
    min-width: 40px

@media (min-width: 576px)
  .modal-dialog
    max-width: 400px
    margin: 30px auto
    &.modal-xl
      max-width: 1200px
    &.modal-lg
      max-width: 800px

.modal-content
  position: relative
  display: flex
  flex-direction: column
  width: 100%
  pointer-events: auto
  background-color: #fff
  background-clip: padding-box
  border: none
  border-radius: 30px
  outline: 0

.modal-dialog-scrollable
  .modal-body
    scrollbar-color: $accent #E2EBEC
    scrollbar-width: thin!important
    -moz-appearance: none !important

    &::-webkit-scrollbar
      width: 4px

    &::-webkit-scrollbar-track
      background-color: #E2EBEC

    &::-webkit-scrollbar-thumb
      background-color: $accent

.modal-header
  padding: 45px 50px 0
  border: none
  display: block
  position: relative

  +r(767)
    padding: 45px 17px 0

  .btn-close
    display: flex
    justify-content: center
    align-items: center
    width: 30px
    height: 30px
    flex: 0 0 30px
    padding: 0
    position: absolute
    top: 20px
    right: 20px

.modal-title
  display: block
  position: relative
  font-style: normal
  font-weight: 700
  font-size: 20px
  line-height: 100%
  letter-spacing: 0.02em
  color: #3C3C3B
  small
    display: block
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.2em
    color: #3C3C3B
    margin-top: 4px
  &::before
    content: ''
    display: block
    width: 40px
    height: 12px
    background: #FFAE00
    border-radius: 0px 60px
    margin-bottom: 12px

.modal-body
  padding: 30px 50px 50px
  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 130%
  letter-spacing: 0.02em
  color: #3C3C3B

  +r(767)
    padding: 30px 17px 20px

  .container-fluid
    +r(767)
      padding: 0

  .form-group
    margin-bottom: 15px
    &:last-child
      margin-top: 30px
      margin-bottom: 0

    .drop
      width: 100%
      display: flex

    .drop-input
      height: 45px
      line-height: 45px

    .text-lead
      font-style: normal
      font-weight: 400
      font-size: 16px
      line-height: 130%
      letter-spacing: 0.02em
      color: #3C3C3B

  .button,
  .button-accent,
  .button-outline
    height: 45px

.login-btns-group
  display: flex
  justify-content: space-between
  align-items: center
  margin-top: 10px
  button
    display: flex
    align-items: center
    justify-content: center
    width: calc( 50% - 3px )
    padding: 0 8px
    height: 40px
    background: #F8F9FA
    border-radius: 30px
    border: none
    font-style: normal
    font-weight: 700
    font-size: 14px
    line-height: 100%
    color: #B4BACA
    +tr(0.25s)
    &:before
      margin-right: 6px
      font-size: 20px
    +h
      color: rgba(135, 140, 152, 1)

.links-group
  display: flex
  justify-content: center
  align-items: center
  margin-top: 30px
  a
    display: inline-block
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 15px
    letter-spacing: 0.02em
    color: #3C3C3B
    +tr(0.25s)
    +h
      color: $accent
    & + a
      margin-left: 10px
      padding-left: 10px
      border-left: 1px solid #E1E3EA

.password-quality
  display: flex
  justify-content: flex-start
  align-items: center
  &__title
    display: inline-block
    margin-right: 10px
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
  &__list
    display: flex
    justify-content: space-between
    align-items: center
    flex-grow: 1
    list-style-type: none
    padding: 0
    margin: 0
    background: #F0F1F4
    border-radius: 8px
    overflow: hidden
    & > li
      width: 20%
      height: 12px
      &._active
        background: #57CA70

.m-contacts
  .modal-dialog
    max-width: 575px
  .modal-content
    overflow: hidden
    &::before
      content: ''
      display: block
      position: absolute
      left: 0
      bottom: 0
      width: 50%
      max-width: 175px
      height: 225px
      background: #F0F1F4

  &__location
    display: flex
    align-items: center
    margin-top: 12px
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    +ico()
      color: $accent
      font-size: 15px

.contacts-user-card
  display: block
  position: relative
  padding: 30px
  background: #FFFFFF
  box-shadow: 0px 20px 20px -20px rgba(146, 157, 165, 0.3)
  border-radius: 30px
  &__box
    text-align: center
  &__avatar
    display: inline-block
    width: 98px
    height: 98px
    flex: 0 0 98px
    border-radius: 50%
    overflow: hidden
    margin-bottom: 10px
    img
      max-width: 100%
      display: block
  &__name
    font-style: normal
    font-weight: 700
    font-size: 14px
    line-height: 120%
    letter-spacing: 0.02em
    color: #3C3C3B
  &__position
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 120%
    letter-spacing: 0.02em
    color: rgba(180, 186, 202, 1)
  &__line
    display: inline-block
    margin: 10px 0 15px
    width: 98px
    height: 1px
    background: #E1E3EA
  &__links
    a
      display: block
      position: relative
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 100%
      letter-spacing: 0.04em
      color: #3C3C3B
      +h
        color: $accent
  &__btn
    margin-top: 15px
    button
      display: inline-flex
      align-items: center
      border: 1px solid #E1E3EA
      border-radius: 40px
      height: 30px
      padding: 0 15px

      font-style: normal
      font-weight: 700
      font-size: 14px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      +tr(0.25s)
      +h
        border-color: $accent
        background-color: $accent
        color: #fff


.table-return-product
  thead
    border-bottom: 27px solid transparent
    & > tr
      position: relative
      th
        padding: 0 10px 7px
        font-style: normal
        font-weight: 400
        font-size: 12px
        line-height: 15px
        letter-spacing: 0.02em
        &:first-child
          padding-left: 0
          &:after
            content: ''
            display: block
            position: absolute
            bottom: 15px
            left: 0
            width: 100%
            height: 1px
            background: #F0F1F4
        &:last-child
          padding-right: 0
  tbody
    tr
      position: relative

      &:last-child
        td
          .table-return-product__reviews
            margin-bottom: 0

      td
        padding: 0 10px
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 18px
        letter-spacing: 0.02em
        color: #3C3C3B
        & > *
          display: flex

        &:first-child
          padding-left: 0

        &:last-child
          padding-right: 0

        a.ico_download
          display: flex
          justify-content: center
          align-items: center
          width: 25px
          height: 25px
          flex: 0 0 25px
          font-size: 25px
          color: $accent

  &__numb
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.04em

  &__title
    font-style: normal
    font-weight: 700
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-top: 5px
    +tr(0.25s)
    +h
      color: $accent

  &__reviews
    display: flex
    justify-content: flex-start
    align-items: flex-start

    padding-top: 20px
    padding-bottom: 20px
    margin-bottom: 20px
    border-bottom: 1px solid #F0F1F4

  &__img
    display: block
    position: relative
    margin-right: 20px

    a
      display: flex
      justify-content: center
      align-items: center
      position: relative

      +icon(plus)

      &::before
        position: absolute
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        z-index: 2
        width: 30px
        height: 30px
        flex: 0 0 30px
        display: flex
        justify-content: center
        align-items: center
        border: 2px solid rgba(255, 255, 255, 0.5)
        border-radius: 20px
        font-size: 14px
        color: #fff

      &:after
        content: ''
        display: block
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(#3C3C3B, 0.6)
        +tr(0.25s)

      +h
        &::before
          border: 2px solid #fff
        &:after
          background: rgba(#3C3C3B, 0.9)


  &__desc
    h4
      font-style: normal
      font-weight: 700
      font-size: 14px
      line-height: 140%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin: 0
    p
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 140%
      color: #3C3C3B

.m-return-product-big,
.m-print-order
  &__footer
    display: flex
    justify-content: space-between
    align-items: center
    +r(767)
      flex-direction: column
      justify-content: flex-start
      align-items: stretch

    .table-total
      +r(767)
        margin-bottom: 20px

.print-overflow
  max-height: 540px
  overflow-y: auto
  margin-right: -10px
  padding-right: 10px

  +r(767)
    max-height: 250px

  &::-webkit-scrollbar
    width: 4px

  &::-webkit-scrollbar-track
    background-color: #E2EBEC

  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5

.m-group-products
  textarea.form-control
    height: 160px

.table-print-order
  width: 100%
  thead
    border-bottom: 11px solid transparent
    & > tr
      position: relative
      th
        padding: 0 10px 7px
        font-style: normal
        font-weight: 400
        font-size: 12px
        line-height: 15px
        letter-spacing: 0.02em
        vertical-align: top
        &:first-child
          padding-left: 0!important
        &:last-child
          padding-right: 0
  tbody
    tr
      position: relative
      td
        padding: 15px 10px
        &:first-child
          &::before
            display: none
        & > *
          display: flex
          font-style: normal
          font-weight: 400
          font-size: 14px
          line-height: 18px
          letter-spacing: 0.02em
          color: #3C3C3B

          &.big
            display: block
            max-width: 150px
            font-style: normal
            font-weight: 400
            font-size: 16px
            line-height: 20px
            letter-spacing: 0.02em
            color: #3C3C3B

        &:first-child
          padding-left: 0

        &:last-child
          padding-right: 0

        a.ico_download
          display: flex
          justify-content: center
          align-items: center
          width: 25px
          height: 25px
          flex: 0 0 25px
          font-size: 25px
          color: $accent

.m-print-order
  .modal-body
    padding-top: 0
  .lk-modal__sort
    display: flex
    justify-content: flex-end
    align-items: flex-start
    margin-bottom: 30px
    +r(767)
      margin-top: 10px
      margin-bottom: 20px

.modal-complaint-item
  display: flex
  flex-direction: column
  padding-left: 20px
  border-left: 4px solid $accent
  span
    display: block
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.04em
    color: #878C98
  a
    display: block
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-top: 5px

.m-new-message
  textarea
    height: 160px

