.m-similar-goods
  .modal-dialog
    max-width: 950px

  .similar-goods-grid
    display: grid
    grid-template-columns: repeat(3, 1fr)
    grid-gap: 30px

    +r(991)
      grid-template-columns: repeat(2, 1fr)

    +r(767)
      grid-template-columns: repeat(1, 1fr)

    .product-card
      border: 1.5px solid #F8F8F8
      border-radius: 30px
      +h
        border: 1.5px solid transparent

      .product-card__btn
        .button-outline
          height: 30px
          +h
            border-color: $accent

.address-list
  margin-bottom: 30px

.address-row
  display: flex
  align-items: center
  flex-wrap: wrap
  border-bottom: 1px solid #F8F8F8
  padding: 15px 0
  justify-content: space-between
  max-width: 100%
  &:first-child
    padding-top: 0
  &:last-child
    padding-bottom: 0
    border-bottom: 0
  &__left
    flex: 0 0 calc(100% - 90px)
    max-width: calc(100% - 90px)
    min-width: 0
  &__data
    font-weight: 400
    font-size: 14px
