.cookie-tabs
  border-bottom: 1px solid #FFAE00
  margin-bottom: 30px

  .nav-link
    font-weight: 700
    font-size: 18px
    line-height: 100%
    letter-spacing: 0.01em
    color: #3C3C3B
    border: 0
    border-bottom: 3px solid transparent
    margin-bottom: 0
    flex: 0 0 50%
    text-align: left
    padding: 13px 0
    +r(568)
      font-size: 16px

    +h
      border-bottom: 3px solid #FFAE00

    &.active
      border-bottom: 3px solid #FFAE00

.cookie-drop
  display: none

.cookie-accordion
  .accordion-item
    border: 1px solid #F0F1F4
    border-radius: 30px
    padding: 30px
    margin-bottom: 10px
    +r(568)
      padding: 15px
  .accordion-header
    position: relative
    .check-button
      position: absolute
      right: 34px
      top: 50%
      transform: translateY(-50%)
  .accordion-body
    padding: 0
  .accordion-button
    padding: 0
    border: 0
    background: #fff
    box-shadow: none
  .accordion-body
    border-top: 1px solid #F0F1F4
    margin-top: 10px
    padding-top: 10px
  .cookie-btn
    padding-right: 140px
    position: static
    flex-direction: column
    align-items: start
    cursor: pointer
    
    +r(568)
      padding-right: 70px

    &:not(.collapsed):before
      transform: translateY(-50%) rotate(0)

    &::before
      position: absolute
      right: 0
      top: 50%
      transform: translateY(-50%) rotate(-180deg)
      width: 14px
      height: 14px
      content: ''
      background-image: url('data:image/svg+xml;utf8,<svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1.5 5.21484L4.92857 1.78627L8.35714 5.21484" stroke="%233C3C3B" stroke-width="1.8" stroke-linecap="round"/></svg>')
      background-size: auto
      background-position: center
      background-repeat: no-repeat
      +tr(.25s)
    &::after
      display: none

    &__title
      font-weight: 700
      font-size: 16px
      line-height: 100%
      letter-spacing: 0.01em
      color: #3C3C3B
      margin-top: 0
      margin-bottom: 5px
    &__text
      font-weight: 400
      font-size: 12px
      line-height: 120%
      letter-spacing: 0.02em
      color: #878C98

      p
        margin: 0

.cookie-inner
  &__text
    font-weight: 400
    font-size: 12px
    line-height: 130%
    letter-spacing: 0.02em
    color: #3C3C3B
    p
      margin-top: 0
      margin-bottom: 10px
  &__title
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.01em
    color: #878C98
    margin-bottom: 5px
  &__bottom 
    border-top: 1px solid #F0F1F4
    padding-top: 10px

.cookie-date-row
  display: flex
  align-items: center

.cookie-date
  &__icon
    &::before
      color: #57CA70
      margin-right: 8px
      font-size: 16px

  &__status
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.01em
    color: #3C3C3B
    margin-right: 26px
  &__info 
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.01em
    color: #3C3C3B

.check-button
  position: relative
  cursor: pointer
  &__input
    position: absolute
    left: 0
    top: 0
    opacity: 0
    appearance: none
  &__box
    border: 1.5px solid #E1E3EA
    border-radius: 15px
    width: 32px
    height: 20px
    display: block
    +tr(.25s)
    &::before
      width: 12px
      height: 12px
      border-radius: 50%
      background: #E1E3EA
      position: absolute
      left: 4px
      top: 4px
      content:""
      display: block
      +tr(.25s)

.check-button__input:checked + .check-button__box
  border: 1.5px solid #3C3C3B
  &::before
    left: auto
    right: 4px
    background: #3C3C3B

.cookie-category
  display: flex
  align-items: center
  margin-bottom: 10px

  &__text
    font-weight: 400
    font-size: 12px
    line-height: 120%
    letter-spacing: 0.02em
    color: #3C3C3B
    flex: 0 0 calc( 100% - 140px )
    
    +r(568)
      flex: 0 0 calc( 100% - 70px )
  &__tooltip
    font-size: 24px
    color: #C3C8D5
    margin-right: 11px
    margin-left: auto

.cookie-logo
  display: inline-block
  margin-bottom: 40px

.modal-cookie
  .modal-body
    padding-top: 10px