.section-delivery
  margin: 75px 0
  +r(1199)
    margin: 60px 0
  +r(767)
    margin: 30px 0 70px

  .--decor
    &:before
      bottom: -120px
      +r(1199)
        bottom: -65px


.delivery-desc
  display: flex
  justify-content: center
  align-items: flex-start
  flex-direction: column
  height: 100%
  padding-right: 30px
  +r(767)
    padding-right: 0
    margin-bottom: 30px
    height: auto

.delivery-info
  display: flex
  justify-content: center
  align-items: center
  background: $accent
  padding: 20px 40px
  height: 100%
  +r(1199)
    padding: 40px

  ul
    list-style-type: none
    padding: 0
    margin: 0
    li
      & + li
        margin-top: 30px

      h1, h2, h3, h4, h5, h6
        font-style: normal
        font-weight: 700
        font-size: 16px
        line-height: 110%
        letter-spacing: 0.02em
        color: #FFFFFF
        margin: 0 0 6px 0

      span
        font-style: normal
        font-weight: 400
        font-size: 12px
        line-height: 120%
        letter-spacing: 0.02em
        color: #FFFFFF

.delivery-img
  height: 100%
  img
    width: 100%
    max-width: 100%
    min-height: 100%
    object-fit: cover
    object-position: center