.lk-page 
  &__m-user-box
    background: #FFFFFF
    border-radius: 30px
    padding: 30px
    max-width: 663px
    +r(767)
      padding: 20px

.lk-section-title
  flex: 0 0 100%
  margin-bottom: 30px

.m-user-box
  display: flex
  flex-wrap: wrap
  margin-bottom: 30px
  &__left
    flex: 0 0 calc( 100% - 300px )  
  &__title
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E
    margin-top: 0
    margin-bottom: 15px
  &__right
    flex: 0 0 300px
    padding-left: 40px 
    .form-group
      margin-bottom: 10px
  .drop
    width: 100%
  .check__txt
    font-size: 12px
  +r(991)
    &__left
      flex: 0 0 100%
      margin-bottom: 20px
    &__right
      flex: 0 0 100%
      padding-left: 0  
.m-user
  margin-bottom: 15px
  &__name
    margin-bottom: 10px
    font-weight: 700
    font-size: 18px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B

  &__data
    margin-bottom: 10px
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    &:last-child
      margin-bottom: 0
  &__links
    display: flex
    align-items: center
    justify-content: space-between

.m-user-aip
  background: #FFFFFF
  border-radius: 30px
  padding: 30px
  max-width: 663px
  +r(767)
    padding: 20px
  &__title
    font-weight: 700
    font-size: 18px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E
    margin-top: 0
    margin-bottom: 10px
  &__token
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E
    margin-bottom: 10px

.m-user-activity 

  &__title
    font-weight: 400
    font-size: 14px
    line-height: 100%
    color: #3C3C3B
    margin-bottom: 10px
  &__row
    margin-bottom: 30px
    position: relative
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    display: flex
    align-items: center
    &.--lock
      .m-user-activity__date
