@font-face
    font-family: "icons_2"
    src: url('../fonts/icons_2.eot')
    src: url('../fonts/icons_2.eot?#iefix') format('eot'), url('../fonts/icons_2.woff') format('woff'),url('../fonts/icons_2.woff2') format('woff2'), url('../fonts/icons_2.ttf') format('truetype'), url('../fonts/icons_2.svg#icons_2') format('svg')


@mixin icon-styles
    font-family: "icons_2"
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    font-style: normal
    font-variant: normal
    font-weight: normal
    // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
    text-decoration: none
    text-transform: none

    display: inline-block
    text-decoration: inherit
    width: 1em
    line-height: 1em


%icon
    +icon-styles

@function icon-char($filename)
    $char: ""

    @if $filename == angle-down
        $char: "\E001"
    
    @if $filename == angle-left
        $char: "\E002"
    
    @if $filename == angle-right
        $char: "\E003"
    
    @if $filename == angle-up
