.user-box
  display: block
  position: relative
  width: 100%
  max-width: 875px
  background: #FFFFFF
  border-radius: 30px
  padding: 30px
  +r(1359)
    max-width: 100%
  +r(767)
    padding: 20px 17px
  &__inner
    display: flex
    justify-content: flex-start
    align-items: stretch
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 18px
    letter-spacing: 0.02em
    color: #3C3C3B
    +r(767)
      display: block
    .drop
      width: 100%
    .button-accent
      height: 45px
  &__left
    width: 100%
    max-width: 400px
    margin-right: 30px
    +r(1359)
      max-width: 50%
    +r(767)
      max-width: 100%
      margin-right: 0
      margin-bottom: 15px
  &__right
    display: flex
    justify-content: space-between
    flex-direction: column
    position: relative
    flex-grow: 1
    & > div:last-child
      margin-bottom: 10px
      +r(767)
        text-align: right
        margin-bottom: 0

    .ip-list
      list-style-type: none
      padding: 0
      margin: 15px 0 30px
      li
        display: flex
        justify-content: flex-start
        align-items: center
        margin-bottom: 5px
        &:last-child
          margin-bottom: 0
        span:first-child
          display: block
          font-style: normal
          font-weight: 400
          font-size: 14px
          line-height: 18px
          letter-spacing: 0.02em
          color: #3C3C3B
          width: 100px
          margin-right: 20px
        span:last-child
          font-style: normal
          font-weight: 400
          font-size: 14px
          line-height: 18px
          letter-spacing: 0.02em
          color: #9FA4B0

  &__btn
    display: block
    position: absolute
    top: 0
    right: 0
    +r(767)
      position: static
      top: auto
      right: auto
      text-align: right
  &__bottom
    flex: 0 0 100%
    border-top: 1px solid #F0F1F4
    padding-top: 20px
    margin-top: 20px
    +r(568)
      .button--red
        width: 100%

.lk-page.--users

  .lk-page__head
    +r(1199)
      flex-direction: column
      .lk-page__title
        margin-bottom: 20px
      .switcher
        margin-left: 0

  .lk-page__filters
    +r(1199)
      display: flex
      justify-content: space-between
      align-items: center
      flex-wrap: wrap
      grid-gap: 20px
      & > *
        width: calc(33% - 15px)
        &:nth-child(1),
        &:nth-child(2)
          width: calc(50% - 10px)
    +r(767)
      display: block
      margin-bottom: 0
      & > *
        width: 100%!important
        margin-bottom: 15px

  .submenu
    +r(767)
      margin: 0

  .lk-page__action-btns
    +r(1199)
      margin-top: 0
      width: 100%
      max-width: calc(66% + 20px)
      padding-left: 20px
      box-sizing: border-box
      .drop
        min-width: auto
        width: 100%
    +r(767)
      max-width: 100%
      padding-left: 0
      .drop.--search
        flex-grow: 1

    +r(767)
      flex-direction: column

    & > *
      +r(767)
        margin-left: 0
        margin-top: 15px

.lk-page.--users
  .switcher
    +r(767)
      display: inline-flex
      justify-content: center
      align-self: center