

.lk-manager-document-invoice, .lk-document-invoice
  +r(1640)
    .lk-page__table-after-btns
      flex: 0 0 100%
      margin-left: 0
      margin-top: 20px
  +r(991)
    .lk-page__table-after-btns
      flex-direction: column
      align-items: flex-start
      gap: 10px
      .button-outline
        width: 100%
        max-width: 290px
  +r(640)
    .lk-page__table-after-btns
      .button-outline
        width: 100%
        max-width: 100%

.lk-manager-waiting
  .show-mobile
    display: none
    +r(767)
      display: block
      margin-left: auto
  .lk-page__filters
    grid-template-columns: repeat(1, 262px)
    +r(767)
      grid-template-columns: repeat(1, 1fr)

.lk-manager-recivables-inner
  .button-accent, .button-outline
    height: 45px
  .lk-page__head
    +r(1199)
      padding-right: 180px
    +r(767)
      padding-right: 0

.lk-manager-document-act
  .lk-page__head
    +r(1199)
      padding-right: 180px
    +r(767)
      padding-right: 0

.recivables-pay
  color: #FFBC08
  font-size: 26px

  &.ico_card
    font-size: 18px

.lk-manager-recivables
  +r(1199)
    .lk-page__filters
      grid-template-columns: repeat(3, 1fr)

  +r(767)
    .lk-page__filters
      grid-gap: 15px
      grid-template-columns: repeat(1, 1fr)
  +r(991)
    table.ftable > tbody > tr > td 
      height: 50px
      font-size: 12px
      line-height: 1.25

.my-price
  +r(767)
    font-size: 14px!important

.lk-page__recivables-info
  background: #FFFFFF
  border-radius: 30px
  padding: 30px
  max-width: 484px

  .form-label
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.01em
    color: #9FA4B0

  .form-info
    font-weight: 400
    font-size: 14px
    line-height: 100%
    color: #30393E
    padding: 11px 20px 10px
    height: 35px
    display: flex
    align-items: center
    background: #F8F9FA
    border-radius: 30px

  .form-control
    height: 35px

  .js-date
    padding-right: 30px

.js-date-cont
  position: relative
  +icon(datedots)

  &::before
    color: #C3C8D5
    position: absolute
    right: 15px
    top: 50%
    transform: translateY(-50%)
    pointer-events: none

.table-invoice-link
  text-align: center
  font-size: 12px!important
  display: inline-block!important
  &.--green
    color: #57CA70!important
  &.--red
    color: #FF6A6A!important

.ftable.documents-table-inner 
  width: 100%
  > tbody > tr > td:first-child
    padding-left: 0
  > tbody > tr:hover > td
    border: 0
    &:first-child
      border: 0
  > tbody > tr > td:last-child
    padding-right: 5px

  td
    &:first-child
      max-width: 200px
  +r(920)
    > tbody > tr > td
      height: auto
    tr
      display: flex
      flex-wrap: wrap
      justify-content: center
      border-bottom: 1px solid #F0F1F4
      &:last-child
        border-bottom: 0
      td
        display: flex
        align-items: center
        &:first-child
          flex-direction: column
          align-items: start
          justify-content: center
  +r(767)
    width: 100%
    tr
      flex-direction: column
      align-items: center
      width: 100%

  > tbody > tr > td:first-child
    border-radius: 0
    padding-left: 20px
  



.documents-table
  width: 100%
  table.footable-details>tbody>tr>td:nth-child(2)
    word-break: normal

  .status
    white-space: nowrap


  &.ftable > tbody > tr.footable-detail-row > td:first-child
    padding: 0

  &.footable-details::before 
    width: 100%
    z-index: 5
    left: 0

  &.footable-details.ftable > tbody > tr > td
    padding: 0
  
  &.footable-details.table-w-inner > tbody > tr
    > th 
      padding: 10px 10px 10px 20px
    > td
      padding: 10px 10px 10px 20px
    &:last-child
      > td
        padding: 0

.table-product-card__text
  max-width: 290px
  font-weight: 400
  font-size: 12px
  line-height: 130%
  letter-spacing: 0.02em
  color: #3C3C3B

  p
    margin-bottom: 0

  +r(920)
    max-width: 150px

.documents-head-row
  background: #F8F9FA

.lk-manager-document-invoice, .lk-document-invoice
  .documents-table-inner
    border-collapse: separate
    border-spacing: 0
    +rmin(921)
      > tbody 
        > tr
          > td
            border-top: 1px solid #F0F1F4!important
          > td:first-child
            border-top: 1px solid transparent!important
          &.documents-head-row
            > td
              border-top: 0!important
        tr.documents-head-row + tr
          td
            border-top: 1px solid transparent!important
    +r(920)
      > tbody 
        > tr:not(.documents-head-row)
          > td
            flex-direction: column
            +rmin(768)
              align-items: flex-start

.checkmark-done
  color: #57CA70
  font-size: 20px