.action-group
  display: inline-block!important
  position: relative
  width: 30px
  height: 30px
  text-align: center

  &.is-show
    .action-group-drop
      visibility: visible
      opacity: 1
      pointer-events: all

.action-group-btn
  display: flex!important
  justify-content: center
  align-items: center
  width: 30px
  height: 30px
  flex: 0 0 30px
  border: none
  border-radius: 50%
  background-color: #fff
  box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
  font-size: 13px!important
  color: #C3C8D5!important
  cursor: pointer
  +hh
    background-color: #fff
    box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)!important
    color: #FFAE00!important

.action-group-drop
  -webkit-transition: 0.5s
  -o-transition: 0.5s
  display: block
  visibility: hidden
  z-index: 3
  position: absolute
  top: -13px
  right: -13px
  opacity: 0
  pointer-events: none
  transition: 0.5s
  background: #FFFFFF
  border: 1.5px solid #E1E3EA
  overflow: hidden
  box-sizing: border-box
  box-shadow: 0px 15px 30px -20px rgba(146, 157, 165, 0.2)
  border-radius: 30px 0px 30px 30px

.action-group-list
  -webkit-box-pack: center
  -ms-flex-pack: center
  -webkit-box-align: center
  -ms-flex-align: center
  -webkit-box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.16)
  display: -webkit-inline-box
  display: -ms-inline-flexbox
  display: inline-flex
  align-items: center
  justify-content: center
  margin: 0
  padding: 15px
  overflow: hidden
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.16)
  list-style-type: none


  li
    -webkit-box-pack: center
    -ms-flex-pack: center
    -webkit-box-align: center
    -ms-flex-align: center
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    align-items: center
    justify-content: center
    width: 30px
    height: 30px
    flex: 0 0 30px
    +tr(0.3s)
    margin: 0 5px

    &:first-child
      margin-left: 0
    &:last-child
      margin-right: 0

    &.is-active
      button
        background-color: #929DA5
        color: #fff

    button,
    a
      display: flex!important
      justify-content: center
      align-items: center
      width: 30px
      height: 30px
      flex: 0 0 30px
      border: none
      border-radius: 50%
      background-color: #fff
      box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
      font-size: 16px
      color: #C3C8D5
      +hh
        background-color: #fff
        box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)!important
        +ico()
          color: #FFAE00

      .ico_close
        font-size: 12px

      .ico_plus
        font-size: 12px

      .ico_edit
        font-size: 20px

      .ico_checkmark
        font-size: 18px
      +h
        .ico_checkmark
          color: #57CA70!important

      .ico_trash
        font-size: 16px
      +h
        .ico_trash
          color: #FF6A6A!important

    &:last-child
      button
        background-color: #fff
        box-shadow: none
        +h
          color: #C4C4C4!important
          box-shadow: none!important
          [class*='ico_']
            color: #C4C4C4!important