.m-contacts
  .swiper-wrapper
    gap: 20px
  .swiper-slide
    margin: 0 auto!important
    width: 227.5px
  .swiper-button-disabled
    visibility: hidden!important
  .swiper-button-lock,
  .swiper-pagination-lock
    display: none!important
  +rmin(568)
    &.m-contacts-single
      .modal-dialog
        max-width: 350px

.contacts-items
  display: block
  position: relative
  margin: 30px 0

.contacts-item
  display: block
  position: relative
  height: 100px
  +r(1199)
    max-width: 550px
  +h
    .contacts-item__link
      box-shadow: 0px 14px 26px -14px rgba(60, 60, 59, 0.2)
    .contacts-item__btn
      .button-more
        border: 1px solid #3C3C3B
        &::after
          opacity: 1
  &__link
    display: flex
    justify-content: space-between
    align-items: center
    padding: 10px 30px 10px 30px
    background: #fff
    border-radius: 30px
    height: 100px
    +tr(0.25s)
    +r(767)
      padding: 10px 15px 10px 30px
  &__info
    display: block
    position: relative
    margin-right: 20px
  &__title
    font-style: normal
    font-weight: 700
    font-size: 16px
    line-height: 110%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 10px
    +line(3)
    +r(767)
      font-size: 14px
  &__location
    display: flex
    justify-content: flex-start
    align-items: center
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    +ico()
      color: $accent
      font-size: 14px
      margin-right: 8px
  &__btn
    .button-more
      width: 30px
      height: 30px
      flex: 0 0 30px
      border: 1px solid #E1E3EA
      &:after
        content: ''
        font-size: 0
        display: block
        width: 30px
        height: 30px
        flex: 0 0 38px
        position: absolute
        top: calc(50% - 2px)
        left: calc(50% + 1px)
        transform: translate(-50%, -50%)
        border: 6px solid $accent
        border-radius: 50%
        clip-path: polygon(50% 0, 50% 50%, 100% 50%, 100% 0%)
        opacity: 0
        +tr(0.25s)

.contacts-map
  display: block
  position: relative
  margin: 30px 0
  +r(767)
    margin: 40px -15px 0

.contacts-map-box
  height: 500px