.page-home
  .section-banner
    display: block
    position: relative
    margin-bottom: 75px
    +r(1199)
      margin-top: 60px
      margin-bottom: 67px
      margin-left: -10.5px
      margin-right: -10.5px
    +r(767)
      margin-top: 30px
      margin-bottom: 30px
  .banner__box
    +r(1199)
      min-height: 360px
    +r(767)
      flex-direction: column-reverse
      min-height: 530px
  .banner__media
    +r(1199)
      background-position: right center

.banner
  display: block
  position: relative
  background: rgba(60, 60, 59, 0.7)
  overflow: hidden

  &__box
    display: flex
    justify-content: space-between
    align-items: center
    position: relative
    +r(1199)
      min-height: 360px
    +r(767)
      flex-direction: column-reverse
    &::after
      content: ''
      position: absolute
      left: 0
      top: 0
      width: 100%
      height: 100%
      background: rgba(#000, 0.2)
      display: block

  &__desc
    width: 50%
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: flex-start
    padding: 50px 0 50px 60px
    position: relative
    z-index: 1
    +r(1439)
      padding: 30px 50px
    +r(1359)
      padding: 30px 15px
      width: 50%
    +r(1199)
      width: 70%
    +r(767)
      width: 100%
      padding-top: 0

  &__top
    display: flex
    justify-content: flex-start
    align-items: center
    margin-bottom: 20px

    span
      font-style: normal
      font-weight: 700
      font-size: 22px
      line-height: 100%
      letter-spacing: 0.02em
      color: #FFFFFF
      margin-left: 30px
      padding-left: 30px
      border-left: 1px solid rgba(#FFFFFF, 0.4)
      +r(767)
        margin-left: 10px
        padding-left: 10px
        font-size: 20px

  &__title
    font-style: normal
    font-weight: 700
    font-size: 36px
    line-height: 120%
    color: #FFFFFF
    margin: 0
    +r(1199)
      font-size: 26px
    +r(767)
      font-size: 24px

  &__text
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 140%
    color: #FFFFFF
    margin: 10px 0 20px
    +r(1199)
      font-size: 14px
    +r(767)
      font-size: 13px

  &__bottom
    display: flex
    justify-content: flex-start
    align-items: center

  &__price
    display: flex
    justify-content: flex-start
    align-items: flex-end
    margin-right: 35px
    +r(1199)
      margin-right: 28px
    +r(767)
      margin-right: 15px
    del
      font-style: normal
      font-weight: 400
      font-size: 18px
      line-height: 1
      text-decoration-line: line-through
      color: #FFFFFF
    span
      font-style: normal
      font-weight: 700
      font-size: 30px
      line-height: 0.8
      color: #FFAE00
      +r(575)
        font-size: 24px
    & > * + *
      margin-left: 10px

  &__btn
    .button-outline
      color: #fff

  &__media
    width: 100%
    height: 100%
    display: block
    position: absolute
    background-repeat: no-repeat
    background-size: cover
    background-position: right center

section.section-banner.--mobile
  .banner__desc
    width: 55%
    +r(767)
      width: 100%
  .banner__title
    font-size: 42px
  .banner__text
    font-size: 14px
  .banner__box:after
    display: none
  +r(1199)
    .banner__box
      min-height: auto
  +r(767)
    .banner
      background: none
      overflow: visible
    .banner__box
      min-height: auto
      +r(1199)
        min-height: auto
      &:after
        display: none
    .banner__desc
      padding: 0
    .banner__title,
    .banner__text,
    .banner__media,
    .banner__bottom
      display: none
    .banner__top
      display: block
      margin-bottom: 0
      width: 100%
      .button-back
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 100%
        letter-spacing: 0.04em
        color: #3C3C3B
        [class*='ico_']
          border: 1px solid rgba(#3C3C3B, 0.4)
      span
        display: block
        text-align: center
        margin: 10px 0 0 0
        padding: 0
        border-left: 0
        color: #000
