.product-full-box.--compare
  .compare-items
    display: block
    position: relative

    .swiper
      margin: 0
      padding: 0

    .swiper-nav.--section-slider-nav
      position: absolute
      top: -40px
      right: 0


.compare-item
  display: block
  position: relative
  border-left: 1px solid #E1E3EA
  border-right: 1px solid #E1E3EA
  +r(767)
    border-left: 0
    border-right: 0

  +h
    .compare-item__sub-price
      display: none
    .compare-item__btn
      display: flex
  &__box
    display: flex
    flex-direction: column
  &__head
    display: block
    position: relative

  &__action
    display: flex
    justify-content: space-between
    position: absolute
    top: 20px
    left: 20px
    right: 20px
    width: calc(100% - 40px)
  &__label
    display: inline-flex
    align-items: center
    height: 20px
    padding: 0 6px
    background: #27D9A1
    font-style: normal
    font-weight: 700
    font-size: 10px
    line-height: 100%
    letter-spacing: 0.06em
    text-transform: uppercase
    color: #FFFFFF
    
    &.not
      background: #B9BFC2
      +r(568)
        font-size: 9px
    &.for-order
      background: #FFAE00
    &.sale
      background: #FF5F72
  &__media
    display: flex
    justify-content: center
    align-items: center
    position: relative
    img
      width: 100%
      max-width: 100%
      max-height: 200px
      object-fit: cover
      object-position: center
      +r(767)
        max-height: none
  &__brand
    max-height: 20px
    & > img
      max-height: 20px
  &__info
    padding: 15px 20px 23px
    border-bottom: 1px solid #E1E3EA
    margin-top: auto
  &__number
    text-align: center
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 130%
    letter-spacing: 0.04em
    color: #9FA4B0
    margin-bottom: 5px
  &__title
    min-height: 46px
    margin-bottom: 12px
    a
      font-style: normal
      font-weight: 700
      font-size: 20px
      line-height: 117%
      text-align: center
      letter-spacing: 0.02em
      text-transform: uppercase
      color: #3C3C3B
      text-decoration: none
      +line(2)
      +tr(0.3s)
      +hh
        color: $accent
        text-decoration: none
      +r(767)
        font-size: 15px
        
  &__sub-price
    color: #9FA4B0
    font-size: 14px
    text-align: center
    min-height: 30px
    display: flex
    align-items: center
    justify-content: center
  &__price
    display: flex
    justify-content: center
    align-items: center
    font-style: normal
    font-weight: 700
    font-size: 17px
    line-height: 100%
    text-align: center
    color: #3C3C3B
  &__btn
    display: none
    justify-content: center
    align-items: center
  &__body
    display: block
    position: relative
  &__list
    list-style-type: none
    padding: 0
    margin: 0
    li
      &:nth-child(odd)
        background-color: #F8F9FA
  &__item
      display: flex
      align-items: center
      padding: 0 20px
      margin: 0 auto
      height: 35px
      line-height: 35px
      box-sizing: border-box
      +tr(0.3s)
      +r(767)
        justify-content: space-between
      &.is-active
        background: #F0F1F4
      span
        font-style: normal
        font-weight: normal
        font-size: 14px
        line-height: 120%
        letter-spacing: 0.02em
        color: #5A5D65
        +line(1)
        &.lbl
          display: none
          font-style: normal
          font-weight: 500
          font-size: 12px
          line-height: 14px
          color: #929DA5
          +tr(0.3s)
          +r(767)
            display: block
            width: calc(45% - 10px)
            margin-right: 10px
        &.value
          +line(1)
          +r(767)
            width: 55%

.swiper-slide.swiper-slide-active
  .compare-item__item span.label
    opacity: 1

.compare-items
  .swiper-scrollbar
    bottom: -10px

.compare-sidebar
  display: block
  position: relative
  +r(767)
    display: none
  &__head
    display: flex
    flex-direction: column
    justify-content: flex-end
    height: 369px

    img
      max-width: 120px
      margin: 20px auto 20px
      +r(1199)
        margin: 20px 0

    h3
      font-style: normal
      font-weight: 600
      font-size: 20px
      line-height: 100%
      letter-spacing: 0.02em
      color: #5A5D65
      margin-bottom: 15px

    .check
      margin-bottom: 16px

  &__body
    display: block
    position: relative
  &__list
    list-style-type: none
    padding: 0
    margin: 0
    li
      width: 100%
      height: 35px
      line-height: 35px
      padding: 0 20px
      white-space: nowrap
      text-overflow: ellipsis
      overflow: hidden
      font-style: normal
      font-weight: normal
      font-size: 14px
      letter-spacing: 0.02em
      color: #5A5D65
      +line(1)
      +tr(0.3s)
      &:nth-child(odd)
        background: #F8F9FA
      +h
        background: #F0F1F4
      &.is-active
        background: #F0F1F4