.page-catalog
  .page-content
    padding-top: 0
  .section-banner
    margin-bottom: 30px

.catalog
  display: flex

.catalog-overlay
  display: none
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  background: rgba(#000, 0.6)
  z-index: 98
  &.is-show
    display: block

.catalog-sidebar
  width: 262px
  flex: 0 0 262px
  margin-right: 30px

  +r(1365)
    position: fixed
    top: 0
    left: 0
    width: 280px
    height: 100%
    min-height: 100vh
    background: #fff
    overflow-x: hidden
    overflow-y: auto
    z-index: 99
    margin-right: 0
    transform: translateX(-100%)

    scrollbar-color: $accent #E2EBEC
    scrollbar-width: thin!important
    -moz-appearance: none !important

    &::-webkit-scrollbar
      width: 4px

    &::-webkit-scrollbar-track
      background-color: #E2EBEC

    &::-webkit-scrollbar-thumb
      background-color: $accent
    +tr(0.35s)

    &.is-show
      transform: translateX(0)



.catalog-header
  display: flex
  justify-content: space-between
  align-items: center
  flex-wrap: wrap

  &__filter-left
    display: flex
    margin-bottom: 20px

    +r(767)
      flex: 0 0 100%
      flex-wrap: wrap
      justify-content: space-between

    .catalog-header__filter-btn
      display: none
      +r(1365)
        display: block
      +r(767)
        width: 100%
        flex: 0 0 100%
        margin-bottom: 20px
        display: flex
        justify-content: center
      .button
        height: 35px
        padding: 0 38px
        margin-right: 20px
        +r(767)
          margin-right: 0
          width: 100%
        +ico()
          margin-right: 10px

    .drop
      .drop-button
        min-width: 260px
      +r(568)
        min-width: 100%
        margin-left: 0!important
        margin-bottom: 20px
        &:last-child
          margin-bottom: 0

  &__filter-right
    display: flex
    margin-left: auto
    margin-bottom: 20px

    +r(991)
      flex: 0 0 100%
      justify-content: flex-end
    +r(767)
      justify-content: space-between


  .drop

    .form-control
      border: 1px solid #E1E3EA
      box-sizing: border-box

    .drop-button
      height: 35px
      padding: 0 45px 0 15px
      background-image: url("data:image/svg+xml,%3Csvg width='10' height='6' viewBox='0 0 10 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.99999 4.31654L1.67513 0.991678C1.48622 0.802774 1.18058 0.802774 0.991678 0.991678C0.802774 1.18058 0.802774 1.48622 0.991678 1.67513L4.65827 5.34172C4.75246 5.43591 4.87641 5.48332 5 5.48332C5.1236 5.48332 5.24755 5.43591 5.34174 5.34172L9.00833 1.67513L9.00834 1.67512C9.19722 1.48622 9.19722 1.18058 9.00831 0.991677C8.81941 0.802773 8.51377 0.802773 8.32486 0.991677L4.99999 4.31654Z' fill='%23B4BACA' stroke='%23B4BACA' stroke-width='0.3'/%3E%3C/svg%3E%0A")
      background-position: right 12px center

    .drop-clear
      width: 35px
      height: 35px
      flex: 0 0 35px

    & + .drop
      margin-left: 20px
      +r(767)
        margin-left: 10px
      +r(550)
        margin-right: 10px

    &._active
      .drop-button
        background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cpolyline fill='none' stroke='%23B4BACA' stroke-width='1.03' points='4 13 10 7 16 13'%3E%3C/polyline%3E%3C/svg%3E")
        background-size: 15px
    
    &.--select-amount
      margin-left: auto
      +r(767)
        margin-left: auto

.change-view
  display: flex
  justify-content: flex-end
  align-items: center
  margin-left: 20px
  +r(550)
    margin-left: auto

.change-view-item
  display: none
  justify-content: center
  align-items: center
  width: 35px
  height: 35px
  flex: 0 0 35px
  background: #FFFFFF
  border-radius: 6px
  cursor: pointer
  &.is-active
    display: flex

  +h
    color: $accent

.catalog-list
  margin-top: 10px


.catalog-list.--grid
  display: grid
  grid-template-columns: repeat(5, 1fr)
  grid-gap: 30px
  +r(1679)
    grid-template-columns: repeat(4, 1fr)
  +r(1199)
    grid-template-columns: repeat(3, 1fr)
    grid-gap: 20px
  +r(767)
    grid-template-columns: repeat(2, 1fr)
    grid-gap: 20px
  +r(575)
    grid-template-columns: repeat(1, 1fr)
    grid-gap: 10px

.catalog-list.--list
  display: block
  .product-card
    & + .product-card
      margin-top: 15px

.product-card
  display: block
  position: relative
  height: 100%
  z-index: 1
  +h
    z-index: 2
  
  &__box
    display: flex
    flex-direction: column
    background: #FFFFFF
    border-radius: 30px
    height: 100%
    min-height: 410px
    +h
      height: auto
      box-shadow: 0px 14px 26px -14px rgba(60, 60, 59, 0.2)
      .product-card__sub-price
        display: none
      .product-card__btn
        display: block
      .product-card__compare
        opacity: 1
        pointer-events: all
      .product-card__intro
        display: block
        box-shadow: 0px 14px 26px -14px rgba(60, 60, 59, 0.2)
    &.not
      .product-card__media
        filter: grayscale(1)
        img
          opacity: 0.8
      +r(991)
        .button-small
          font-size: 12px
      +r(658)
        .button-small
          font-size: 14px
      
  &__media
    display: flex
    justify-content: center
    align-items: center
    position: relative
    height: 212px
    padding-top: 20px
    img
      width: 100%
      max-height: 192px
  &__label
    display: block
    top: 20px
    left: 5px
    padding: 6px
    height: 22px
    background: #27D9A1
    font-style: normal
    font-weight: 700
    font-size: 10px
    line-height: 100%
    letter-spacing: 0.06em
    text-transform: uppercase
    color: #FFFFFF
    white-space: nowrap
    +r(767)
      left: 10px
    &.not
      background: #B9BFC2
      +r(568)
        font-size: 9px
    &.for-order
      background: #FFAE00
    &.sale
      background: #FF5F72

  &__badges
    position: absolute
    top: 20px
    left: 5px
    height: 20px
    display: flex
    z-index: 5
    +h
      z-index: 10

  &__badge
    margin-left: 8px

  &__badge-button
    width: 20px
    height: 20px
    background: transparent
    font-size: 17px

  &__badge-new
    .product-card__badge-button, .product-card__badge-icon
      color: #57CA70

  &__badge-discount
    .product-card__badge-button, .product-card__badge-icon
      color: #FF5F72

  &__badge

    +h
      .product-card__badge-dropdown
        opacity: 1
        pointer-events: all
        visibility: visible
  
  &__badge-dropdown
    position: absolute
    top: -15px
    right: -15px
    background: #FFFFFF
    box-shadow: 0px 14px 26px -14px rgba(180, 186, 202, 0.5)
    border-radius: 30px 0px 30px 30px
    z-index: 7
    padding: 20px 20px 20px 60px
    width: 330px
    +tr(.25s)
    opacity: 0
    pointer-event: none
    visibility: hidden

    +r(1750)
      left: -10px
      right: auto
      width: 270px
      border-radius: 30px


  &__badge-icon
    position: absolute
    left: 20px
    top: 20px
    font-size: 25px

  &__badge-title
    font-weight: 700
    font-size: 14px
    line-height: 140%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 5px

  &__badge-content
    font-weight: 400
    font-size: 12px
    line-height: 140%
    letter-spacing: 0.02em
    color: #3C3C3B

    p
      margin: 0

  &__brand
    display: block
    position: absolute
    top: 20px
    right: 20px
    max-width: 80px
    max-height: 20px
    opacity: 0.5
    z-index: 4
    img
      width: 100%
      height: auto
  &__compare
    display: flex
    justify-content: center
    align-items: center
    width: 30px
    height: 30px
    flex: 0 0 30px
    border-radius: 50%
    color: #9FA4B0
    cursor: pointer
    position: absolute
    top: 50px
    left: 20px
    opacity: 0
    pointer-events: none
    font-size: 15px
    +tr(0.25s)
    +h
      background: #F8F9FA
      color: #3C3C3B
    &.is-active
      background: #F8F9FA
      color: #000!important
      opacity: 1
      pointer-events: all
  &__info
    padding: 10px 20px
    text-align: center
    min-height: 140px
  &__colors
    list-style-type: none
    padding: 0
    margin: 0 0 10px 1px
    display: flex
    justify-content: center
    align-items: center
    height: 15px
    li
      font-style: normal
      font-weight: 700
      font-size: 13px
      line-height: 100%
      letter-spacing: 0.04em
      & + li
        margin-left: 5px
      span
        display: block
        width: 15px
        height: 15px
        flex: 0 0 15px
        border-radius: 50%
        cursor: pointer
        box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2)
        &.is-select
          box-shadow: 0 0 0 1px #000
  &__number
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 130%
    letter-spacing: 0.04em
    color: #6F777C
    height: 17px
    display: block
  &__category
    display: none
  &__title
    display: block
    margin: 0 0 3px
    min-height: 48px
    a
      display: inline-block
      font-style: normal
      font-weight: 700
      font-size: 20px
      line-height: 120%
      letter-spacing: 0.02em
      text-transform: uppercase
      color: #3C3C3B
      +line(2)
      +tr(0.25s)
      +h
        color: $accent
  &__sizes
    display: block
    position: relative
    margin-top: 5px
    height: 22px
    ul
      list-style-type: none
      padding: 0
      margin: 0
      display: flex
      justify-content: center
      align-items: center
      li
        border: 1px solid #E1E3EA
        box-sizing: border-box
        border-radius: 20px
        padding: 4px 8px
        font-style: normal
        font-weight: 700
        font-size: 12px
        line-height: 100%
        letter-spacing: 0.02em
        color: #9FA4B0
        cursor: pointer
        &.is-active
          border-color: #000
          color: #000
        & + li
          margin-left: 5px
  &__bottom
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    padding: 10px 20px 15px
    text-align: center
  &__sub-price
    font-size: 14px
    color: #878C98
    min-height: 30px
    display: flex
    align-items: center
  &__price
    display: flex
    align-items: center
    font-style: normal
    font-weight: 700
    font-size: 18px
    line-height: 100%
    color: #3C3C3B
    +tr(0.25s)
    del
      font-style: normal
      font-weight: 400
      font-size: 16px
      line-height: 100%
      text-decoration-line: line-through
      color: #9FA4B0
      margin-right: 10px
  &__counter
    display: none
  &__btn
    display: none
    .button-outline
      border: 1px solid #DEE4E8

  &__intro-inner
    +line(4)

  &__intro
    text-align: left
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 140%
    letter-spacing: 0.02em
    color: #3C3C3B
    position: absolute
    top: 100%
    width: 100%
    left: 0
    border-radius: 0 0 30px 30px
    background: #FFFFFF
    padding: 0 20px 30px
    display: none
    z-index: 10
    &::after, &::before
      content: ''
      width: 30px
      height: 55px
      display: block
      position: absolute
      bottom: 100%
      background: #fff
    &::after
      left: 0
    &::before
      right: 0

.catalog-content
  width: 100%

.product-card.--small
  .product-card__box
    padding: 0
    border-radius: 0
    +h
      position: static
      box-shadow: none
  .product-card__badge-dropdown
    left: -10px
    right: auto
    border-radius: 30px
    width: 195px
    padding: 10px 10px 10px 35px
  .product-card__badge-icon
    font-size: 15px
    left: 10px
    top: 10px
  .product-card__label
    font-style: normal
    font-weight: 700
    font-size: 8px
    line-height: 100%
    letter-spacing: 0.06em
    text-transform: uppercase
    color: #FFFFFF
    padding: 5px
    top: 0
    left: 0
  .product-card__media
    height: 132px
    padding: 0
  .product-card__info
    padding: 10px 10px 0
    min-height: 79px
  .product-card__title
    min-height: auto
    a
      font-style: normal
      font-weight: 700
      font-size: 14px
      line-height: 117%
      text-align: center
      letter-spacing: 0.02em
      text-transform: uppercase
      color: #3C3C3B
  .product-card__number,
  .product-card__brand
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 130%
    text-align: center
    letter-spacing: 0.04em
    color: #9FA4B0
  .product-card__brand
    position: static
    text-align: center
    max-width: 100%
  .product-card__bottom
    padding: 0 0 5px
    min-height: 45px
    .button-small 
      height: auto
      padding: 5px 10px
      overflow: hidden
      min-height: 30px

  .product-card__price
    font-style: normal
    font-weight: 500
    font-size: 14px
    line-height: 100%
    text-align: center
    color: #3C3C3B
    margin-top: 5px

  .product-card__sub-price
    font-size: 12px

.catalog-list.--list
  display: block
  .product-card
    & + .product-card
      margin-top: 15px
    &__box
      position: static!important
      display: grid
      justify-content: flex-start
      align-items: center
      grid-template-columns: 180px 279px 1fr
      min-height: 140px
      &.not
        .product-card__btn
          min-width: 150px
          margin: 0
          +rmin(1200)
            min-width: 296px
          +rmin(992)
            min-width: 200px
          .button-outline
            width: 100%
        .product-card__counter
          display: none
      +r(1199)
        grid-template-columns: 125px 280px 1fr
      +r(767)
        display: flex
        justify-content: flex-start
        flex-direction: row
        flex-wrap: wrap
        padding-bottom: 18px
        padding-top: 17px
    &__media
      padding: 7px 25px
      height: 140px
      img
        max-height: 140px
      +r(1199)
        padding: 0
      +r(991)
        height: 180px
      +r(767)
        width: 40%
        height: 140px
    &__label
      top: 15px
    &__brand
      display: none
    &__info
      min-height: 0
      justify-content: flex-start
      text-align: left
      margin-right: auto
      padding: 0 20px 0 15px
      box-sizing: border-box
      +r(767)
        width: 60%
        margin: 0
        padding: 0
    &__number
      height: auto
    &__colors
      justify-content: flex-start
      margin-bottom: 15px
      height: auto
    &__info-grid
      display: flex
      align-items: center
    &__category
      display: inline-block
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 100%
      letter-spacing: 0.04em
      margin-left: 15px
      +r(1199)
        font-size: 12px
        margin-left: 5px
    &__title
      min-height: auto
      margin-top: 5px
      a
        +line(2)
        +r(767)
          font-size: 18px
    &__sizes ul
      justify-content: flex-start
    &__sizes
      display: none
    &__compare
      opacity: 1
      pointer-events: all
      background: #F8F9FA
      color: #3C3C3B
      position: static
      width: 45px
      height: 45px
      flex: 0 0 45px
      +rmin(768)
        +r(991)
          grid-column: span 1 
          grid-row: span 2
    &__bottom
      justify-content: space-evenly
      align-items: center
      flex-direction: row
      box-sizing: border-box
      padding: 0
      +rmin(768)
        max-width: 831px
        width: 100%
        margin-left: auto
        padding-right: 22px
        display: grid
        grid-template-columns: 45px 1fr 150px
        +r(991)
          grid-auto-rows: 45px
      +rmin(992)
        grid-template-columns: 45px 1fr 1fr 200px
      +rmin(1200)
        grid-template-columns: 45px 1fr 1fr 296px
      +r(767)
        flex-wrap: wrap
        justify-content: flex-start
        padding: 0 15px 0
        &  > *
          width: 50%
    &__grid
      display: flex
      align-items: center
      justify-content: space-between
      min-width: 150px
      +rmin(768)
        margin-left: auto
        +r(991)
          grid-column: span 1 
          grid-row: span 2
          display: grid
          gap: 5px
      +r(1199)
        flex-direction: column
        justify-content: center
      +r(767)
        width: 100%
        flex-direction: row
        justify-content: space-between
        margin-top: 15px
    &__counter
      display: block
      +r(1199)
        margin: 0 auto 14px
      +r(767)
        margin: 0
      +rmin(768)
        +r(991)
          margin-bottom: 0
      .counter__field
        +r(767)
          width: 50px
          input
            width: 50px
    &__sub-price
      display: flex
      align-items: center
      justify-content: center
      min-height: 0
      +rmin(768)
        +r(991)
          grid-column: 2/3 
          grid-row: 2
      +r(767)
        padding-left: 10px
        justify-content: start
        margin-left: 45px
    &__price
      display: flex
      justify-content: center
      align-items: center
      width: 100%
      max-width: 150px
      +rmin(768)
        margin-left: auto
        +r(991)
          margin-left: 0
          max-width: 100%
          grid-column: 2/3 
      del
        display: none
      +r(767)
        padding-left: 10px
        justify-content: start
    &__btn
      display: flex
      justify-content: center
      align-items: center
      margin: 0 0 0 10px
      +r(1199)
        margin: 0
        min-width: auto
      .button-outline
        height: 45px
        padding: 0 30px
        border: 1px solid #E1E3EA
        font-style: normal
        font-weight: 700
        font-size: 16px
        line-height: 100%
        letter-spacing: 0.02em
        color: #3C3C3B
        +r(1199)
          font-size: 14px
        +r(767)
          padding: 0 20px

        +h
          color: #3C3C3B!important
          background: none
          border: 1px solid #FFAE00

    &__intro
      display: none

.tags
  display: inline-flex
  margin-bottom: 20px

  .tags-list
    display: inline-flex
    justify-content: flex-start
    align-items: flex-start
    flex-wrap: wrap
    list-style-type: none
    padding: 0
    margin: 0
    &__item
      margin: 0 5px 5px 0
    &__link
      display: inline-flex
      align-items: center
      border: 1px solid #E1E3EA
      box-sizing: border-box
      border-radius: 20px
      padding: 0 11px
      height: 30px
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 18px
      letter-spacing: 0.01em
      color: #3C3C3B
      +tr(0.25s)
      +h
        border-color: $accent
        background-color: #fff
      &.is-active
        border-color: $accent
        background-color: #fff

  .tags-more
    display: flex
    justify-content: center
    align-items: center
    text-align: center
    width: 30px
    height: 30px
    flex: 0 0 30px
    border: 1px solid #3C3C3B
    box-sizing: border-box
    border-radius: 20px
    cursor: pointer
    +tr(0.25s)
    +icon(plus)
    +h
      border-color: $accent
      background-color: #fff
      color: $accent
    &::before
      text-align: center
      width: auto
      margin-left: 2px
    &.is-active
      +icon(minus)

.catalog.--right
  .catalog-sidebar
    margin-right: 0
    margin-left: 30px
    +r(1365)
      margin: 0
  .product-card__badge-dropdown
    left: -10px
    right: auto
    width: 330px
    border-radius: 30px
    +r(1365)
      width: 270px

.catalog.--top
  display: block
  .catalog-sidebar
    width: 100%
    +r(1365)
      width: 280px
  .catalog-list.--grid
    grid-template-columns: repeat(6, 1fr)
    +r(1499)
      grid-template-columns: repeat(5, 1fr)
    +r(1359)
      grid-template-columns: repeat(4, 1fr)
    +r(1199)
      grid-template-columns: repeat(3, 1fr)
      grid-gap: 20px
    +r(767)
      grid-template-columns: repeat(2, 1fr)
      grid-gap: 20px
    +r(575)
      grid-template-columns: repeat(1, 1fr)
    grid-gap: 10px
  .products-filter-footer
    display: none
    +r(1365)
      display: block

.filters-box
  display: block
  position: relative
  background: #ffffff
  border-radius: 30px
  padding: 20px
  margin-bottom: 30px
  +r(1359)
    margin-bottom: 0
    padding: 0

.filters-grid
  display: flex
  align-items: flex-start
  flex-wrap: wrap
  gap: 10px
  +r(1359)
    flex-direction: column
    align-items: stretch
    gap: 5px
  & > *
    +r(1359)
      padding: 12px 15px
      border-bottom: 1px solid rgba(210, 214, 223, 0.3)
      &:last-child
        border-bottom: 0

.search-item
  margin-right: 15px
  position: relative
  +r(1359)
    margin: 0
    .filter-search__clear
      right: 10px
  input
    display: flex
    justify-content: flex-start
    align-items: center
    width: 350px
    height: 35px
    background-color: #F8F9FA
    background-image: url(../img/svgico/search.svg)
