.filter-drop-search.search-item
  margin-right: 0

.filter-drop-price
  margin-top: 12px
  min-width: 212px

.filter-drop
  display: block
  position: relative
  &.--open
    .filter-drop-btn
      border: 1px solid #D2D6DF
      border-radius: 17px 17px 0px 0px
      +r(1359)
        border: none
        border-radius: 0
    .filter-drop-arw
      transform: rotate(180deg)
      color: #3C3C3B
    .filter-drop-box
      top: 35px
      opacity: 1
      visibility: visible
      pointer-events: all
      +tr(0.25s)
      +r(1359)
        top: auto
        height: auto
  &.--active
    .filter-drop-btn
      border: 1px solid #FFAE00
      +r(1359)
        border: none
    .filter-drop-arw
      color: #FFAE00
      +r(1359)
        color: #3C3C3B
.filter-drop-btn
  display: inline-flex
  align-items: center
  border: 1px solid #E1E3EA
  border-radius: 20px
  padding: 0 15px
  width: auto
  height: 35px
  box-sizing: border-box
  +tr(0.25s)
  +h
    border: 1px solid #D2D6DF
  +r(1359)
    border: none
    display: flex
    width: 100%
    padding: 0
    +h
      border: none
.filter-drop-val
  display: block
  font-style: normal
  font-weight: 700
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.02em
  color: #3C3C3B
  margin-top: 2px
.filter-drop-col
  display: block
  font-style: normal
  font-weight: 700
  font-size: 14px
  line-height: 125%
  letter-spacing: 0.02em
  color: #3C3C3B
  margin-left: 6px
  padding-left: 6px
  border-left: 1px solid #F0F1F4
.filter-drop-arw
  display: flex
  justify-content: center
  align-items: center
  width: 14px
  height: 14px
  flex: 0 0 14px
  margin-left: 10px
  +icon(angle-down)
  +r(1365)
    margin-left: auto
.filter-drop-box
  display: block
  min-width: calc(100% + 20px)
  position: absolute
  top: 55px
  left: 0
  background: #FFFFFF
  border: 1px solid #D2D6DF
  border-radius: 0px 20px 20px 20px
  padding: 20px 15px 15px 15px
  margin-top: -1px
  z-index: 5
  pointer-events: none
  visibility: hidden
  opacity: 0
  +r(1359)
    position: relative
    top: auto
    left: auto
    margin-top: 0
    border-radius: 0
    padding: 0
    height: 0
    border: none
    min-width: 100%
.filter-drop-scroll
  margin-right: -15px
  padding-right: 15px
  max-height: 275px
  overflow-y: auto
  scrollbar-color: #C3C8D5 #fff
  scrollbar-width: thin!important
  -moz-appearance: none !important
  &::-webkit-scrollbar
    width: 4px
  &::-webkit-scrollbar-track
    background-color: #fff
  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5
  +r(1359)
    max-height: none
    margin-right: 0
    padding-right: 0
    padding-top: 10px
    padding-bottom: 10px
.filter-drop-list
  list-style-type: none
  padding: 0
  margin: 0
  display: flex
  flex-direction: column
  gap: 5px
  .check,
  .form-switch
    cursor: pointer
  .check__box
    width: 20px
    height: 20px
    flex: 0 0 20px
    margin-right: 6px
  .check__txt
    padding-top: 3px
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
  .form-switch
    font-size: 20px
  .form-check-input
    background-color: transparent
    border-color: #E1E3EA
  .form-check-input:checked
    background-color: transparent
    border-color: #3C3C3B
  .form-switch .form-check-input:checked
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%233C3C3B'/%3e%3c/svg%3e")
  .form-switch .form-check-input:focus
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%233C3C3B'/%3e%3c/svg%3e")
  .form-check-input:active
    filter: brightness(100%)
  .form-check-label
    display: inline-block
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-top: 9px
  label.check.--checkbox-icon
    display: flex
    align-items: center
    .check__txt
      display: flex
      align-items: center
      margin: 0
      padding: 0
