        height: 30px
        flex: 0 0 30px
        margin-right: 8px
        opacity: 0.3
    .check__input:checked ~ .check__txt
      img
        opacity: 1
  .drop
    width: 100%
    height: 35px
    .drop-button
      width: 100%
      height: 35px
      background-position: right 15px center
    .drop-clear
      width: 35px
      height: 35px
      flex: 0 0 35px
      background-size: 15px
.filter-drop-grid
  display: flex
  align-items: center
  flex-wrap: wrap
  gap: 5px
  list-style-type: none
  padding: 0
  margin: 0
  max-width: 205px
  li
    display: flex
    justify-content: center
    align-items: center
    width: 30px
    height: 30px
    flex: 0 0 30px
  .check.--icon
    position: relative
    padding: 0
    .check__input,
    .check__box
      position: absolute
      visibility: hidden
      opacity: 0
    .check__icon
      display: flex
      justify-content: center
      align-items: center
      width: 30px
      height: 30px
      flex: 0 0 30px
      opacity: 0.3
      +tr(0.25s)
