.lk-page__head-right
  margin-left: auto

  +r(767)
    margin-top: 15px
    margin-left: 0

.manager-order-edit 
  display: flex
  flex-direction: column
  align-items: flex-end
  font-weight: 400
  font-size: 12px
  line-height: 120%
  letter-spacing: 0.01em
  color: #B4BACA
  +r(767)
    align-items: flex-start

.button--circle
  width: 45px
  height: 45px
  flex: 0 0 45px
  min-width: 0
  padding: 0
  justify-content: center
  align-items: center
  &::before
    margin-right: 0
    text-align: center

.table-product-card
  max-width: 500px
  
  &__info
    display: flex!important
    flex-wrap: wrap
    align-items: center
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    margin-top: 10px!important
  &__info-label
    color: #B4BACA
    margin-right: 5px
  &__info-data
    color: #3C3C3B
    margin-right: 15px
  &__info-item
    display: flex!important
    align-items: center

.ftable > tbody > tr > td 
  .download__bill
    font-weight: 400
    font-size: 12px
    line-height: 15px
    letter-spacing: 0.02em
    color: #308AFC
    +h
      color: #FFBC08

  .invioce-link
    display: flex
    align-items: center
    +h
      .invioce-link__name
        color: #308AFC
    &__ico
      width: 45px
      height: 45px
      display: flex
      align-items: center
      justify-content: center
      text-align: center
      font-size: 26px
      color: #FFBC08
      +r(767)
        width: 25px
    &__text
      margin-left: 3px
    &__name
      font-weight: 400
      font-size: 14px
      line-height: 18px
      letter-spacing: 0.02em
      color: #3C3C3B
      +tr(.25s)
    &__date
      font-weight: 400
      font-size: 12px
      line-height: 15px
      letter-spacing: 0.02em
      color: #878C98
  
  .product-previve__photo
    width: 100px
    height: 80px
    position: relative
    &::after
      content: ''
      position: absolute
      background: #3C3C3B
      opacity: 0.6
      display: block
      width: 100%
      height: 100%
      +tr(.25s)
      left: 0
      top: 0
      z-index: 2
      mix-blend-mode: multiply
    img
      width: 100%
      height: 100%
      object-fit: cover
    +h
      &::after
        opacity: 0
  .product-previve__plus
    position: absolute
    z-index: 3
    font-weight: 700
    font-size: 14px
    line-height: 18px
    letter-spacing: 0.02em
    color: #FFFFFF
    right: 12px
    bottom: 7px

.lk-page.--manager-orders
  +r(1270)
    .lk-page__filters
      grid-gap: 20px
      display: flex
      flex-wrap: wrap
      align-items: center
      justify-content: space-between

      > div
        flex: 0 0 calc( 50% - 10px )
  +r(767)
    .lk-page__filters
      grid-gap: 20px
      display: flex
      flex-wrap: wrap
      align-items: center
      justify-content: space-between

      > div
        flex: 0 0 100%

.availability
  font-weight: 400
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.02em
  +r(767)
    font-size: 12px

  &.--yellow
    color: #FFAE00
  
  &.--red
    color: #FF5F72

.lk-page__inner-cont 
  display: flex
  flex-wrap: wrap

.lk-page__inner-cont-body
  flex: 0 0 calc( 100% - 198px )
  padding-right: 30px
  +r(1199)
    padding-right: 20px
  +r(767)
    flex: 0 0 100%
    padding-right: 0
    order: 2

.lk-page__inner-cont-sidebar
  flex: 0 0 198px
  +r(767)
    margin-bottom: 20px
    flex: 0 0 100%
    order: 1

.lk-manager-order-create
  .lk-page__table
    margin-top: 0
  .submenu
    margin-bottom: 30px
  .lk-submenu
    display: inline-flex
  .lk-page__filters
    padding-right: 80px
    +r(1300)
      padding-right: 0
    +r(1199)
      grid-gap: 20px
      grid-template-columns: repeat(3, 1fr)
    +r(767)
      grid-template-columns: repeat(1, 1fr)
      margin-top: 0
  .lk-page__title
    +r(767)
      margin-bottom: 20px
  .lk-page__head
    +r(767)
      margin-bottom: 0
  table.ftable > tbody > tr > td .table-product-card.--table-product-card-2 .table-product-card__title
    font-size: 14px
  +r(1199)
    .ftable > tbody > tr > td .button-info-group
      justify-content: flex-end
      margin-right: -15px
    .ftable > tbody > tr > td .button-info-group .button-info__dropdown
      width: 240px
    .ftable > tbody > tr > td .button-info-group > *
      margin-right: 10px
    .ftable > tbody > tr > td .button-info-group > *:last-child
      margin-right: 0
  +r(568)
    .lk-page__table-after-btns > *
      min-width: 135px

.lk-manager-order, .lk-manager-document-invoice, .lk-manager-document-act
  +r(767)
    .lk-page__action-btns
      margin-left: auto
  
+rmin(768)
  .lk-manager-order-create, .lk-manager-order, .lk-manager-document-invoice
    .lk-page__table-after > *:first-child
      max-width: 80%

.lk-manager-cart
  .button-accent
    height: 45px
  .product-card__box:hover .product-card__price
    display: none!important
  .product-card__media img
    max-height: 130px
  .product-card
    .button-small
      height: 24px
      padding: 0 15px
  .product-card__bottom
    height: 24px


.manager-filter
  background: #F0F1F4
  border-radius: 30px 
  padding: 30px
  height: calc( 100% - 65px )
  +r(767)
    height: auto
    padding: 20px


  &__title
    font-weight: 700
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.01em
    color: #3C3C3B
    margin-top: 0
    margin-bottom: 11px
  &__list
    margin: 0
    padding: 0
    list-style: none
  &__item
    margin-bottom: 8px
    position: relative
    padding-left: 8px
    &::before
      width: 3px
      height: 3px
      position: absolute
      content: ''
      display: block
      background: #6C707A
      left: 0
      top: 8px
  &__link
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.01em
    color: #6C707A

.lk-page__table-after-btns
  .action-group
    min-width: 45px
    width: 45px
    padding: 0

  .action-group-btn
    width: 45px
    height: 45px
    font-size: 19px
    border: 2px solid #FFAE00
    color: #FFAE00
    background: transparent
    +tr(.25s)
    .ico_submenu
      font-size: 19px
      color: #FFAE00
      +tr(.25s)
    +h
      color: #fff
      background: #FFAE00
      .ico_submenu
        color: #fff
  .action-group-drop
    right: auto
    left: -13px
    border-radius: 30px 30px 30px 0
    z-index: 6
    box-shadow: 0px 15px 30px -20px rgba(146, 157, 165, 0.2)
    +r(767)
      left: 0

.lk-manager-order
  .lk-page__table-after-btns
    flex-wrap: wrap
    flex: 0 0 calc( 100% - 230px )

    > * 
      margin-bottom: 10px

    +r(1050)
      flex: 0 0 100%
      margin-top: 20px
      margin-left: 0

    +r(767)
      > * 
        width: 100%
        margin-right: 0
