.footable-details.table-w-inner
  > tbody
    > tr
      background: #fff
      border-top: 0
      border-bottom: 1px solid #F0F1F4
      &:last-child
        border-bottom: 0
        > th
          display: none!important
        > td:last-child
          max-width: 100%
  +r(1600)
    > tbody
      > tr
        background: #fff
        display: flex
        align-items: center
        > td
          display: flex
          align-items: center
          height: auto
        &:last-child
          display: flex
          > th
            display: none!important


  
table.footable-details.table-w-inner
  & > tbody
    & > tr
      td
        border: 0


+r(500)
  table.footable-details.table-w-inner > tbody > tr > td:last-child
    max-width: calc( 100% - 100px )

table.table-w-inner
  & > tbody
    & > tr[data-expanded="true"]
      border-bottom: 1px solid #E1E3EA
  .footable-toggle

    &::before
      background-image: url('data:image/svg+xml;utf8,<svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 1.5L5 5.5L9 1.5" stroke="%23B4BACA" stroke-width="1.8" stroke-linecap="round"/></svg>')
      content: ''
      width: 10px
      height: 7px
      margin-top: 3px
      
    &.fooicon-minus
      &::before
        background-image: url('data:image/svg+xml;utf8,<svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 1.5L5 5.5L9 1.5" stroke="%23878C98" stroke-width="1.8" stroke-linecap="round"/></svg>')


.reserve-table-inner
  width: 100%
  background: #fff
  tr
    border: 0!important
    td
      border: 0!important
    td::before
      display: none!important
  
  +r(767)
    > tbody > tr > td:first-child
      height: auto
      .table-product-card
        justify-content: center
        margin: 0
    > tbody
      > tr
        display: flex
        align-items: center
        flex-wrap: wrap
        border-bottom: 5px
        &:last-child
          > td:last-child
            border-bottom: 0!important
        > td
          flex: 0 0 100%
          height: auto
          text-align: center
          &:last-child
            border-bottom: 1px solid #E1E3EA!important
          .table-product-card
            justify-content: center
            text-align: left


.lk-manager-reserve
  .lk-page__filters
    grid-template-columns: repeat(1, 262px)
    +r(767)
      grid-template-columns: repeat(1, 1fr)

