.button-info-group
  display: flex!important
  justify-content: flex-start
  align-items: center
  & > *
    margin-right: 20px
    &:last-child
      margin-right: 0

.button-info
  display: block
  position: relative
  z-index: 1
  &__button
    display: flex
    justify-content: center
    align-items: center
    position: relative
    width: 45px
    height: 45px
    flex: 0 0 45px
    font-size: 20px
    color: #fff
    background: #B4BACA
    border-radius: 50%
    border: none
    z-index: 2
    +tr(0.25s)
  &__dropdown
    display: block
    position: absolute
    top: -10px
    right: -10px
    width: 285px
    padding: 20px 85px 20px 20px
    background: #FFFFFF
    border: 1.5px solid #E1E3EA
    box-shadow: 0px 21px 38px -20px rgba(180, 186, 202, 0.5)
    border-radius: 30px 0px 30px 30px
    z-index: 1
    opacity: 0
    pointer-events: none
    +tr(0.25s)
  &__title
    display: block
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #B4BACA
    padding-bottom: 8px
    margin-bottom: 8px
    border-bottom: 1px solid #E1E3EA
  &__content
    display: flex
    flex-direction: column
    & > *
      display: block
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin-bottom: 5px
      &:last-child
        margin-bottom: 0
    & > a
      +tr(0.25s)
      +h
        color: #FFAE00
  +hh
    z-index: 2
    .button-info__button
      background: #FFAE00
    .button-info__dropdown
      opacity: 1
      pointer-events: all

  &.--table
    .button-info__button
      width: 30px
      height: 30px
      flex: 0 0 30px
      background: none
      color: #000
    .button-info__dropdown
      padding: 20px 50px 20px 20px
      background: #FFFFFF
      box-shadow: 0px 14px 26px -1px rgba(180, 186, 202, 0.5)
      border-radius: 30px 0px 30px 30px
      border: none
      right: -15px
      top: -15px
    .button-info__title
      border-bottom: 0
      margin-bottom: 5px
      padding-bottom: 0
      font-style: normal
      font-weight: 700
      font-size: 14px
      line-height: 140%
      letter-spacing: 0.02em
      color: #3C3C3B
    .button-info__content
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 140%
      letter-spacing: 0.02em
      color: #3C3C3B

  &.--favorite
    .button-info__button
      color: #57CA70

  &.--discount
    .button-info__button
      color: #FF5F72

  &.--view
    .button-info__button
      width: 30px
      height: 30px
      flex: 0 0 30px
      background: #fff
      border-radius: 50%
      color: #C3C8D5
      font-size: 12px
      box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
      &::before
        width: auto
      +h
        color: #FFAE00

.button-circle
  display: flex
  justify-content: center
  align-items: center
  position: relative
  width: 45px
  height: 45px
  flex: 0 0 45px
  font-size: 20px
  color: #fff
  background: #B4BACA
  border-radius: 50%
  border: none
  +tr(0.25s)
  &::before
    width: auto
  +h
    background: #FFAE00
    color: #fff

.col
  display: inline-flex!important
  justify-content: center
  align-items: center
  border: 1.5px solid #E1E3EA
  border-radius: 30px
  width: 120px
  height: 45px

  font-style: normal
  font-weight: 400
  font-size: 16px
  line-height: 100%
  text-align: center
  letter-spacing: 0.05em
  color: #3C3C3B

  &.--waiting
    border: 1.5px solid #FFAE00

    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    text-align: center
    letter-spacing: 0.03em
    color: #3C3C3B

    pointer-events: none

.products-select
  display: block
  position: relative

  .swiper
    margin: 0
    padding: 0

  .product-card
    &__box
      border-radius: 0
      min-height: 0
      +h
        box-shadow: none
    &__media
      padding-top: 0
      padding-left: 10px
      padding-right: 10px
      height: 120px
    &__label
      top: 5px
      left: 15px
      height: 15px
      font-style: normal
      font-weight: 700
      font-size: 8px
      line-height: 15px
      letter-spacing: 0.06em
      text-transform: uppercase
      color: #FFFFFF
      padding: 0 5px
    &__del
      display: flex
      justify-content: center
      align-items: center
      position: absolute
      top: 0
      right: 15px
      width: 30px
      height: 30px
      flex: 0 0 30px
      background: #FFFFFF
      box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
      border-radius: 30px
      border: none
      font-size: 15px
      color: #B4BACA
      +tr(.25s)
      +h
        color:#FF6A6A
    &__info
      padding: 10px 15px
      min-height: 0
    &__title
      min-height: 33px
      margin: 0
      a
        font-style: normal
        font-weight: 700
        font-size: 14px
        line-height: 117%
        text-align: center
        letter-spacing: 0.02em
        text-transform: uppercase
        color: #3C3C3B
        +tr(0.25s)
        +h
          color: $accent
    &__bottom
      padding: 0 15px
    &__price
      display: block!important
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 100%
      text-align: center
      letter-spacing: 0.02em
      color: #3C3C3B
      min-height: auto
    &__price-rrc
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #9FA4B0
      margin-top: 3px
      margin-bottom: 10px
    &__col
      display: flex
      justify-content: center
      align-items: center
      width: 80px
      height: 30px
      border: 1.5px solid #E1E3EA
      border-radius: 30px
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      text-align: center
      letter-spacing: 0.02em
      color: #9FA4B0
      input
        width: 17px
        margin-top: -2px
        border: 0
        color: #9FA4B0
        padding: 0
        text-align: center

.import-box
  display: block
  position: relative
  max-width: 700px
  background: #FFFFFF
  border-radius: 30px
  padding: 30px
  +r(1359)
    max-width: 100%
  +r(767)
    padding: 20px 17px
  &__upload
    margin-bottom: 20px



  &__subtitle
    font-style: normal
    font-weight: 400
    font-size: 18px
    line-height: 130%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 20px
  &__example
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 130%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 20px
    p
      margin-bottom: 5px
    ul
      padding-left: 20px
      margin: 0
      li
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 130%
        letter-spacing: 0.02em
        color: #3C3C3B
  &__info
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 130%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 20px
  &__textarea
    margin-bottom: 30px
    +r(767)
      margin-bottom: 20px
  &__submit
    display: block
    position: relative
    +r(767)
      text-align: right

    .button-accent
      padding: 0 20px
      height: 45px

.lk-page.--order-import-result

  .lk-page__title
    margin-bottom: 30px
    +r(767)
      margin-bottom: 10px

  .ftable
    tbody
      tr
        td
          height: 50px
        td:first-child::before
          opacity: 1
          border: 1px solid #E1E3EA
          & > *
            position: relative
            z-index: 2

        &.--sts-lost
          td:first-child::before
            border: 1px solid rgba(255, 174, 0, 0.2)
            background: rgba(255, 174, 0, 0.2)
            z-index: 1
        &.--sts-del
          td:first-child::before
            border: 1px solid rgba(#FF6A6A, 0.2)
            background: rgba(#FF6A6A, 0.2)
            z-index: 1

  .lk-page__table-after
    +r(767)
      margin-top: 0
    & > *:first-child
      +r(767)
        margin-top: 0
  .lk-page__table-after-btns
    +r(767)
      margin-top: 0
      justify-content: flex-end

  .lk-page__table-after-btns > *
    +r(767)
      min-width: auto

.lk-page
  &__import
    display: flex
    justify-content: space-between
    align-items: stretch
  &__import-table
    flex-grow: 1
  &__import-banner
    padding-left: 30px
    padding-bottom: 20px
    +r(1199)
      display: none

.import-banner
  display: block
  position: relative
  width: 262px
  height: 100%
  &__bg
    display: flex
    justify-content: center
    align-items: flex-end
    position: relative
    height: 100%
    padding: 60px 30px
    box-sizing: border-box
    &::before
      content: ''
      display: block
      position: absolute
      left: 0
      top: 0
      width: 100%
      height: 100%
      background: rgba(60, 60, 59, 0.7)
  &__content
    position: relative
    z-index: 2
    text-align: center
  &__title
    font-style: normal
    font-weight: 700
    font-size: 24px
    line-height: 120%
    text-align: center
    color: #FFFFFF
  &__btn
    margin-top: 20px
    .button-outline
      color: #fff

.lk-page.--order-save
  .lk-page__action-btns
    +r(1199)
      margin-top: 0
    +r(767)
      align-self: flex-end
  .lk-page__table-after
    > *:first-child
      +r(1199)
        max-width: 60%
      +r(767)
        max-width: 100%

.lk-page.--order-delivery
  .lk-page__table-after
    > *:first-child
      +r(1199)
        max-width: 70%
      +r(767)
        max-width: 100%
  .lk-page__table-after-btns
    +r(1199)
      flex-direction: column
      justify-content: flex-start
      align-items: flex-start
      & > *
        margin-right: 0
        margin-bottom: 10px
        &:last-child
          margin-bottom: 0
    +r(767)
      align-items: flex-end

.lk-page.--order-success
  .lk-page__action-btns
    +r(1199)
      margin-top: 0
    +r(767)
      align-self: flex-end
  .lk-page__table-after
    > *:first-child
      +r(1199)
        max-width: 60%
      +r(767)
        max-width: 100%

.lk-page.--order-return
  .lk-page__table-after
    +r(1199)
      flex-wrap: wrap
    > *:first-child
      max-width: 80%
      flex-grow: 0
      align-items: flex-start
      flex-wrap: nowrap
      +r(1199)
        flex-wrap: wrap
        max-width: 100%
    & > div:nth-child(2)
      +r(767)
        order: 1
  .lk-page__table-after-btns
    flex-wrap: wrap
    +r(1199)
      margin-left: 0
      margin-top: 20px
    & > *
      margin-bottom: 10px
      +r(1359)
        margin-right: 10px
      +r(767)
        margin-right: 0
        width: 100%

.lk-page.--order-creat
  .lk-page__head,
  .lk-page__title
    margin-bottom: 0
  .lk-page__action
    margin-bottom: 0
    +r(1199)
      display: block
  .lk-page__table
    margin-top: 0
  .lk-page__filters
    margin-bottom: 20px
    grid-template-columns: repeat(3, 1fr)
    +r(767)
      grid-template-columns: repeat(1, 1fr)
      grid-gap: 10px
  .submenu
    margin-bottom: 0
  .lk-page__table
    +r(1679)
      padding-left: 20px
      padding-right: 20px
    +r(1199)
      padding: 0
  .ftable
    > thead
      > tr.footable-header
        > th
          padding: 8px 3px
          +r(1199)
            padding: 10px 12px
            &:first-child
              padding-left: 20px
    > tbody
      > tr
        > td
          padding: 8px 3px
          +r(1199)
            padding: 10px 12px

          .table-product-card
            +r(1679)
              margin-left: 0
            &__img
              +r(1679)
                margin-right: 5px

          .status
            font-size: 10px
            white-space: nowrap

          .col
            +r(1679)
              width: 80px
              height: 35px
              font-size: 12px

          .button-info-group
            +r(1199)
              justify-content: flex-end
              & > *
                margin-right: 10px
                &:last-child
                  margin-right: 0
              .button-info__dropdown
                width: 240px

    > tbody
      > tr.footable-detail-row
        > td:first-child
          padding: 0 20px

.lk-page.--cart
  .lk-page__action
    +r(767)
      display: flex
      justify-content: space-between
      flex-direction: row
      align-items: center

  .lk-page__table-total
    +r(767)
      flex-direction: column

  .table-total.--row
    +r(767)
      padding-left: 15px
      .table-total__item
        margin-right: 14px
        margin-bottom: 10px

.lk-page.--documents
  .lk-page__title
    +r(1359)
      text-align: center
  .lk-page__action
    +r(1359)
      justify-content: center
    +r(767)
      margin-bottom: 0
  .lk-page__filters
    +r(1679)
      grid-template-columns: repeat(4, 1fr)
    +r(1359)
      grid-template-columns: repeat(2, 1fr)
    +r(767)
      display: block
      margin-top: 0
      & > * + *
        margin-top: 15px

  .table-button-group
    +r(767)
      display: flex
      flex-direction: column
      justify-content: flex-end
      align-items: flex-end
      & > *
        margin-right: 0
        margin-bottom: 8px

.lk-manager-reserve
  .table-button-group
    +r(767)
      display: flex
      flex-direction: column
      justify-content: flex-end
      & > *
        margin-right: 0!important
        margin-bottom: 8px
        width: 127px


.lk-page.--documents-complaint,
.lk-page.--documents-return
  .lk-page__action-btns
    +r(767)
      align-self: flex-end

.lk-page.--waiting
  .lk-page__filters
    +r(1679)
      grid-template-columns: repeat(4, 1fr)
    +r(1199)
      grid-template-columns: repeat(1, 1fr)

.lk-page.--receivables
  .lk-page__filters
    +r(1679)
      grid-template-columns: repeat(4, 1fr)
    +r(1199)
      grid-template-columns: repeat(3, 1fr)
    +r(767)
      grid-template-columns: repeat(1, 1fr)
      grid-gap: 15px

.page-main.lk-order
  .lk-page__table-after > *:first-child
    align-items: center

.lk-page.--order-creat
  .lk-page__filters
    grid-template-columns: repeat(3, 262px)
    margin-bottom: 20px
    +r(1199)
      grid-template-columns: repeat(3, 1fr)
      grid-gap: 15px

.lk-page.--cart
  .lk-page__action-btns
    .button-outline.ico_trash
      width: 45px
      padding: 0
      border: 1px solid #F0F1F4
      &:before
        font-size: 20px
        color: #3C3C3B
        margin: 0
        width: auto
    .button-outline.ico_group
      +r(374)
        padding: 0 10px
  .lk-page__table-after
    h5
      font-style: normal
      font-weight: 400
      font-size: 18px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin: 0


.cart-info
  display: flex
  align-items: flex-start
  background: #FFFFFF
  border: 2px solid #FFD3D3
  border-radius: 0px 30px 30px 30px
  padding: 20px
  margin: 30px 0
  width: 100%
  &__ico
    font-size: 26px
    color: #FF5F72
    margin-right: 15px
  &__box
    display: block
  &__title
    font-style: normal
    font-weight: 700
    font-size: 18px
    line-height: 100%
    letter-spacing: 0.01em
    color: #3C3C3B
    margin: 0 0 8px 0
  &__subtitle
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin: 0 0 8px 0
  &__text-info
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #9FA4B0
    margin: 0 0 8px 0
  &__list
    padding-left: 15px
    li
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      & + li
        margin-top: 5px
      a
        color: #308AFC
      span
        color: #FF5F72
        white-space: nowrap

.quantity-cont
  position: relative
  display: inline-block
  &.is-done
    .quantity-cont__badge
      display: block
  .quantity-cont__badge
    display: none
    width: 18px
    height: 18px
    background-image: url('data:image/svg+xml;utf8,<svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg"><circle cx="9" cy="9" r="9" fill="%2357CA70"/><path d="M5 9L8 12L13 6" stroke="white" stroke-width="2"/></svg>')
    position: absolute
    right: 0
    top: 0
  &.quantity--gray
    .col
      color: #9FA4B0


.lk-orders
  .ftable > tbody > tr > td:first-child
    overflow: hidden

.order-counter
  .counter
    max-width: 120px
    border: 1.5px solid #E1E3EA
    border-radius: 100vw

  .counter__btn
    width: 30px
    flex:  0 0 30px
    border: 0

.lk-cart
  .lk-page__head
    flex-direction: row
    align-items: center

  .button-circle.ico_print
    margin-left: auto