.offcanvas-overlay
  display: block
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  background: rgba(255, 255, 255, 0.1)
  backdrop-filter: blur(10px)

  opacity: 0
  pointer-events: none
  z-index: -1

  +tr(0.25s)

  &.is-show
    opacity: 1
    pointer-events: none
    z-index: 11

.offcanvas-menu
  display: block
  position: fixed
  top: 0
  right: 0
  width: 600px
  background: #3C3C3B
  height: 100vh
  z-index: 12
  +tr(0.3s)
  transform: translateX(100%)
  &.is-show
    transform: translateX(0)

  +r(575)
    width: 100%

  &__box
    display: block
    position: relative
    padding: 62px 90px 20px
    overflow-y: auto
    height: 100vh
    +r(575)
      padding: 0 0 0 20px
      margin-top: 20px
      height: calc(100vh - 40px)

    scrollbar-color: $accent #E2EBEC
    scrollbar-width: thin!important
    -moz-appearance: none !important

    &::-webkit-scrollbar
      width: 4px

    &::-webkit-scrollbar-track
      background-color: #E2EBEC

    &::-webkit-scrollbar-thumb
      background-color: $accent

    .logo
      justify-content: flex-start
      margin-bottom: 35px
      img
        max-width: 140px

  &__close
    display: block
    position: absolute
    top: 80px
    right: 90px
    cursor: pointer
    font-size: 20px
    color: #fff
    z-index: 5
    +tr(0.5s)
    +h
      color: $accent
      transform: rotate(180deg)
    +r(575)
      top: 25px
      right: 25px
      font-size: 16px

  &__list
    display: block
    position: relative

.offcanvas-menu-list
  display: block
  position: relative
  list-style-type: none
  padding: 0
  margin: 0

.offcanvas-menu-item
  display: block
  position: relative
  border-bottom: 2px solid rgba(#F8F9FA, 0.05)
  &.offcanvas-menu-item--active
    .offcanvas-menu-link
      span
        display: inline-block
        position: relative

  &:first-child
    .offcanvas-menu-link:before
      display: none

.offcanvas-menu-link
  display: block
  position: relative
  font-style: normal
  font-weight: 700
  font-size: 16px
  line-height: 100%
  letter-spacing: 0.02em
  color: #FFFFFF
  padding: 20px 30px
  +tr(0.3s)
  +h
    text-decoration: none
    color: #fff
    &::before
      color: $accent
  +r(575)
    line-height: 2.4
    padding: 10px 15px

  +icon(angle-right)
  &::before
    position: absolute
    top: 50%
    right: 20px
    margin-top: -8px
    display: flex
    justify-content: center
    align-items: center
    color: rgba(#fff, 0.3)
    +tr(0.25s)

.offcanvas-submenu-list
  display: block
  position: relative
  list-style-type: none
  margin: 8px 0 35px
  padding-left: 65px
  +r(575)
    margin: 10px 0 20px
    padding-left: 50px

.offcanvas-submenu-item
  display: block
  position: relative

  & + .offcanvas-submenu-item
    margin-top: 14px

  &.offcanvas-submenu-item--active
    .offcanvas-submenu-link
      color: #fff
      &::before
        background: $accent


.offcanvas-submenu-link
  display: block
  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.02em
  color: #FFFFFF
  +tr(0.3s)
  +h
    text-decoration: none
    color: #fff
    &::before
      background: $accent
  &::before
    content: ''
    display: block
    position: absolute
    top: 50%
    left: -16px
    margin-top: -3px
    width: 6px
    height: 6px
    background: rgba(255, 255, 255, 0.3)
