.section-banner-promo
  display: block
  position: relative
  margin: 75px 0
  +r(1199)
    margin: 60px -10.5px
  +r(767)
    margin: 30px -10.5px

.banner-promo
  display: block
  position: relative
  &__bg
    display: flex
    justify-content: center
    align-items: center
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    padding: 55px
    min-height: 260px
    +r(767)
      background-size: 768px 260px

  &__content
    text-align: center
    .button-outline
      color: #fff
  &__title
    font-style: normal
    font-weight: 700
    font-size: 36px
    line-height: 120%
    color: #FFFFFF
    margin-bottom: 20px
    +fz(36, 24)
  &__text
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 140%
    text-align: center
    letter-spacing: 0.02em
    color: #FFFFFF