.section-brands
  display: block
  position: relative
  margin: 75px 0 60px
  +r(767)
    margin-top: 85px
    margin-bottom: 40px

.brand-card
  display: block
  position: relative
  &__link
    display: block
    position: relative
    height: 125px
    overflow: hidden
    +h
      .brand-card__desc
        opacity: 1
        pointer-events: all
        visibility: visible
  &__media
    display: flex
    justify-content: center
    align-items: center
    background: #FFFFFF
    border: 4px solid #F8F9FA
    height: 125px
    padding: 5px

    img
      max-width: 100%

  &__desc
    display: block
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: #E1E3EA
    border: 4px solid #F8F9FA
    padding: 20px 20px 40px
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 130%
    color: #3C3C3B
    +line(4)
    +r(767)
      padding: 10px

    opacity: 0
    pointer-events: none
    visibility: hidden

    +tr(0.25s)

.button-more-brands
  display: block
  position: absolute
  right: 15px
  bottom: 10px
  border-width: 1px
  display: flex
  justify-content: center
  align-items: center
  border: 1px solid #fff
  margin: 0
  border-radius: 100vw
  +tr(0.25s)
  +icon(angle-right)
  padding-left: 12px
  +h
    border: 1px solid #000
  &::before
    text-align: center
    margin-top: -1px
    margin-right: -3px
    font-size: 14px
    order: 2
    font-size: 20px
    display: flex
    justify-content: center
    align-items: center
    color: #000

.brands-slider
  .swiper
    margin: 0
    padding: 0