.others-article
  margin-bottom: 60px

.news-card
  display: block
  position: relative
  +h
    .news-card__box
      box-shadow: 0px 14px 26px -14px rgba(60, 60, 59, 0.2)
    .news-card__btn
      .button-more
        border: 1px solid #3C3C3B
        &::after
          opacity: 1
  &__box
    display: block
    position: relative
    background: #FFFFFF
    border-radius: 30px
    overflow: hidden
    +tr(0.25s)
  &__media
    display: block
    position: relative
    a
      display: block
      position: relative
      width: 100%
      height: 240px
    img
      display: block
      position: absolute
      top: 50%
      left: 50%
      transform: translate(-50%, -50%)
      width: 100%
      height: 100%
      object-fit: cover
      object-position: center
  &__info
    padding: 30px
    +r(767)
      padding: 20px 16px
  &__title
    margin-bottom: 5px
    min-height: 72px
    +r(767)
      min-height: auto
    a
      font-style: normal
      font-weight: 700
      font-size: 20px
      line-height: 120%
      letter-spacing: 0.02em
      text-transform: uppercase
      color: #3C3C3B
      +line(3)
      +tr(0.25s)
      +h
        color: $accent
  &__text
    min-height: 78px
    +r(767)
      min-height: auto
    p
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 140%
      display: flex
      align-items: center
      letter-spacing: 0.02em
      margin: 0
      +line(4)
  &__date
    color: #B4BACA
  &__bottom
    margin-top: 20px
    display: flex
    justify-content: space-between
    align-items: center
  &__numb
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 120%
    letter-spacing: 0.02em
    color: #3C3C3B
  &__btn
    .button-more
      width: 30px
      height: 30px
      flex: 0 0 30px
      border: 1px solid #E1E3EA
      &:after
        content: ''
        font-size: 0
        display: block
        width: 30px
        height: 30px
        flex: 0 0 38px
        position: absolute
        top: calc(50% - 2px)
        left: calc(50% + 1px)
        transform: translate(-50%, -50%)
        border: 6px solid $accent
        border-radius: 50%
        clip-path: polygon(50% 0, 50% 50%, 100% 50%, 100% 0%)
        opacity: 0
        +tr(0.25s)

.news-card.--horisontal
  .news-card__box
    display: flex
    justify-content: flex-start
  .news-card__media
    +r(1199)
      width: 40%
      flex: 0 0 40%
    a
      width: 230px
      height: 240px
      flex: 0 0 230px
      min-height: 100%
      +r(1199)
        width: 100%
        flex: 0 0 100%
        height: 100%
  .news-card__info
    display: flex
    flex-direction: column
    align-self: stretch
    flex-grow: 1
    +r(1199)
      padding: 30px 15px 30px 10px
    +r(767)
      padding: 15px 15px 15px 10px
  .news-card__title
    min-height: 72px
    a
      +line(3)
      +r(1199)
        font-size: 16px
  .news-card__text
    margin-bottom: 15px
    min-height: 78px
    p
      +line(4)
      +r(1199)
        font-size: 14px
  .news-card__bottom
    margin-top: auto


.page-news
  .page-content
    padding: 10px 0 40px
  .others-article
    .section-header
      padding-bottom: 0
      margin-bottom: 0