body
  background: #F8F8F8

.page-wrapper
  min-width: 320px
  min-height: 100vh
  max-width: 1920px
  margin: 0 auto
  display: flex
  flex-direction: column
  justify-content: space-between
  overflow: hidden
  .page-main
    flex-grow: 1

.copy-text
  cursor: pointer

.stop-scroll
  overflow: hidden

.nowrap
  white-space: nowrap

.scroll-box
  scrollbar-color: $accent #E2EBEC
  scrollbar-width: thin!important
  -moz-appearance: none !important

  &::-webkit-scrollbar
    width: 4px

  &::-webkit-scrollbar-track
    background-color: #E2EBEC

  &::-webkit-scrollbar-thumb
    background-color: $accent

input[type="number"]:not(.--arrows)
  -moz-appearance: textfield

input[type=number]:not(.--arrows)::-webkit-inner-spin-button,
input[type=number]:not(.--arrows)::-webkit-outer-spin-button
  -webkit-appearance: none
  margin: 0

.--decor
  &::before
    content: ''
    display: block
    position: absolute
    left: 0
    width: 100%
    height: 180px
    background-image: url(../img/bg_decor.png)
    background-repeat: no-repeat
    background-size: cover
    z-index: -1

.datepicker--day-name,
.datepicker--cell.-current-
  color: $accent
.datepicker--cell.-selected-
  background-color: $accent

.button
  display: inline-flex
  justify-content: center
  align-items: center
  height: 40px
  padding: 0 30px
  background: #3C3C3B
  border-radius: 40px
  border: none
  +tr(0.25s)

  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.04em
  color: #FFFFFF

  &:hover
    background-color: $accent
    color: #fff!important

  &::before
    margin-right: 10px

.button-small
  height: 30px
  padding: 0 15px

.button-accent.button-xsmall
  height: 30px
  padding: 0 15px
  font-size: 12px

.button-accent
  display: inline-flex
  justify-content: center
  align-items: center
  height: 40px
  padding: 0 30px
  background: $accent
  border-radius: 40px
  border: none
  +tr(0.25s)
  font-style: normal
  font-weight: 700
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.04em
  color: #FFFFFF

  &.is-disable
    opacity: .5
    pointer-events: none

  &:hover
    background-color: #FF8A00
    color: #fff!important

  &::before
    margin-right: 10px

.button-break
  display: inline-flex
  justify-content: center
  align-items: center
  height: 40px
  padding: 0 20px
  border-radius: 40px
  border: 2px solid #628EFF
  +tr(0.25s)

  font-style: normal
  font-weight: 700
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.04em
  color: #3C3C3B

  &:hover
    background-color: #628EFF
    color: #fff

  img
    margin-right: 8px

.button-outline
  display: inline-flex
  justify-content: center
  align-items: center
  height: 40px
  padding: 0 20px
  border-radius: 40px
  border: 2px solid $accent
  +tr(0.25s)

  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.04em
  color: #3C3C3B

  &:hover
    background-color: $accent
    color: #fff!important
    &::before
      color: #fff!important

  &::before
    margin-right: 10px
    font-size: 25px
    color: $accent
    +tr(0.25s)

.button-small
  height: 30px
  padding: 0 15px

.button-more
  display: flex
  justify-content: center
  align-items: center
  width: 35px
  height: 35px
  flex: 0 0 35px
  border: 2px solid #FFFFFF
  border-radius: 50%
  margin: 0
  position: relative
  +tr(0.25s)
  +icon(angle-right)
  &::before
    margin-right: -2px
    font-size: 20px
    display: flex
    justify-content: center
    align-items: center
    color: #000
    text-align: center
  +h
    border: 2px solid #000000

.button-back
  display: inline-flex
  flex-direction: row-reverse
  align-items: center
  position: relative
  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.04em
  color: #FFFFFF
  +tr(0.25s)
  +h
    color: $accent

  +ico()
    margin-right: 14px
    display: flex
    justify-content: center
    align-items: center
    width: 35px
    height: 35px
    flex: 0 0 35px
    border: 1px solid rgba(#FFFFFF, 0.4)
    border-radius: 50%
    text-align: center
    font-size: 20px
    margin: 0

  &:after
    content: ''
    font-size: 0
    display: block
    width: 38px
    height: 38px
    flex: 0 0 38px
    position: absolute
    top: -2px
    left: -2px
    border: 6px solid $accent
    border-radius: 50%
    clip-path: polygon(50% 0%, 50% 51%, 0 50%, 0 0)

.button-upload
  background: none
  border: 1.5px dashed #FFAE00

  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 125%
  letter-spacing: 0.03em
  color: #3C3C3B
  cursor: pointer

  height: 45px
  max-width: 100%

  &::before
    color: $accent
    font-size: 18px
    margin-right: 10px
    width: auto

  +h
    background: none
    border: 1.5px solid #FF8A00
    color: #3C3C3B

    .file-upload-text
      color: #3C3C3B

  .ico_close
    display: none
    justify-content: center
    align-items: center
    margin-left: 15px
    color: #FFB5B5
    font-size: 12px
    width: 25px
    height: 25px
    flex: 0 0 25px
    position: relative
    z-index: 2
    +h
      color: #FF6A6A

.button--red
  background: #FF6A6A
  color: #fff
  border: 2px solid #FF6A6A
  +h
    background: transparent
    color: #3C3C3B!important

.button-upload
  &.is-upload
    border: none
    background: #F8F9FA
    &::before
      display: none
    .ico_close
      display: flex

.button-delete
  display: inline-flex
  justify-content: center
  align-items: center
  width: 30px
  height: 30px
  flex: 0 0 30px
  border: none
  border-radius: 50%
  background-color: #fff
  box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
  font-size: 16px
  color: #C3C8D5

  +h
    color: #FF6A6A

.button-icon
  display: inline-flex!important
  justify-content: center
  align-items: center
  width: 30px
  height: 30px
  flex: 0 0 30px
  padding: 0
  text-align: center
  border: none
  border-radius: 50%
  background-color: #fff
  box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
  font-size: 20px!important
  color: #C3C8D5!important
  +tr(0.25s)
  &::before
    margin: 0
  +h
    color: $accent!important

.button-table
  display: inline-flex!important
  justify-content: center
  align-items: center
  width: 30px!important
  height: 30px!important
  flex: 0 0 30px!important
  border: none
  border-radius: 50%
  background-color: #fff
  box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
  font-size: 20px!important
  color: #C3C8D5!important
  padding: 0
  box-sizing: border-box
  +hh
    background-color: #fff
    box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)!important
    color: #FFAE00!important

.swiper
  padding: 20px
  margin: -20px

.swiper-nav
  display: flex
  justify-content: space-between
  align-items: center
  width: 150px

  .swiper-button-prev,
  .swiper-button-next
    position: relative
    inset: auto
    display: flex
    justify-content: center
    align-items: center
    width: 35px
    height: 35px
    flex: 0 0 35px
    border: 1px solid #E1E3EA
    border-radius: 50%
    margin: 0
    +tr(0.25s)
    &::before
      font-size: 20px
      display: flex
      justify-content: center
      align-items: center
      color: #000
      text-align: center
    &:after
      display: none
      +tr(0.25s)
      opacity: 0
    +h
      border: 1px solid #3C3C3B
      &::after
        opacity: 1

  .swiper-button-prev
    +icon(angle-left)
    
    &:after
      font-size: 0
      display: block
      width: 38px
      height: 38px
      flex: 0 0 38px
      position: absolute
      top: calc(50% - 2px)
      left: calc(50% - 1px)
      transform: translate(-50%, -50%)
      border: 6px solid $accent
      border-radius: 50%
      clip-path: polygon(50% 0%, 50% 51%, 0 50%, 0 0)
      opacity: 0

  .swiper-pagination
    position: relative
    inset: auto
    width: auto
    margin: 0 10px
    font-style: normal
    font-weight: 400
    font-size: 18px
    line-height: 100%
    text-align: center
    letter-spacing: 0.04em
    color: #878c98

    .swiper-pagination-current
      color: #3C3C3B

  .swiper-button-next
    +icon(angle-right)
    &::before
      margin-right: -2px
    &:after
      font-size: 0
      display: block
      width: 38px
      height: 38px
      flex: 0 0 38px
      position: absolute
      top: calc(50% - 2px)
      left: calc(50% + 1px)
      transform: translate(-50%, -50%)
      border: 6px solid $accent
      border-radius: 50%
      clip-path: polygon(50% 0, 50% 50%, 100% 50%, 100% 0%)



.swiper-button-lock
  display: none!important

.section-header
  display: block
  position: relative
  margin-bottom: 20px
  +r(575)
    padding-bottom: 35px

.section-content
  display: block
  position: relative
  .--section-slider-nav
    position: absolute
    top: -52px
    right: 0
    z-index: 1
    +r(575)
      right: auto
      left: 0
    &.--mobile
      +r(767)
        position: static
        top: auto
        left: auto
        right: auto
        margin: 20px auto 0

.page-content
  padding: 30px 0 60px

.page-search
  .page-content
    padding: 0 0 60px

.pagination
  align-items: center
  margin-bottom: 0

.page-item
  display: flex
  justify-content: center
  align-items: center

.page-link
  display: flex
  justify-content: center
  align-items: center
  border: none
  background: none
  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.04em
  color: #3C3C3B
  padding: 5px 10px
  +hh
    background: none
    color: $accent
  +ico()
    display: flex
    justify-content: center
    align-items: center

  &[aria-label="Previous"],
  &[aria-label="Next"]
    transform-origin: center
    +tr(0.25s)
    +h
      transform: scale(1.2)


.page-item.active .page-link
  display: flex
  justify-content: center
  align-items: center
  width: 35px
  height: 35px
  flex: 0 0 35px
  background: none
  border: 1px solid #3C3C3B
  border-radius: 50%
  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.04em
  color: #3C3C3B
  z-index: 1

.page-item:first-child .page-link
  padding: 0

.page-item:last-child .page-link
  padding: 0

.page-breadcrumb
  padding: 0 0 20px 0

  .breadcrumb
    margin: 0

  .breadcrumb-item
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #B4BACA

    a
      display: inline-block
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #9FA4B0
      +h
        color: $accent

  .breadcrumb-item+.breadcrumb-item:before
    content: '|'
    color: #F0F1F4

  .breadcrumb-item+.breadcrumb-item
    padding-left: 10px

  .breadcrumb-item+.breadcrumb-item:before
    padding-right: 10px

.page-pagination
  margin: 35px 0 40px
  display: flex
  justify-content: flex-end
  align-items: center
  position: relative
  z-index: 1
  .drop
    margin-left: 20px
    flex: 0 0 90px
  .drop-button
    height: 35px
  .pagination
    justify-content: center


.video-box
  display: block
  position: relative
  &__link
    display: block
    position: relative
    &::after
      content: ''
      display: block
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      background: rgba(#000, 0.6)
      z-index: 1

  &__img
    display: block
    position: relative
    width: 100%
    +tr(0.25s)
  &__btn
    display: flex
    justify-content: center
    align-items: center
    position: relative
    width: 75px
    height: 75px
    flex: 0 0 75px
    background-image: url(../img/btn-play.svg)
    background-position: center
    background-repeat: no-repeat
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    border: 2px solid rgba(255, 255, 255, 0.5)
    border-radius: 50%
    z-index: 2
    +tr(0.25s)

    &:after
      content: ''
      display: block
      position: absolute
      top: calc(50% - 1px)
      left: calc(50% + 1px)
      flex: 0 0 78px
      width: 78px
      height: 78px
      transform: translate(-50%, -50%)
      border: 6px solid #FFAE00
      border-radius: 50%
      font-size: 0
      clip-path: polygon(50% 0, 50% 50%, 100% 50%, 100% 0%)
      opacity: 0
      +tr(0.25s)

    +h
      &:after
        opacity: 1

.fancybox__thumbs .carousel__slide .fancybox__thumb:after
  content: ""
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  border: 3px solid $accent
  opacity: 0
  transition: opacity .15s ease
  border-radius: var(--fancybox-thumbs-border-radius,4px)


.status
  display: flex
  justify-content: flex-start
  align-items: center
  width: 180px
  height: 35px
  box-sizing: border-box
  border: 2px solid #CDEFD4
  border-radius: 20px
  padding: 0 10px

  font-style: normal
  font-weight: 400
  font-size: 16px
  line-height: 100%
  letter-spacing: 0.02em
  color: #3C3C3B

  span.circle
    display: flex!important
    justify-content: center
    align-items: center
    width: 20px
    height: 20px
    flex: 0 0 20px
    margin-right: 8px
    border-radius: 50%
    background: #CDEFD4
    &::before
      content: ''
      display: block
      position: relative
      width: 5px
      height: 5px
      flex: 0 0 5px
      border-radius: 50%
      background: #57CA70

  &.--status-1
    border: 2px solid #CDEFD4
    span.circle
      background: #CDEFD4
      &::before
        background: #57CA70

  &.--status-2
    border: 2px solid #FFD3D3
    span.circle
      background: #FFD3D3
      &::before
        background: #FF6A6A

  &.--status-3
    border: 2px solid #FEE5B4
    span.circle
      background: #FEE5B4
      &::before
        background: #FBA705

  &.--status-4
    border: 2px solid #D0DEFF
    span.circle
      background: #D0DEFF
      &::before
        background: #628EFF

  &.--status-5
    border: 2px solid #DFDBFF
    span.circle
      background: #DFDBFF
      &::before
        background: #9588FF

  &.--status-6
    border: 2px solid #E2E4E7
    span.circle
      background: #E2E4E7
      &::before
        background: #9FA4B0

.list-files
  list-style-type: none
  padding: 0
  margin: 0
  &__item
    padding: 10px
    display: flex
    justify-content: space-between
    align-items: center
    background: #F8F9FA
    margin-bottom: 2px
    &:last-child
      margin-bottom: 0
  &__title
    display: block
    position: relative
    +line(1)
    flex-grow: 1
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-right: 20px
  &__link
    display: flex
    justify-content: center
    align-items: center
    width: 25px
    height: 25px
    flex: 0 0 25px
    font-size: 25px
    color: $accent
    +tr(0.25s)
    +h
      color: #000

.switcher
  display: flex
  justify-content: flex-start
  align-items: center
  background: #F0F1F4
  border-radius: 30px
  overflow: hidden
  a
    display: flex
    justify-content: center
    align-items: center
    height: 45px
    padding: 0 20px
    border: 2px solid transparent
    border-radius: 30px

    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    text-align: center
    letter-spacing: 0.03em
    color: #3C3C3B

    &.active
      border-color: #3C3C3B


.tagger
  display: block
  position: relative
  box-sizing: border-box
  min-height: 45px
  border: 1.5px solid #D2D6DF
  border-radius: 20px
  background: #FFFFFF
  color: #5B6485
  font-size: 14px
  font-style: normal
  font-weight: 500
  letter-spacing: 0.02em
  line-height: 1.14
  padding: 4px 10px 3px

  & > ul
    margin: 0
    padding: 0
    flex-wrap: wrap
    justify-content: flex-start
    & > li
      margin: 0
      padding-left: 0
      padding-right: 5px
    & > li:not(.tagger-new)>a,
    & > li:not(.tagger-new)>span
      display: inline-flex
      align-items: center
      border-color: $accent
      background-color: $accent
      color: #000
      box-sizing: border-box
      padding: 0 5px 0 10px
      margin: 2.5px 0

      .label
        padding: 0
        background: none

  .tagger-new
    margin: 0
    padding: 0 0 0 5px
    input
      height: 35px
      line-height: 1
      overflow: hidden
      white-space: nowrap
      text-overflow: ellipsis
      font-weight: 100
      +placeholder()
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 16px
        letter-spacing: 0.02em
        color: #9FA4B0

.tagger > ul > li:not(.tagger-new) > a,
.tagger > ul > li:not(.tagger-new) > span
  box-sizing: border-box
  display: inline-flex
  align-items: center
  height: 25px
  margin: 2px 0
  padding: 0 4px 0 8px
  background: #308AFC
  border-radius: 20px
  border: none

  font-style: normal
  font-weight: 400
  font-size: 12px
  line-height: 100%
  color: #FFFFFF
  +tr(0.25s)

.tagger li .close
  display: flex
  justify-content: center
  align-items: center
  width: 16px
  height: 16px
  flex: 0 0 16px
  padding: 0
  margin-left: 4px
  font-size: 14px!important
  line-height: 14px!important
  color: #fff!important
  border: 1px solid rgba(#fff, 0.3)
  border-radius: 50%
  padding: 0!important
  +h
    border: 1px solid red
    background: red

.form-example
  list-style-type: none
  padding: 0
  margin: 0
  display: flex
  justify-content: flex-start
  align-items: center
  flex-wrap: wrap
  li
    display: block
    position: relative
    margin-right: 10px
    margin-top: 10px
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 15px
    letter-spacing: 0.02em
    color: #3C3C3B

    &:last-child
      margin-right: 0

.check.fz12
  .check__txt
    font-size: 12px
