/* Check */

.check
  display: inline-flex
  align-items: flex-start
  position: relative
  padding-left: 27px
  line-height: 1.1
  cursor: pointer

  +h
    .check__box
      &::before
        opacity: 0.3


.check__input
  position: absolute
  width: 1px
  height: 1px
  overflow: hidden
  clip: rect(0 0 0 0)
  +hh
    box-shadow: none

.check__box
  display: inline-flex
  justify-content: center
  align-items: center
  position: relative
  margin-left: -24px
  margin-right: 10px
  width: 24px
  height: 24px
  flex: 0 0 24px
  overflow: hidden
  border-radius: 50%
  background-color: white
  background-repeat: no-repeat
  background-position: 50% 50%
  border: 1px solid #E1E3EA
  box-sizing: border-box
  position: relative

  &::before
    content: ''
    display: block
    width: 8px
    height: 8px
    border-radius: 50%
    background: #3C3C3B
    opacity: 0
    margin: auto
    +tr(0.2s)

.check__txt
  padding-top: 5px

/* Checked */

.check__input:checked + .check__box:before
  opacity: 1

/* Focused */

.check__input:focus + .check__box
  border: 1.5px solid #CCD2E3

/* Disabled */

.check__input:disabled + .check__box
  opacity: 0.5

.check__input:checked:disabled + .check__box
  opacity: 0.5