.page-content.--page-404
  display: flex
  justify-content: center
  align-items: center
  padding: 200px 0
  padding: (18.5vh) 0

.page-404-box
  display: flex
  justify-content: center
  align-items: flex-start
  +r(767)
    flex-direction: column
    text-align: center
  & > div
    margin-left: 30px
    h1
      font-style: normal
      font-weight: 400
      font-size: 38px
      line-height: 100%
      color: #3C3C3B
      margin: 20px 0 0 0
      +fz(38, 24)
    p
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 140%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin: 10px 0 20px 0
      +fz(14, 12)
