.page-services
  .page-content
    padding: 90px 0
    +r(1499)
      padding: 40px 0

.service-items
  display: block
  position: relative

.service-item
  &:first-child
    position: relative
    +rmin(768)
      &::after
        content: ''
        display: block
        position: absolute
        right: -312px
        bottom: -162px
        width: 730px
        height: 250px
        background-image: url(../img/logo-decor.svg)
        background-repeat: no-repeat
        background-position: center
        background-size: contain
        z-index: -1 

  & + .service-item
    margin-top: 90px
    +r(1499)
      margin-top: 40px
  &__box
    display: flex
    align-items: center
    +r(1023)
      display: block
  &__media
    display: block
    position: relative
    width: 50%
    padding: 0 30px 30px 0
    +r(1023)
      width: 100%
      padding: 0
      margin-bottom: 20px
    img
      max-width: 100%
      width: 100%
  &__intro
    position: absolute
    right: 0
    bottom: 0
    max-width: 375px
    display: inline-flex
    align-items: center
    gap: 20px
    background: #FFFFFF
    border-radius: 30px
    padding: 45px 40px 45px 45px
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 120%
    letter-spacing: 0.02em
    color: #3C3C3B
    +r(1199)
      padding: 30px
    +r(1023)
      border-radius: 30px 0 0 0
    +r(767)
      position: static
      margin-top: 20px
      border-radius: 20px
      width: 100%
      max-width: 100%
      font-size: 12px
      padding: 20px
    & > div:first-child
      width: 60px
      height: 60px
      flex: 0 0 60px
      +r(1199)
        width: 40px
        height: 40px
        flex: 0 0 40px
  &__desc
    width: 50%
    padding-left: 30px
    +r(1023)
      width: 100%
      padding: 0
    & > *
      max-width: 552px
      +r(1023)
        max-width: 100%
  &__title
    font-style: normal
    font-weight: 700
    font-size: 26px
    line-height: 130%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 20px
    +r(767)
      font-size: 20px
  &__text
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 140%
    color: #3C3C3B
    b
      font-style: normal
      font-weight: 700
      font-size: 14px
      line-height: 120%
      letter-spacing: 0.02em
      color: #3C3C3B
  &__btn
    margin-top: 20px

    .button
      height: 45px

.service-item
  &:nth-child(even)
    .service-item__box
      flex-direction: row-reverse
    .service-item__media
      padding: 0 0 30px 30px
      +r(1023)
        padding: 0
    .service-item__intro
      left: 0
      right: auto
      +r(1023)
        border-radius: 0 30px 0 0
    .service-item__desc
      padding-left: 0
      padding-right: 30px
      +r(1023)
        padding: 0
      & > *
        margin-left: auto