.lk-widjet
  display: block
  position: relative
  padding: 30px
  background: #FFFFFF
  border-radius: 30px
  overflow: hidden
  +r(767)
    padding: 20px 17px
  &__parallax
    display: block
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    z-index: 1
  &__decor
    display: block
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
  &__head
    display: flex
    justify-content: space-between
    align-items: center
    margin-bottom: 20px
  &__title
    display: inline-flex
    align-items: center
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: #B4BACA
    margin: 0
    span
      display: inline-block
      margin-left: 10px
      font-style: normal
      font-weight: 400
      font-size: 16px
      line-height: 100%
      letter-spacing: 0.02em
      color: #FFAE00
    .ico_info
      margin-left: 8px
      font-size: 24px
      color: #C3C8D5
  &__more
    display: flex
    justify-content: center
    align-items: center
    text-align: center
    position: relative
    width: 30px
    height: 30px
    flex: 0 0 30px
    border: 1px solid #E1E3EA
    border-radius: 50%
    font-size: 14px
    color: #3C3C3B!important
    margin-top: -10px
    margin-right: -10px
    text-decoration: none!important
    +tr(0.25s)
    +r(767)
      margin-right: 0
      margin-top: 0
    &:after
      content: ''
      display: block
      position: absolute
      top: calc(50% - 1px)
      left: calc(50% + 1px)
      flex: 0 0 33px
      width: 33px
      height: 33px
      transform: translate(-50%, -50%)
      border: 6px solid #FFAE00
      border-radius: 50%
      font-size: 0
      clip-path: polygon(50% 0, 50% 50%, 100% 50%, 100% 0%)
      opacity: 0
      +tr(0.25s)
    +h
      border: 1px solid #3C3C3B
      color: #3C3C3B
      &:after
        opacity: 1
  &__body
    display: block
    position: relative
    z-index: 2

    a:not(.button)
      color: #308AFC
      +h
        text-decoration: underline


.lk-widjet.--orders
  height: 100%

.lk-widjet.--user-info
  overflow: visible

.widjet-orders-list
  list-style-type: none
  padding: 0
  margin: 0
  &__item
    display: flex
    flex-direction: column
    padding-bottom: 12px
    margin-bottom: 12px
    border-bottom: 1px solid #F8F8F8
    &:last-child
      padding-bottom: 0
      margin-bottom: 0
      border-bottom: 0
  &__item-top
    display: flex
    justify-content: space-between
    align-items: center
    +r(575)
      align-items: flex-start
    & > div:first-child
      display: flex
      justify-content: flex-start
      align-items: center
      +r(575)
        flex-direction: column
        align-items: flex-start
  &__item-numb
    display: block
    .status
      +r(767)
        width: 160px
        font-size: 13px
  &__item-date
    display: inline-block
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-left: 10px
    +r(575)
      margin-left: 0
      margin-top: 8px
      font-size: 12px
  &__item-price
    display: inline-flex
    flex-direction: column
    justify-content: center
    align-items: center
    text-align: center
    width: 160px
    background: #F8F9FA
    border-radius: 20px
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 1.1
    letter-spacing: 0.02em
    color: #3C3C3B
    padding: 5px
    +r(1199)
      width: 120px
    +r(575)
      width: 110px
      font-size: 13px
  &__sub-price
    color: #B4BACA
    font-size: 13px
    +r(575)
      font-size: 11px
  &__item-bottom
    display: block
    margin-top: 8px
  &__item-status
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    +r(575)
      font-size: 11px

.widjet-bottom
  text-align: right
  margin-top: 10px
  a
    text-decoration: none!important
    +h
      .ico_trash
        &::before
          color: #FF6A6A
      span
        text-decoration: underline
    .ico_trash
      text-decoration: none
      vertical-align: middle

      &::before
        color: #D2D6DF
        margin: -3px 10px 0 10px

.recomend-orders
  display: grid
  grid-template-columns: 1fr 1fr
  grid-column-gap: 20px
  grid-row-gap: 14px
  +r(767)
    display: block

.recomend-order-item
  display: block
  position: relative
  padding: 0 20px 0 24px
  +r(767)
    margin-bottom: 10px
    &:last-child
      margin-bottom: 0
  &::before
    content: ''
    display: block
    position: absolute
    top: 0
    left: 0
    width: 4px
    height: 100%
    background: #FFAE00
    +tr(0.25s)
  &::after
    content: ''
    display: block
    position: absolute
    top: 0
    left: 0
    width: 4px
    height: 100%
    background: #FFAE00
    opacity: 0.3
    box-shadow: 1px 0px 4px 1px #FFAE00
    +tr(0.25s)
  +hh
    &::before
      background: #FF7549
    &::after
      background: #FF7549
      box-shadow: 1px 0px 4px 1px rgba(255, 117, 73, 0.3)
  &__link
    display: block
    position: relative
  &__title
    display: block
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 110%
    letter-spacing: 0.02em
    color: #3C3C3B
    +tr(0.25s)
    +h
      color: $accent
  &__numb
    display: block
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.04em
    color: #B4BACA
    margin-top: 8px

.lk-widjet.--bonus
  margin-top: 30px
  +r(1679)
    margin-top: 20px
  .lk-widjet__body
    margin-right: 30px

.widjet-bonus-box
  display: flex
  justify-content: space-between
  align-items: flex-end
  position: relative
  margin-top: -45px
  +r(575)
    flex-direction: column
    align-items: center
    margin-top: 0

.widjet-bonus-total-pay
  display: flex
  justify-content: flex-start
  align-items: center
  margin-right: 40px
  margin-bottom: 10px
  & > div
    display: flex
    flex-direction: column
    margin-left: 15px
    span
      display: block
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 100%
      letter-spacing: 0.02em
      color: #B4BACA
    strong
      display: block
      font-style: normal
      font-weight: 700
      font-size: 24px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin-top: 8px
      +r(1680)
        font-size: 20px
      +r(1390)
        white-space: nowrap

.widjet-bonus-value
  display: block
  position: relative
  width: 330px
  height: 110px
  background-image: linear-gradient(180deg, #FFAE00 0%, #FF7549 100%)
  border-radius: 20px
  padding: 4px
  +r(1679)
    +rmin(1200)
      width: 260px
  +r(767)
    width: 260px
  &__box
    display: flex
    justify-content: center
    align-items: stretch
    background: #fff
    border-radius: 17px
    padding-top: 38px
    height: 100%
    +r(1679)
      flex-wrap: wrap
      justify-content: flex-start
      align-items: flex-start
      padding: 25px 5px 25px 20px
    span
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      +r(1679)
        +rmin(1200)
          display: block
          position: relative
          width: 100%
          margin-bottom: 8px
      +r(767)
        display: block
        position: relative
        width: 100%
        margin-bottom: 8px
    strong
      display: inline-block
      font-style: normal
      font-weight: 700
      font-size: 48px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin: 0 0 0 10px
      max-width: 100%
      white-space: nowrap
      +r(1679)
        +rmin(1200)
          margin-left: 0
          font-size: 40px
      +r(767)
        margin-left: 0
        font-size: 42px
    img
      width: 24px
      height: 24px
      flex: 0 0 24px
  .bonus-amount
    position: relative
    padding-right: 34px
    &__img
      width: 24px
      position: absolute
      right: 0
      top: 0



.lk-widjet.--arrears
  .lk-widjet__head
    margin-bottom: 15px
  .lk-widjet__title
    +r(767)
      flex-direction: column
      align-items: flex-start
    span
      display: inline-block
      cursor: pointer
      margin-left: 30px
      +r(767)
        margin: 10px 0 0 0

.widjet-arrears-box
  display: flex
  justify-content: flex-start
  align-items: center
  +r(1679)
    flex-direction: column
    align-items: stretch
  +r(1199)
    flex-direction: row
    align-items: center
  +r(767)
    flex-direction: column
    align-items: stretch

.widjet-arrears-card
  background: #F8F9FA
  border-radius: 20px
  padding: 20px 50px 30px 30px
  margin-right: 35px
  +r(1679)
    margin-right: 0
  +r(1199)
    margin-right: 40px
  +r(767)
    margin-right: 0
    display: flex
    flex-direction: column
    align-items: center
    padding: 20px
  &__head
    display: flex
    justify-content: flex-start
    align-items: center
    margin-bottom: 7px
  &__numb
    font-style: normal
    font-weight: 700
    font-size: 12px
    line-height: 100%
    color: #B4BACA
  &__date
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #B4BACA
    margin-left: 10px
  &__body
    display: flex
    justify-content: flex-start
    align-items: flex-start
    +r(767)
      justify-content: space-between
      justify-content: center
      align-items: center
      flex-wrap: wrap
  &__img
    display: block
    position: relative
    margin-right: 20px
    margin-left: -10px
    +r(767)
      margin: 0
    & > img
      opacity: 0
  &__parallax
    display: block
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    z-index: 1
  &__decor
    display: block
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
  &__label
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
  &__value
    display: block
    margin: 8px 0 4px
    font-style: normal
    font-weight: 700
    font-size: 42px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
  &__days
    display: flex
    justify-content: flex-start
    align-items: center
    span
      display: block
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
    b
      display: block
      font-style: normal
      font-weight: 700
      font-size: 14px
      line-height: 100%
      color: #FF7549
      margin-left: 10px

.widjet-arrears-list
  list-style-type: none
  padding: 0
  margin: 0
  +r(1679)
    display: grid
    grid-template-columns: repeat(2, 1fr)
    grid-gap: 10px
    margin-top: 15px
  +r(1199)
    margin-top: 0
    display: block
  +r(767)
    display: grid
    grid-template-columns: repeat(2, 1fr)
    grid-gap: 10px
    margin-top: 20px
  +r(413)
    display: block
  &__item
    display: flex
    justify-content: flex-start
    align-items: center
    margin-bottom: 7px
    &:last-child
      margin-bottom: 0
    +r(413)
      width: 180px
      margin: 0 auto 7px
  &__icon
    width: 32px
    height: 32px
    flex: 0 0 32px
    margin-right: 10px
  &__desc
    display: flex
    flex-direction: column
    span
      display: block
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #B4BACA
    b
      display: block
      font-style: normal
      font-weight: 400
      font-size: 16px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin-top: 2px

.lk-widjet.--favorite,
.lk-widjet.--messages
  padding: 32px 30px
  background-repeat: no-repeat
  background-position: left bottom
  +r(1679)
    height: calc(50% - 10px)
    display: flex
    align-items: center
  +r(767)
    padding: 30px 17px
    background-image: none
  .lk-widjet__decor
    +r(767)
      opacity: 0
  .lk-widjet__body
    width: 100%
    position: relative
  .widjet-favorite-box,
  .widjet-messages-box
    display: flex
    justify-content: center
    align-items: center
    position: relative
    & > div
      display: flex
      justify-content: flex-start
      align-items: flex-start
      width: 250px
      img
        width: 45px
        height: 45px
        flex: 0 0 45px
      strong
        display: block
        margin: 0 15px
        font-style: normal
        font-weight: 400
        font-size: 48px
        line-height: 100%
        text-align: center
        letter-spacing: 0.02em
        color: #3C3C3B
        +r(767)
          font-size: 42px
          margin: 0 5px
      span
        font-style: normal
        font-weight: 400
        font-size: 12px
        line-height: 110%
        letter-spacing: 0.02em
        color: #3C3C3B
    .lk-widjet__more
      position: absolute
      right: 0
      top: 50%
      margin-top: -15px
      +r(767)
        right: 10px

.lk-widjet.--favorite
  .lk-widjet__decor
    padding: 0 0 0 20px
    display: flex!important
    justify-content: flex-start
    align-items: flex-start
    img
      bottom: -20%
      position: relative
      +r(1679)
        bottom: -50%
      +r(1199)
        bottom: -20%

.lk-widjet.--messages
  margin-top: 30px
  +r(1679)
    margin-top: 20px
  .lk-widjet__decor
    padding: 0 0 0 20px
    display: flex!important
    justify-content: flex-start
    align-items: flex-start
    img
      bottom: -20%
      position: relative
      +r(1679)
        bottom: -50%
      +r(1199)
        bottom: -20%

.lk-widjet.--user-info
  .lk-widjet__head
    margin-bottom: 15px

  .drop-button
    +r(767)
      height: 35px
      font-size: 14px
  .drop-clear
    +r(767)
      width: 35px
      height: 35px
      flex: 0 0 35px

.widjet-user-info
  list-style-type: none
  padding: 0
  margin: 0 0 10px 0
  &__item
    display: flex
    justify-content: space-between
    align-items: center
    padding: 5.5px 0
    border-bottom: 1px solid #F8F8F8
    &:first-child
      padding-top: 0
    &:last-child
      border-bottom: 0
  &__label
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 18px
    letter-spacing: 0.02em
    color: #9FA4B0
    width: 40%
    padding-right: 20px
    box-sizing: border-box
    white-space: nowrap
    +line(1)
    +r(767)
      font-size: 12px
      padding-right: 10px
    a
      +h
        text-decoration: underline
  &__value
    display: flex
    justify-content: flex-end
    align-items: center
    width: 60%
    text-align: right
  &__input
    flex-grow: 1
    input[type="text"]
      height: 24px
      text-align: right
      border: none
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 18px
      letter-spacing: 0.02em
      color: #3C3C3B
      pointer-events: none
      width: 100%
      +r(767)
        font-size: 12px
      &.is-active
        pointer-events: all
        background-color: #f8f8f8
    a
      +h
        text-decoration: underline
      +r(767)
        font-size: 12px
  &__btn
    display: flex
    justify-content: center
    align-items: center
    width: 35px
    height: 35px
    flex: 0 0 35px
    +r(767)
      width: 25px
      height: 25px
      flex: 0 0 25px
    button, a
      border: none
      width: 24px
      height: 24px
      flex: 0 0 24px
      +h
        &::before
          color: $accent
      &::before
        font-size: 24px
        color: rgba(210, 214, 223, 1)
        +r(767)
          font-size: 20px
      &.is-active
        &::before
          color: $accent

.lk-widjet.--manager-info
  .widjet-user-info
    &__item
      padding: 8px 0
      &:last-child
        border-bottom: 1px solid #F8F8F8

.lk-widjet.--price
  height: 100%
  background-repeat: no-repeat
  background-position: left 20px bottom 20px
  +r(1199)
    min-height: 290px
  +r(767)
    min-height: auto
    background-position: left -20px bottom 20px
  .lk-widjet__decor
    display: flex!important
    justify-content: flex-start
    align-items: flex-end
    padding: 0 0 30px 10px
  .lk-widjet__body
    padding: 0 5px 40px 140px
    +r(1679)
      padding: 0 5px 40px 50px
    +r(1199)
      padding: 0 5px 40px 140px
    +r(413)
      padding: 0 5px 40px 100px
    .drop
      margin-bottom: 10px
      background: #fff
    .button
      padding: 0 10px
      width: 100%
      height: 45px

.widjet-banner
  display: block
  position: relative
  height: 290px
  min-height: 100%
  border-radius: 30px
  overflow: hidden
  +r(1199)
    height: 180px
  +r(767)
    height: 260px
  &__bg
    display: flex
    align-items: center
    position: relative
    height: 100%
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    padding: 50px
    +r(767)
      padding: 20px
    &::before
      content: ''
      display: block
      position: absolute
      left: 0
      top: 0
      width: 100%
      height: 100%
      background: rgba(60, 60, 59, 0.5)
  &__title
    display: block
    position: relative
    z-index: 2
    font-style: normal
    font-weight: 700
    font-size: 24px
    line-height: 120%
    color: #FFFFFF