@font-face
  font-family: 'footable'
  src: url('../fonts/footable.eot')
  src: url('../fonts/footable.eot') format("embedded-opentype"), url('../fonts/footable.woff2') format("woff2"), url('../fonts/footable.woff') format("woff"), url('../fonts/footable.ttf') format("truetype"), url('../fonts/footable.svg') format("svg")
  font-weight: normal
  font-style: normal

.fooicon
 display: inline-block
 font-size: inherit
 font-family: footable!important
 font-style: normal
 font-weight: 400
 line-height: 1
 text-rendering: auto
 -webkit-font-smoothing: antialiased
 -moz-osx-font-smoothing: grayscale
 transform: translate(0)

.fooicon:after,
.fooicon:before
 box-sizing: border-box

.fooicon-loader:before
  content: '\e800'
.fooicon-plus:before
  content: '\e801'
.fooicon-minus:before
  content: '\e802'
.fooicon-search:before
  content: '\e803'
.fooicon-remove:before
  content: '\e804'
.fooicon-pencil:before
  content: '\e805'
.fooicon-flash:before
  content: '\e806'
.fooicon-eye-close:before
  content: '\e807'
.fooicon-cog:before
  content: '\e808'
.fooicon-stats:before
  content: '\e809'
.fooicon-sort:before
  content: '\f0dc'
.fooicon-sort-asc:before
  content: '\f160'
.fooicon-sort-desc:before
  content: '\f161'
.fooicon-trash:before
  content: '\f1f8'

table.ftable
  border-collapse: separate
  & > thead
    & > tr.footable-header
      position: relative
      border-bottom: 2px solid transparent
      & > th
        padding: 0 10px 12px
        font-style: normal
        font-weight: 400
        font-size: 12px
        line-height: 15px
        letter-spacing: 0.02em
        color: #878C98
        border-bottom: 1px solid #F0F1F4
        &:first-child
          padding-left: 30px
          +r(1199)
            padding-left: 20px
        &:last-child
          padding-right: 30px
          +r(1199)
            padding-right: 20px
  & > tbody
    & > tr
      position: relative!important
      border-top: 2px solid transparent
      border-bottom: 2px solid transparent

      // td:first-child
      //   > div, > span
      //     &::before
      //       content: ''
      //       display: block
      //       position: absolute
      //       top: 0
      //       left: 0
      //       width: 100%
      //       height: 100%
      //       border: 1px solid #E1E3EA
      //       border-radius: 20px
      //       pointer-events: none
      //       opacity: 0
      //       +tr(0.35s)
      // +h
      //   td:first-child
      //     > div, > span
      //       &::before
      //         opacity: 1

      +rmin(1024)
        +h
          & > td
            border-top: 1px solid #E1E3EA
            border-bottom: 1px solid #E1E3EA

            &:first-child
              border-left: 1px solid #E1E3EA
            &.footable-last-visible
              border-right: 1px solid #E1E3EA

      & > td
        height: 72px
        padding: 8px 10px
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 18px
        letter-spacing: 0.02em
        color: #3C3C3B
        border-top: 1px solid transparent
        border-bottom: 1px solid transparent
        +tr(.25s)

        +r(767)
          font-size: 12px
          line-height: 15px
          height: 50px

        &:first-child
          padding-left: 30px
          border-left: 1px solid transparent
          border-top-left-radius: 20px
          border-bottom-left-radius: 20px
          +r(1199)
            padding-left: 20px
        &.footable-last-visible
          border-right: 1px solid transparent
          border-top-right-radius: 20px
          border-bottom-right-radius: 20px
        &:last-child
          padding-right: 30px
          +r(1199)
            padding-right: 20px

        &.w-1
          width: 1px

        a
          font-style: normal
          font-weight: 400
          font-size: 14px
          line-height: 18px
          letter-spacing: 0.02em
          color: #3C3C3B
          +tr(0.25s)
          +h
            color: #308AFC
            text-decoration: none
          +r(767)
            font-size: 12px
            line-height: 15px

        p,
        a,
        span:not(.check__box),
        span:not(.circle),
        strong
          display: block

          & + *
            margin-top: 2px

          &.small
            font-style: normal
            font-weight: 400
            font-size: 12px
            line-height: 15px
            letter-spacing: 0.02em
            color: #878C98
            +r(767)
              font-size: 11px
            &.black
              color: #3C3C3B

          &.big
            font-style: normal
            font-weight: 400
            font-size: 16px
            line-height: 20px
            letter-spacing: 0.02em
            color: #3C3C3B
            +r(767)
              font-size: 14px

        .check
          .check__box
            display: inline-flex

        a.button,
        a.button-outline,
        a.button-accent
            display: inline-flex

        .status
          display: inline-flex
          width: auto
          font-style: normal
          font-weight: 400
          font-size: 12px
          line-height: 100%
          letter-spacing: 0.02em
          color: #3C3C3B
          box-sizing: border-box
          .circle
            width: 15px
            height: 15px
            flex: 0 0 15px
            margin-right: 5px

        .table-button-group
          display: inline-flex
          justify-content: flex-start
          align-items: center
          & > *
            margin-right: 10px
            &:last-child
              margin-right: 0

        .return
          display: inline-flex
          justify-content: flex-start
          align-items: flex-start
          font-style: normal
          font-weight: 400
          font-size: 14px
          line-height: 18px
          letter-spacing: 0.02em
          color: #3C3C3B
          +ico()
            font-size: 16px
            margin-right: 5px
            width: 16px
            height: 16px
            flex: 0 0 16px
            color: #57CA70
          span.small
            display: block
            font-style: normal
            font-weight: 400
            font-size: 12px
            line-height: 15px
            letter-spacing: 0.02em
            color: rgba(135, 140, 152, 1)

          & > div.--success
            color: #57CA70
          & > div.--cancel
            color: #FF6A6A
          & > div.--waiting
            color: #FFAE00

        .counter
          display: inline-flex

        .check
          & + .table-product-card
            margin-left: 15px
            +r(767)
              margin-left: 0

        .form-control
          height: 35px
          border: 1.5px solid #E1E3EA
          border-radius: 30px

        .drop-clear
          width: 35px
          height: 35px
          flex: 0 0 35px
          background-size: 15px

        .table-product-card
          display: flex
          justify-content: flex-start
          align-items: center
          margin-top: -4px
          margin-left: -15px
          margin-bottom: -4px
          &.not
            margin-left: 52px
            +r(767)
              margin-left: 37px
            .table-product-card__img
              filter: grayscale(1)
              opacity: 0.8
          &__img
            display: flex
            align-items: center
            width: 90px
            height: 90px
            flex: 0 0 90px
            margin-right: 32px
            +r(1199)
              margin-right: 10px
              width: 64px
              height: 64px
              flex: 0 0 64px
            img
              max-width: 100%
              max-height: 100%
          &__desc
            display: flex
            flex-direction: column
          &__art
            display: flex
            justify-content: flex-start
            align-items: center
            font-style: normal
            font-weight: 400
            font-size: 14px
            line-height: 100%
            letter-spacing: 0.04em
            color: #9FA4B0
            margin-bottom: 5px
            +ico()
              margin-right: 5px
              color: rgba(48, 138, 252, 1)
              font-size: 12px
              cursor: pointer
            +r(767)
              font-size: 12px
          &__title
            font-style: normal
            font-weight: 400
            font-size: 16px
            line-height: 100%
            letter-spacing: 0.02em
            color: #3C3C3B
            margin-top: 5px
            +r(767)
              font-size: 14px
          &__others-product
            display: block
            position: relative
            margin-top: 5px
            font-style: normal
            font-weight: 400
            font-size: 12px
            line-height: 100%
            letter-spacing: 0.04em
            color: #FFAE00


          &.--table-product-card-2
            .table-product-card__art
              font-size: 12px
            .table-product-card__title
              font-size: 12px

        .message
          p
            margin: 0
            font-style: normal
            font-weight: 400
            font-size: 14px
            line-height: 18px
            letter-spacing: 0.02em
            color: #9FA4B0
            max-width: 700px

          &.--new
            span
              display: block
              font-style: normal
              font-weight: 400
              font-size: 12px
              line-height: 15px
              letter-spacing: 0.02em
              color: #FF6A6A
              margin-bottom: 2px
            p
              color: #000

        .user-box
          padding: 0
          display: flex
          align-items: center
          img
            margin-right: 7px
            +r(767)
              width: 40px
              height: 40px
              flex: 0 0 40px

        .hide-mobile
          +r(767)
            display: none
    
    & > tr[data-expanded="true"]
      & > td
        border-top: 1px solid #E1E3EA
        border-bottom: 1px solid #E1E3EA

        &:first-child
          border-left: 1px solid #E1E3EA
          border-bottom-left-radius: 0
        &.footable-last-visible
          border-right: 1px solid #E1E3EA
          border-bottom-right-radius: 0

.lk-page__order-info
  display: flex
  justify-content: flex-start
  align-items: center
  flex-wrap: wrap
  +r(767)
    flex-direction: column
    justify-content: flex-start
    align-items: stretch
    margin-bottom: 20px

.order-info-item
  padding-right: 30px
  margin-right: 30px
  border-right: 1px solid #E1E3EA
  font-size: 14px
  line-height: 18px
  +r(1199)
    padding-right: 20px
    margin-right: 20px
  +r(767)
    padding-right: 0
    margin-right: 0
    margin-bottom: 15px
    border-right: 0
    display: flex
    align-items: center
    font-size: 12px
    line-height: 15px
  &:last-child
    margin-right: 0
    padding-right: 0
    border-right: 0
    +r(767)
      margin-bottom: 0
      height: 15px
  &__label
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 18px
    letter-spacing: 0.02em
    color: #9FA4B0
    +r(767)
      min-width: 50%
      flex: 0 0 50%
      padding-right: 20px
      font-size: 12px
      line-height: 15px
  &__value
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 18px
    letter-spacing: 0.02em
    color: #3C3C3B
    +r(767)
      min-width: 50%
      flex: 0 0 50%
      font-size: 12px
      line-height: 15px

.ftable.table-with-table-inner
  & > tbody
    & > td.footable-last-visible
      padding-right: 30px

.ftable.table-with-table-inner.breakpoint
  & > tbody
    & > tr
      & > td.footable-last-visible
        padding-right: 70px!important
        +r(767)
          padding-right: 55px!important

.footable-details.table-with-table-inner
  & > tr
    & > th
      display: none
    & > td
      padding: 0
      +r(767)
        padding: 10px

  div[data-label]
    display: flex
    justify-content: flex-start
    align-items: center

    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 15px
    letter-spacing: 0.02em
    color: #3C3C3B

    &::before
      content: attr(data-label)
      width: 100%
      max-width: 23%
      padding-right: 10px
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 15px
      letter-spacing: 0.02em
      color: #6C707A

  tbody
    & > tr
      & > th
        display: none
      & > td
        padding-left: 0
        padding-right: 0

.ftable
  & > tbody
    & > tr[data-expanded="true"]
      border-bottom: 0
      & > td:first-child
        > div, > span
          &::before
            opacity: 1
            border-bottom: 0
            border-radius: 20px 20px 0 0
            +tr(0s)
    & > tr
      & > td:first-child
        > div, > span
          &::before
            +tr(0s)
    & > tr.footable-detail-row
      border-top: 0
      border-bottom: 0
      & > td
        border: 0!important
      & > td:first-child
        padding: 0 20px
        &::before
          border-top: 0
          border-radius: 0 0 20px 20px
          opacity: 1

.ftable.table-with-table-inner
  & > tbody
    & > tr.footable-detail-row
      & > td:first-child
        padding: 0

table.footable-details.table-with-table-inner
  & > tbody
    & > tr
      & > td:last-child
        padding: 0 30px
        position: relative
        &::after
          content: ''
          display: block
          position: absolute
          left: 20px
          top: 0
          width: calc(100% - 40px)
          height: 1px
          background: #F0F1F4
        +r(1199)
          padding: 0 20px

table.ftable.breakpoint
  tbody
    td.footable-last-visible
      padding-right: 55px!important
      position: relative

table.footable-details
  &::before
    content: ''
    display: block
    position: absolute
    top: 0    
    left: -20px
    width: calc(100% + 40px)
    height: calc(100% + 1px)
    border: 1px solid #E1E3EA
    border-top: 0
    border-radius: 20px
    border-top-left-radius: 0
    border-top-right-radius: 0
    pointer-events: none
    opacity: 1
    +tr(0.35s)
  & > tbody
    width: 100%
    & > tr
      border-top: 0
      border-bottom: 1px solid #F0F1F4
      &:last-child
        border-bottom: 0
      & > td
        border: 0!important
      & > th:first-child
        font-style: normal
        font-weight: 400
        font-size: 12px
        line-height: 14px
        letter-spacing: 0.02em
        max-width: 20%
        +r(414)
          width: 100px
          max-width: 100px
          min-width: 100px
          padding-right: 10px
      & > td:last-child
        padding-right: 0
        width: 100%
        max-width: 80%
        +r(414)
          max-width: calc( 100% - 130px )
      &:last-child
        & > th:first-child
          &::before
            display: none

.table-inner
  display: block
  position: relative
  padding: 0 20px
  margin-left: -29px
  margin-right: -29px
  background: #F8F9FA
  border-radius: 0 0 20px 20px
  margin: 0 1px
  +r(1199)
    margin-left: -19px
    margin-right: -19px
    padding: 0 10px
  .table-inner__row
    display: grid
    grid-template-columns: repeat(6, 1fr)
    text-align: left
    justify-content: space-between
    align-items: center
    +r(1199)
      display: grid
      grid-template-columns: repeat(3, 1fr)
    +r(767)
      display: flex
      flex-direction: column
      align-items: stretch
    &:nth-child(even)
      background-color: #fff

    & + .table-inner__row
      border-top: 1px solid #fff
    .table-inner__cell
      display: block
      position: relative
      padding: 15px 10px
      &:first-child
        padding-left: 10px
      &:last-child
        padding-right: 10px

      +r(767)
        &::after
          content: ''
          display: block
          position: absolute
          left: 20px
          top: 0
          width: calc(100% - 40px)
          height: 1px
          background: #F0F1F4

      .button-group
        display: flex
        align-items: center
        .action-group
          margin-left: 10px
        .drop
          width: 200px

.footable-toggle
  position: absolute
  top: 50%
  transform: translateY(-50%)
  right: 30px
  display: flex!important
  justify-content: center
  align-items: center
  width: 30px
  height: 30px
  flex: 0 0 30px
  border: none
  border-radius: 50%
  background-color: #fff
  box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
  font-size: 20px
  color: #C3C8D5
  opacity: 1!important
  cursor: pointer
  margin: 0!important
  z-index: 1

  &::before
    background-image: url('data:image/svg+xml;utf8,<svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 1.5L5 5.5L9 1.5" stroke="%23B4BACA" stroke-width="1.8" stroke-linecap="round"/></svg>')
    content: ''
    width: 10px
    height: 7px
    margin-top: 3px
    
  &.fooicon-minus
    &::before
      background-image: url('data:image/svg+xml;utf8,<svg width="10" height="7" viewBox="0 0 10 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 1.5L5 5.5L9 1.5" stroke="%23878C98" stroke-width="1.8" stroke-linecap="round"/></svg>')

  +hh
    background-color: #fff
    box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)!important
    color: #FFAE00!important

  &.fooicon-minus
    transform: translateY(-50%) rotate(180deg)
    color: #FFAE00!important

  +r(1199)
    right: 17px



.dinamic-list
  display: flex
  justify-content: flex-end
  align-items: flex-start
  list-style-type: none
  padding: 0
  margin: 0

  +r(575)
    flex-direction: column

  &.--row
    display: flex!important
    flex-direction: row
    justify-content: flex-start
    +r(575)
      flex-direction: column

.dinamic-item
  margin-right: 20px
  +r(575)
    margin-right: 0
    margin-bottom: 10px
  &:last-child
    margin-right: 0
    +r(575)
      margin-bottom: 0
  &__value
    font-style: normal
    font-weight: normal
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E

  &__progress
    width: 130px
    height: 6px
    background-color: #E1E3EA
    margin: 3px 0 4px
    display: block
    position: relative
    span
      display: block
      position: absolute
      top: 0
      left: 0
      height: 6px
      background: linear-gradient(90deg, #FFAE00 0%, #FF7549 100%), linear-gradient(274.47deg, #72CB9D -4.92%, #4890FA 102.55%)

  &__dates
    font-style: normal
    font-weight: 500
    font-size: 11px
    line-height: 100%
    letter-spacing: 0.02em
    color: #B4BACA
