body.page-jobs
  section.section-banner-promo
    margin: 60px 0
    +r(1199)
      margin: 20px -15px 80px
    +r(767)
      margin: 40px -15px 80px
  .banner
    &__box
      +r(1199)
        min-height: 400px
  .banner-promo
    &__bg
      +r(1199)
        min-height: 350px
      +r(767)
        background-size: cover
    .button-outline
      font-weight: 700

blockquote.jobs
  display: flex
  justify-content: center
  align-items: flex-start
  margin: 30px 0 60px
  +r(1199)
    margin: 10px 0 40px
  +r(767)
    flex-direction: column
    align-items: stretch

  h3
    text-align: right
    +r(767)
      margin: 0
      text-align: center

  span
    width: 12px
    align-self: stretch
    background: $accent
    border-radius: 40px 0px
    margin: 0 25px
    +r(767)
      margin: 15px auto
      width: 110px
      height: 12px
      border-radius: 40px 0px

  p
    max-width: 410px
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 140%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin: 0
    +r(767)
      max-width: 100%

.jobs-info
  display: block
  position: relative
  &__box
    display: flex
    justify-content: space-between
    align-items: center
    background: #FFFFFF
    border-radius: 30px
    +r(767)
      align-items: flex-start
  &__media
    display: block
    width: 150px
    flex: 0 0 150px
    position: relative
    +r(767)
      width: 100px
      flex: 0 0 100px
    img
      max-width: 100%
  &__desc
    flex-grow: 1
    padding: 35px
    +r(767)
      padding: 30px 15px 30px 10px
  &__title
    font-style: normal
    font-weight: 700
    font-size: 18px
    line-height: 120%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin: 0 0 10px 0
  &__text
    ul, p
      font-style: normal
      font-weight: 400
      font-size: 14px
      line-height: 140%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin: 0
      +r(767)
        font-size: 13px

  &.--small
    .jobs-info__box
      align-items: center
      overflow: hidden
    .jobs-info__media
      width: 140px
      height: 160px
      flex: 0 0 140px
      overflow: hidden
      +r(767)
        width: 120px
        flex: 0 0 120px
      img
        display: block
        position: absolute
        left: 50%
        top: 50%
        transform: translate(-50%, -50%)
    .jobs-info__title
      +r(767)
        font-size: 16px
    .jobs-info__desc
      padding: 20px 30px 20px 15px
      +r(767)
        padding: 15px 15px 15px 15px


.jobs-card
  display: block
  position: relative
  &::before
    content: ''
    display: block
    position: absolute
    top: 35px
    left: -6px
    width: 12px
    height: 50px
    background: $accent
    border-radius: 40px 0px
    +r(767)
      left: 0
  &__box
    display: block
    padding: 35px
    background: #FFFFFF
    border-radius: 30px
    +tr(0.25s)
    +h
      box-shadow: 0px 14px 26px -14px rgba(60, 60, 59, 0.2)
    +r(767)
      padding: 30px 25px
  &__title
    display: block
    font-style: normal
    font-weight: 700
    font-size: 18px
    line-height: 120%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin: 0 0 13px 0
    -webkit-box-orient: vertical
    display: -webkit-box
    -webkit-line-clamp: 1
    overflow: hidden
  &__info
    list-style-type: none
    padding: 0
    margin: 0
    display: flex
    justify-content: flex-start
    align-items: center
    margin-bottom: 10px
    li
      display: flex
      justify-content: flex-start
      align-items: center
      & + li
        margin-left: 20px
      +ico()
        margin-right: 6px
        color: #CCD2E3
        font-size: 14px
      span
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 100%
        letter-spacing: 0.02em
        color: #3C3C3B
  &__desc
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 140%
    letter-spacing: 0.02em
    margin: 0 20px 0 0
    color: rgba(135, 140, 152, 1)
    min-height: 59px
    -webkit-box-orient: vertical
    display: -webkit-box
    -webkit-line-clamp: 3
    overflow: hidden

  &__btn
    display: block
    position: absolute
    right: 20px
    bottom: 20px
    .button-more
      width: 30px
      height: 30px
      flex: 0 0 30px
      border: 1px solid #E1E3EA
      position: relative
      +h
        border: 1px solid #000
        &:after
          opacity: 1

      &:after
        content: ''
        display: block
        position: absolute
        top: calc(50% - 1px)
        left: calc(50% + 1px)
        flex: 0 0 38px
        width: 33px
        height: 33px
        transform: translate(-50%, -50%)
        border: 6px solid #FFAE00
        border-radius: 50%
        font-size: 0
        clip-path: polygon(50% 0, 50% 50%, 100% 50%, 100% 0%)
        opacity: 0
        +tr(0.25s)

.section-jobs-slider
  .section-header
    padding-bottom: 0