.lk-page.--lk-index
  .section-products-view
    margin: 30px 0 60px
    .section-header
      +r(767)
        padding-bottom: 0
        margin-bottom: 0

.lk-page
  &__inner
    display: flex
    justify-content: space-between
    align-items: flex-start
    margin-bottom: 60px
    +r(1365)
      display: block
  &__content
    padding-left: 30px
    width: calc(100% - 262px)
    +r(1365)
      width: 100%
      padding-left: 0
    .g-5, .gx-5
      +r(1679)
        --bs-gutter-x: 20px
    .g-5, .gy-5
      +r(1679)
        --bs-gutter-y: 20px
  &__breadcrumb
    margin-bottom: 20px
    .breadcrumb
      margin: 0
    .breadcrumb-item
      font-style: normal
      font-weight: 500
      font-size: 12px
      line-height: 100%
      color: #B4BACA
      & > a
        font-style: normal
        font-weight: 500
        font-size: 12px
        line-height: 100%
        color: #9FA4B0
    .breadcrumb-item
      & + .breadcrumb-item
        padding-left: 10px
        &:before
          content: '|'
          color: #F0F1F4
          padding-right: 10px
  &__head
    display: flex
    justify-content: flex-start
    align-items: center
    margin-bottom: 30px
    +r(1199)
      justify-content: space-between

    +r(767)
      flex-direction: column
      align-items: stretch
      margin-bottom: 20px

    .lk-page__back
      +r(1199)
        width: 100%
        max-width: 180px
        margin-right: 0
      +r(767)
        margin-bottom: 20px

    .lk-page__title
      +r(1199)
        flex-grow: 1
        text-align: center
      +r(767)
        text-align: center
        font-size: 20px

    .lk-page__empty
      +r(1199)
        width: 100%
        max-width: 180px
      +r(767)
        display: none

    &.--justify
      justify-content: space-between
      align-items: center
      +r(767)
        flex-direction: column
        align-items: stretch
        margin-bottom: 20px

      & > div
        display: flex
        justify-content: flex-start
        align-items: center

        &:first-child
          +r(1199)
            flex-grow: 1
            margin-right: 10px
            .lk-page__title
              flex-grow: 1

        +r(767)
          flex-direction: column
          justify-content: flex-start
          align-items: stretch

      .lk-page__action-btns
        margin-top: 0

      .lk-page__back,
      .lk-page__title
        +r(767)
          margin-bottom: 20px

      .lk-page__title
        +r(767)
          text-align: center
          font-size: 20px

      .button-info-group
        justify-content: flex-end
        position: relative
        & > *
          +r(767)
            margin-right: 10px
            &:last-child
              margin-right: 0

        +r(767)
          .button-info
            position: static

          .button-info__dropdown
            top: calc(100% + 10px)
            right: 0

    .lk-page__back
      padding-right: 30px
      margin-right: 30px
      border-right: 1px solid #E1E3EA
      +r(767)
        padding-right: 0
        margin-right: 0
        border-right: 0

      .button-back
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 100%
        letter-spacing: 0.04em
        color: #3C3C3B
        +ico()
          border: 1px solid #3C3C3B
        +h
          color: $accent

    .lk-page__title
      margin-bottom: 0

    .switcher
      margin-left: 30px

  &__title
    display: block
    font-style: normal
    font-weight: 700
    font-size: 22px
    line-height: 130%
    color: #3C3C3B
    margin-bottom: 35px
    +r(1199)
      margin-bottom: 20px
      text-align: center
    +r(767)
      font-size: 20px
      text-align: center

  &__subtitle
    font-style: normal
    font-weight: 400
    font-size: 18px
    line-height: 130%
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 20px
    +r(767)
      font-size: 16px
      text-align: center

  &__search-mobile
    +r(767)
      margin: 20px 0
    .drop-input
      height: 45px
      line-height: 45px
      background-size: 15px
    .drop-clear
      width: 45px
      height: 45px
      flex: 0 0 45px
  &__action
    display: flex
    justify-content: space-between
    align-items: center
    margin-bottom: 20px
    +r(767)
      flex-direction: column
      align-items: stretch
      margin-bottom: 27px
  &__sort-items
    display: flex
    justify-content: flex-start
    align-items: center
    flex-wrap: wrap
    border-bottom: 1px solid #B4BACA
    +r(1199)
      margin-top: 0
  &__action-btns
    margin-left: 20px
    align-self: flex-start
    display: flex
    flex-direction: row
    +r(1199)
      margin-left: 0
      margin-top: 10px
    +r(767)
      margin-top: 0
    +r(575)
      align-self: center
    .button,
    .button-outline
      white-space: nowrap
      height: 45px
    & > * + *
      margin-left: 20px
    .drop
      min-width: 262px
  &__filters
    display: grid
    grid-template-columns: repeat(5, 1fr)
    grid-gap: 30px
    margin-top: 30px
    margin-bottom: 30px
    & > *
      width: 100%
    .drop
      width: 100%
    .form-control-group
      display: flex
      justify-content: space-between
      align-items: center
      span
        width: 25px
        flex: 0 0 25px
        display: flex
        justify-content: center
        align-items: center
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 18px
        color: #C3C8D5
  &__table-after
    display: flex
    justify-content: space-between
    align-items: flex-start
    margin-top: 30px
    +r(767)
      flex-direction: column-reverse
      align-items: stretch
      margin-top: 24px
    & > *:first-child
      flex-grow: 1
    & > *:first-child
      display: flex
      justify-content: flex-start
      align-items: center
      flex-wrap: wrap
      max-width: 50%
      +r(767)
        max-width: 100%
        margin-top: 20px
        flex-direction: column
        align-items: stretch
    & > *:last-child
      margin-left: 20px
      +r(767)
        margin-left: 0
        margin-bottom: 0
    .--search
      width: 100%
      max-width: 260px
      +r(1199)
        max-width: 100%
      .drop-input
        height: 45px
        line-height: 45px
        background-size: 15px
      .drop-clear
        width: 45px
        height: 45px
        flex: 0 0 45px
  &__table-bonus
    display: flex
    flex-direction: column
    align-self: flex-start
    margin: 0 30px
    span
      display: block
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 15px
      letter-spacing: 0.02em
      color: rgba(135, 140, 152, 1)
      margin-bottom: 4px
    strong
      font-style: normal
      font-weight: 400
      font-size: 22px
      line-height: 28px
      letter-spacing: 0.02em
      color: #3C3C3B

  &__table-after-btns
    display: flex
    align-items: center
    margin-left: 30px
    +r(1199)
      margin-left: 20px
    +r(767)
      margin-left: auto
      margin-top: 20px
    & > *
      margin-right: 20px
      min-width: 150px
      height: 45px
      padding: 0 20px
      &:last-child
        margin-right: 0
  &__table
    background: #FFFFFF
    border-radius: 30px
    padding: 30px
    margin: 25px 0 30px
    +r(1199)
      padding: 20px 0
      margin: 20px 0
  &__table-total-action
    max-width: 1140px
    margin-top: 30px
  &__table-total
    display: flex
    justify-content: flex-start
    align-items: flex-start
    margin: 30px 0
  &__slider-products
    display: block
    position: relative
    padding: 50px 60px 60px
    background: #FFFFFF
    border-radius: 30px
    margin: 30px 0
    +r(1679)
      padding: 30px
    +r(767)
      padding: 30px 20px

    .section-header
      display: flex
      justify-content: flex-start
      align-items: center
      margin-bottom: 30px
      +r(767)
        padding-bottom: 15px
        margin-bottom: 15px

      .button-outline
        padding: 0 15px
        height: 35px
        +r(767)
          padding: 0 10px
          white-space: nowrap

    .section-title
      text-transform: none
      margin-right: 30px
      margin-bottom: 0

      font-style: normal
      font-weight: 700
      font-size: 24px
      line-height: 100%
      letter-spacing: 0.01em
      color: #3C3C3B
      +r(767)
        font-size: 20px

    .section-content .--section-slider-nav
      top: -70px

    .product-card
      border-right: 2px solid #F8F8F8

.filter-sort-item
  &__link
    display: flex
    align-items: center
    position: relative
    box-sizing: border-box
    padding: 0 40px 25px 0
    +r(1599)
      padding: 0 20px 25px 0
    +r(1199)
      padding: 0 15px 15px 0
    +tr(0.25s)
    &:after
      content: ''
      display: block
      position: absolute
      left: 0
      bottom: 0
      width: 0
      height: 4px
      background: #30393E
      +tr(0.5s)
  &__title
    display: inline-block
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: rgba(135, 140, 152, 1)
    +tr(0.25s)
    +r(1199)
      font-size: 14px
  &__numb
    display: inline-block
    margin-left: 10px
    padding-left: 10px
    border-left: 1px solid #E1E3EA
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: rgba(135, 140, 152, 1)
    +tr(0.25s)
    +r(1199)
      font-size: 14px
  &.is-active
    .filter-sort-item__link
      &:after
        width: 100%
    .filter-sort-item__title,
    .filter-sort-item__numb
      color: rgba(48, 57, 62, 1)
  +hh
    .filter-sort-item__title,
    .filter-sort-item__numb
      color: rgba(48, 57, 62, 1)

dl.table-total
  padding-left: 30px
  border-left: 4px solid $accent
  margin-bottom: 0
  dt
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 15px
    letter-spacing: 0.02em
    white-space: nowrap
  dd
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 20px
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-top: 4px
    margin-bottom: 6px
    &:last-child
      margin-bottom: 0
    &.big
      font-style: normal
      font-weight: 700
      font-size: 22px
      line-height: 28px
      letter-spacing: 0.02em
      color: #3C3C3B
      margin-bottom: 0
    &.sub
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 15px
      letter-spacing: 0.02em
      color: #878C98
      margin-top: 0


.table-total__value-sm
  color: #878C98
  font-style: normal
  font-weight: 400
  font-size: 12px
  line-height: 1.25
  letter-spacing: 0.02em

.table-total.--row
  display: flex
  justify-content: flex-start
  align-items: flex-start
  list-style-type: none
  padding: 0 0 0 30px
  margin: 0
  border-left: 4px solid $accent
  +r(767)
    flex-wrap: wrap
  .table-total__item
    display: flex
    flex-direction: column
    margin-right: 30px
    +r(1199)
      margin-right: 20px
    &:last-child
      margin-right: 0
  .table-total__label
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 15px
    letter-spacing: 0.02em
    color: rgba(60, 60, 59, 1)
    white-space: nowrap
  .table-total__value
    font-style: normal
    font-weight: 700
    font-size: 22px
    line-height: 28px
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-top: 4px
  .table-total__btn
    margin-top: 4px
    .button,
    .button-outline
      height: 30px
      padding: 0 10px
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      text-align: center
      letter-spacing: 0.03em

.table-total-list
  list-style-type: none
  padding: 0
  margin: 0 0 0 180px
  margin: 0 0 0 vw(180px)
  display: flex
  justify-content: flex-start
  align-items: flex-start
  +r(1199)
    margin-left: 30px
  +r(767)
    margin-left: 0
    margin-top: 20px
    justify-content: space-between
  &__item
    display: flex
    flex-direction: column
    margin-right: 30px
    &:last-child
      margin-right: 0
    +r(767)
      margin-right: 20px
  &__label
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 15px
    letter-spacing: 0.02em
    color: rgba(135, 140, 152, 1)
    white-space: nowrap
  &__content
    display: flex
    flex-direction: column
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 18px
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-top: 4px
    .nowrap
      white-space: nowrap

.submenu
  +r(1359)
    display: inline-flex
    justify-content: flex-start
    align-items: center
    position: relative
  +r(767)
    display: flex
    justify-content: flex-end
    margin-bottom: 16px
  &__title
    font-style: normal
    font-weight: normal
    font-size: 14px
    line-height: 16px
    color: #000000
    margin-right: 8px
    display: none
    +r(1359)
      display: block
  &__btn
    display: none
    min-width: 210px
    height: 40px
    padding: 0 10px
    box-sizing: border-box
    border: none
    border: 2px solid #FFAE00
    border-radius: 6px
    color: #000
    +r(1359)
      display: block
    .lk-submenu__link
      justify-content: space-between
    .lk-submenu__title
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #30393E
    .lk-submenu__number
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #30393E
      min-width: 40px
      text-align: center
  &__current
    font-style: normal
    font-weight: 600
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #323537
  &__box
    +r(1359)
      display: block
      position: absolute
      top: calc(100% + 100px)
      right: 0
      min-width: 210px
      background-color: #fff
      box-shadow: 1px 1px 20px rgba(#000, 0.15)
      padding: 10px 12px
      border-radius: 6px
      z-index: 5
      opacity: 0
      pointer-events: none
      overflow: hidden
      +tr(0s)
    &.is-active
      top: calc(100% + 5px)
      visibility: visible
      opacity: 1
      pointer-events: all
      +tr(0.3s)
      .lk-submenu
        display: block
        &__item
          margin: 0 0 2px 0

.lk-submenu
  display: flex
  justify-content: flex-start
  align-items: center
  list-style-type: none
  padding: 0
  margin: 0
  border-bottom: 1px solid #B4BACA
  +r(1359)
    border-bottom: 0
  &__item
    +r(1359)
      border-bottom: 1px solid #F0F1F4
    &:last-child
      +r(1359)
        border-bottom: 0
    & + .lk-submenu__item
      margin-left: 5px
      +r(1359)
        margin-left: 0
    &.active
      .lk-submenu__title,
      .lk-submenu__number
        color: #30393E
      .lk-submenu__link
        &:after
          width: 100%
  &__link
    display: flex
    align-items: center
    position: relative
    box-sizing: border-box
    padding: 0 40px 25px 0
    +r(1599)
      padding: 0 20px 25px 0
    +r(1359)
      padding: 10px 0
      justify-content: space-between
    +tr(0.25s)
    &:after
      content: ''
      display: block
      position: absolute
      left: 0
      bottom: 0
      width: 0
      height: 4px
      background: #30393E
      +tr(0.5s)
  &__title
    display: inline-block
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: rgba(135, 140, 152, 1)
    +tr(0.25s)
    +r(1499)
      font-size: 14px
    +r(1359)
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #30393E
  &__number
    display: inline-block
    margin-left: 10px
    padding-left: 10px
    border-left: 1px solid #E1E3EA
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: rgba(135, 140, 152, 1)
    +tr(0.25s)
    +r(1499)
      font-size: 14px
    +r(1359)
      min-width: 40px
      text-align: center
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 100%
      letter-spacing: 0.02em
      color: #30393E
  &.is-active
    .filter-sort-item__link
      &:after
        width: 100%
    .filter-sort-item__title,
    .filter-sort-item__number
      color: rgba(48, 57, 62, 1)
  +hh
    .filter-sort-item__title,
    .filter-sort-item__number
      color: rgba(48, 57, 62, 1)

.lk-page.--order-return,
.lk-page.--order-save,
.lk-page.--order-delivery,
.lk-page.--order-success
  .lk-page__empty
    +r(1199)
      display: none

  .lk-page__table-after
    +r(767)
      dl.table-total
        align-self: flex-start

.lk-page.--order-save
  +r(374)
    .lk-page__table-after-btns
      display: block
      & > *
        margin-right: 0
        width: 100%
        & + *
          margin-top: 10px

.lk-page.--order-import-result
  .lk-page__subtitle
    +r(1199)
      text-align: center
  .lk-page__table-after
    +r(1199)
      justify-content: flex-end
    & > *:first-child
      +r(1199)
        justify-content: flex-end
    & > *:last-child
      +r(1199)
        display: none

.lk-page.--cart
  .lk-page__table-total
    +r(1199)
      display: block
      .table-total-list
        margin-top: 20px
        margin-left: 0
  .lk-page__action-btns > * + *
    +r(374)
      margin-left: 10px

.lk-page.--order-return
  .lk-page__table-after
    +r(767)
      flex-direction: column
      & > div:nth-child(1)
        order: 2
      & > div:nth-child(2)
        order: 1
      & > div:nth-child(3)
        order: 3
