
.lk-page
  &__messages
    display: flex
    justify-content: space-between
    align-items: stretch
    +r(1199)
      display: block
  &__messages-list
    flex-grow: 1
  &__messages-banner
    padding-left: 30px
    padding-bottom: 20px
    +r(1199)
      padding: 0
      margin-top: 30px
    +r(767)
      display: none

.lk-page.--messages
  .lk-page__action-btns
    +r(767)
      align-self: flex-end

.messages-banner
  display: block
  position: relative
  width: 262px
  height: 100%
  max-height: 545px
  +r(1199)
    width: 100%
  &__bg
    display: flex
    justify-content: center
    align-items: flex-end
    position: relative
    height: 100%
    padding: 60px 30px
    box-sizing: border-box
    +r(1199)
      min-height: 260px
    &::before
      content: ''
      display: block
      position: absolute
      left: 0
      top: 0
      width: 100%
      height: 100%
      background: rgba(60, 60, 59, 0.7)
  &__content
    position: relative
    z-index: 2
    text-align: center
  &__title
    font-style: normal
    font-weight: 700
    font-size: 24px
    line-height: 120%
    text-align: center
    color: #FFFFFF
  &__btn
    margin-top: 20px
    .button-outline
      color: #fff

.messages-box
  display: block
  position: relative
  padding: 30px
  background: #fff
  border-radius: 30px
  +r(1199)
    padding: 20px
  +r(767)
    padding: 20px 17px

.messages-list-box
  margin-right: -30px
  height: 400px
  overflow-y: auto

  scrollbar-color: #C3C8D5 #F0F1F4
  scrollbar-width: thin!important
  -moz-appearance: none !important

  &::-webkit-scrollbar
    width: 4px

  &::-webkit-scrollbar-track
    background-color: #F0F1F4

  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5

.messages-list
  list-style-type: none
  padding: 0
  margin: 0 30px 0 0
  display: flex
  flex-direction: column
  &__item
    display: flex
    align-items: flex-start
    margin-bottom: 15px
    +r(767)
      flex-direction: column
    &:last-child
      margin-bottom: 0
    &.--user
      .messages-list__item-text
        background: #FFEFCC
    &.--manager
      .messages-list__item-text
        background: #F0F1F4
  &__item-avatar
    display: flex
    justify-content: center
    align-items: center
    width: 60px
    height: 60px
    flex: 0 0 60px
    margin-right: 15px
    +r(767)
      margin: 0 0 15px 0
    img, svg
      width: 60px
      height: 60px
      flex: 0 0 60px
      object-fit: cover
      object-position: center
  &__item-content
    display: flex
    flex-direction: column
  &__item-text
    display: block
    position: relative
    margin-bottom: 8px
    border-radius: 0px 8px 8px 8px
    padding: 15px
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 20px
    letter-spacing: 0.02em
    color: #3C3C3B
    background: #F0F1F4
    +r(767)
      font-style: normal
      font-weight: 400
      font-size: 12px
      line-height: 20px
      letter-spacing: 0.02em
      color: #3C3C3B
  &__item-info
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #C3C8D5

.messages-form-box
  display: flex
  justify-content: space-between
  align-items: flex-start
  margin-top: 30px
  +r(1199)
    display: block
    text-align: right
    margin-top: 20px
  textarea
    flex-grow: 1
    margin-right: 30px
    +r(1199)
      margin: 0 0 20px 0