.lk-page.--bonus
  .lk-page__title
    margin-bottom: 30px
    +r(1359)
      margin-bottom: 20px
  .lk-page__subtitle
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 18px
    letter-spacing: 0.02em
    color: #3C3C3B
    margin-bottom: 30px
    +r(1359)
      text-align: center
      margin-bottom: 20px
    b
      display: inline-block
      margin: 0 10px
      font-style: normal
      font-weight: 700
      font-size: 18px
      line-height: 23px
      letter-spacing: 0.02em
      color: #3C3C3B


.discount-widjet
  display: block
  position: relative
  background: #FFFFFF
  box-shadow: 0px 1px 10px -1px rgba(180, 186, 202, 0.5)
  border-radius: 30px
  padding: 30px
  min-height: 100%
  +r(767)
    padding: 20px 17px
  &__head
    display: flex
    justify-content: space-between
    align-items: center
    padding-bottom: 15px
    margin-bottom: 20px
    border-bottom: 1px solid #F0F1F4
    min-height: 45px
    +r(767)
      padding-bottom: 10px
      margin-bottom: 15px
      min-height: auto
    & > div:first-child
      display: flex
      justify-content: flex-start
      align-items: center
    h3
      font-style: normal
      font-weight: 400
      font-size: 18px
      line-height: 100%
      letter-spacing: 0.02em
      color: #3C3C3B
      margin: 0 10px 0 0
      +fz(20, 16)

      span
        display: inline-block
        margin-left: 17px
        color: #FFAE00

    .button-icon
      &[class*='ico_']
        font-size: 14px
      +h
        color: #fff

    +ico()
      font-size: 20px
      color: #3C3C3B

  &__body
    display: block
    position: relative
  &__list
    display: flex
    justify-content: flex-start
    list-style-type: none
    padding: 0
    margin: 0
    +r(767)
      flex-direction: column
    li
      display: flex
      align-items: flex-start
      flex-direction: column
      font-style: normal
      font-weight: 500
      font-size: 16px
      line-height: 100%
      letter-spacing: 0.02em
      color: #323537
      margin-right: 40px
      +r(1439)
        margin-right: 20px
      +r(767)
        margin-right: 0
        margin-bottom: 16px
        &:last-child
          margin-bottom: 0
      strong
        display: inline-block
        margin: 0 7px

      div:first-child
        span
          font-style: normal
          font-weight: 400
          font-size: 14px
          line-height: 100%
          letter-spacing: 0.02em
          color: #B4BACA
          padding-left: 29px
          white-space: nowrap
          +r(1681)
            font-size: 14px
          +r(767)
            font-size: 14px
      div:last-child
        display: flex
        align-items: center
        margin-top: 5px
        span
          padding-left: 29px
          font-style: normal
          font-weight: 600
          font-size: 18px
          line-height: 1.28
          letter-spacing: 0.02em
          color: #929DA5
          +r(767)
            font-size: 16px
        strong
          font-style: normal
          font-weight: 600
          font-size: 18px
          line-height: 100%
          letter-spacing: 0.02em
          color: #323537
          white-space: nowrap
          +r(1681)
            margin-top: 3px
            font-size: 15px

    &.discount-widjet__list--row
      display: flex
      justify-content: flex-start
      +r(767)
        flex-direction: row
      & > li
        display: flex
        align-items: flex-start
        flex-direction: column
        margin-right: 40px
        +r(1439)
          margin-right: 20px
        +r(767)
          margin-right: 16px
        &:last-child
          margin-right: 0
        & + li
          margin-top: 0

.button-info.--bonus
  +h
    .button-info__button
      color: $accent
  .button-info__button
    width: 20px
    height: 20px
    flex: 0 0 20px
    background: none
  .button-info__dropdown
    left: -10px
    right: auto
    padding: 20px 20px 20px 50px
    box-shadow: 0px 14px 26px -14px rgba(180, 186, 202, 0.5)
    border-radius: 0px 30px 30px 30px