.about-desc
  display: block
  position: relative
  margin: 90px 0

  +r(1199)
    margin: 45px 0
  +r(767)
    margin: 30px 0

  .title
    +r(767)
      font-size: 20px

.about-video
  display: block
  position: relative
  margin: 90px 0

  .title
    +r(767)
      font-size: 20px

  +r(1199)
    margin: 45px 0
  +r(767)
    margin: 30px 0

  &::after
    content: ''
    display: block
    position: absolute
    left: -100px
    bottom: -150px
    width: 730px
    height: 250px
    background-image: url(../img/logo-decor.svg)
    background-repeat: no-repeat
    background-position: center
    background-size: contain
    z-index: -1

.about-statistics
  display: block
  position: relative
  margin: 90px 0
  +r(1199)
    margin: 45px 0
  +r(767)
    margin: 30px 0

  .title
    +r(767)
      text-align: center
      font-size: 20px

.statistics
  display: flex
  justify-content: center
  align-items: center
  list-style-type: none
  padding: 0
  margin: 0

  +r(767)
    display: grid
    grid-template-columns: repeat(2, 1fr)
    grid-gap: 20px

  &__item
    display: flex
    flex-direction: column
    & + .statistics__item
      margin-left: 90px
      +r(1199)
        margin-left: 40px
      +r(767)
        margin-left: 0
  &__ico
    width: 45px
    height: 45px
    flex: 0 0 45px
    margin-bottom: 15px
  &__value
    display: block
    font-style: normal
    font-weight: 700
    font-size: 56px
    line-height: 100%
    letter-spacing: 0.04em
    color: #3C3C3B
    +r(1199)
      font-size: 40px
    +r(767)
      font-size: 36px
  &__text
    display: block
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 140%
    letter-spacing: 0.02em
    color: #3C3C3B
    +r(767)
      font-size: 12px

.about-partners
  display: block
  position: relative
  margin: 90px 0

  +r(1199)
    margin: 45px 0
  +r(767)
    margin: 30px 0

  .partners-slider
    display: block
    position: relative
  .swiper
    margin: 0
    padding: 0
  .swiper-nav.--section-slider-nav
    position: absolute
    top: -55px
    right: 0
    &.--mobile
      +r(767)
        position: static
        top: auto
        left: auto
        right: auto
        margin: 20px auto 0

  .title
    +r(767)
      text-align: center
      font-size: 20px

.about-plus
  display: block
  position: relative
  margin: 90px 0

  +r(1199)
    margin: 45px 0
  +r(767)
    margin: 30px 0

  &::after
    content: ''
    display: block
    position: absolute
    right: -50px
    top: 50%
    transform: translateY(-50%)
    width: 730px
    height: 250px
    background-image: url(../img/logo-decor.svg)
    background-repeat: no-repeat
    background-position: center
    background-size: contain
    z-index: -1

  .about-plus-item__img
    display: block
    position: relative
    padding: 0 30px 30px 0

    +r(1199)
      padding: 0 0 140px 0

    img
      width: 100%

    .jobs-info
      max-width: 410px
      position: absolute
      right: 0
      bottom: 0

  .about-plus-item__desc
    max-width: 525px
    .title
      +r(767)
        font-size: 20px

  .about-plus-item
    display: block
    position: relative

    & + .about-plus-item
      margin-top: 60px
      +r(1199)
        margin-top: 30px


  .about-plus-item:nth-child(even)
    & > .row
      flex-direction: row-reverse

    .about-plus-item__img
      display: block
      position: relative
      padding: 0 0 30px 30px

      +r(1199)
        padding: 0 0 140px 0

      img
        width: 100%

      .jobs-info
        max-width: 410px
        position: absolute
        left: 0
        right: auto
        bottom: 0

    .about-plus-item__desc
      margin-left: auto