.manager-messages
  table.ftable > tbody > tr > td .message
    &.--closed
      p 
        color: #878C98 
    p 
      color: #3C3C3B
  .date--closed
    color: #878C98 
  table.ftable > tbody > tr > td .message span.new-messge-bage
    display: inline-block
    font-weight: 700
    font-size: 11px
    line-height: 100%
    letter-spacing: 0.04em
    color: #FFFFFF
    background: #57CA70
    border-radius: 100vw
    padding: 4px 7px
    margin-bottom: 6px
  table.ftable > tbody > tr > td
    &:first-child
      a
        +h
          color: #308AFC
  table.ftable > tbody > tr > td a
    +h
      color: #3C3C3B
  +rmin(1024)
    table.ftable > tbody > tr
          +h
            > td:first-child a
              color: #308AFC


.--messages-inner
  .lk-page__head
    .button-outline
      margin-left: auto
      +r(767)
        margin-top: 15px




