.lk-page.--compare

  .compare-sidebar__head
    height: 299px

  .compare-content
    background: #fff
    padding: 30px 30px 40px
    border-radius: 30px
    +r(767)
      padding: 20px 17px

  .compare-items

    .swiper
      margin: 0
      padding: 0
      border-left: 1px solid #E1E3EA
      +r(767)
        border-left: 0

    .swiper-scrollbar
      bottom: -20px
      height: 4px
      background: #F0F1F4

    .swiper-scrollbar-drag
      background: #C3C8D5

  .compare-item
    border-left: 0

    &__box
      +rmin(768)
        height: 299px
    &__label
      position: absolute
      top: 0
      left: 20px
    &__action
      top: 0
      right: 20px
      left: auto
      width: 30px
      height: 30px
      flex: 0 0 30px

      .btn-delete
        width: 30px
        height: 30px
        flex: 0 0 30px
        display: flex
        justify-content: center
        align-items: center
        background: #FFFFFF
        box-shadow: 0px 2px 8px -1px rgba(204, 210, 227, 0.6)
        border-radius: 30px
        color: #B4BACA
        font-size: 14px
        cursor: pointer
        +tr(0.25s)
        +h
          color: #FF6A6A

    &__info
      padding: 15px 20px 10px
    &__title
      min-height: auto
      margin-bottom: 10px
      a
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 1.1
        letter-spacing: 0.02em
        text-align: left
        color: #3C3C3B
        +line(1)
        display: block
    &__price
      display: flex
      justify-content: flex-start
      align-items: flex-end
      font-style: normal
      font-weight: 700
      font-size: 17px
      line-height: 100%
      letter-spacing: 0.04em
      color: #3C3C3B
      text-align: left
      min-height: auto
      .old-price
        display: inline-block
        font-style: normal
        font-weight: 400
        font-size: 16px
        line-height: 100%
        text-decoration-line: line-through
        color: #9FA4B0
        margin-right: 5px
      +r(1100)
        font-size: 16px
        .old-price
          font-size: 14px
    &__sub-price
      text-align: left
      justify-content: start
      display: flex
      +r(1100)
        font-size: 14px
    +h
      .compare-item__price
        display: block

    &__item
      span.value
        color: #3C3C3B

    &__footer
      padding: 10px
      text-align: center

      .button-small
        font-weight: 700