.lk-page.--lk-leader-order-creat
  .lk-page__action
    .lk-page__filters
      margin: 0
    .lk-page__action-btns
      .button-delete
        width: 45px
        height: 45px
        flex: 0 0 45px
        border: 1px solid #F0F1F4
        box-shadow: none
        background: none
        color: #3C3C3B
        +tr(0.25s)
        +h
          color: red

.lk-widjet.--map
  display: block
  position: relative
  padding: 0
  overflow: hidden
  .lk-widjet__head
    display: flex
    align-items: center
    padding: 20px 30px
    margin-bottom: 0
  .lk-widjet__body
    padding: 0
    position: relative
    display: block
    & > *
      height: 400px
      +r(767)
        height: 305px

.widjet-list-orders
  display: block
  position: relative
  list-style-type: none
  padding: 0
  margin: 0
  &__item
    display: block
    position: relative
    margin-bottom: 8px
    padding-bottom: 8px
    border-bottom: 1px solid #F8F8F8
    &:last-child
      margin-bottom: 0
      padding-bottom: 0
      border-bottom: none
  &__link
    display: flex
    justify-content: space-between
    align-items: center
    position: relative
    padding-left: 24px
    &::before
      content: ''
      display: block
      position: absolute
      top: 0
      left: 0
      width: 4px
      height: 100%
      background: #FFAE00
      +tr(0.25s)
    &::after
      content: ''
      display: block
      position: absolute
      top: 0
      left: 0
      width: 4px
      height: 100%
      background: #FFAE00
      opacity: 0.3
      box-shadow: 1px 0px 4px 1px #FFAE00
      +tr(0.25s)
    +hh
      &::before
        background: #FF7549
      &::after
        background: #FF7549
        box-shadow: 1px 0px 4px 1px rgba(255, 117, 73, 0.3)
      .widjet-list-orders__title
        color: $accent
  &__left
    display: flex
    flex-direction: column
  &__title
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    +tr(0.25s)
    +r(767)
      font-size: 12px
  &__number
    margin-top: 5px
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.04em
    color: #B4BACA
    +r(767)
      font-size: 12px
  &__view
    margin-left: 20px
    font-style: normal
    font-weight: 700
    font-size: 14px
    line-height: 100%
    text-align: right
    letter-spacing: 0.02em
    color: #3C3C3B
    white-space: nowrap
    +r(767)
      font-size: 12px

.widjet-list-links
  list-style-type: none
  padding: 0
  margin: 0
  &__item
    display: block
    position: relative
    margin-bottom: 7px
    padding-bottom: 7px
    border-bottom: 1px solid #F8F8F8
    &:last-child
      margin-bottom: 0
      padding-bottom: 0
      border-bottom: none
  &__link
    display: flex
    justify-content: flex-start
    align-items: center
    position: relative
    +h
      .widjet-list-links__text
        color: $accent
  &__icon
    display: flex
    justify-content: center
    align-items: center
    width: 35px
    height: 35px
    flex: 0 0 35px
    background: #F8F9FA
    border-radius: 50%
    font-size: 10px
    color: #308AFC
    margin-right: 10px
    &::before
      width: auto
  &__text
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    +line(1)
    +tr(0.25s)


.lk-page.--lk-leader-orders
  .lk-page__filters
    +r(1679)
      grid-template-columns: repeat(4, 1fr)
    +r(1199)
      margin-top: 20px
      grid-template-columns: repeat(3, 1fr)
    +r(767)
      grid-template-columns: repeat(1, 1fr)
      grid-gap: 10px
  .lk-page__action-btns
    +r(1199)
      margin: 0
    +r(767)
      align-self: flex-end

.lk-page.--leader-order
  .lk-page__action-btns
    +r(767)
      align-self: flex-end
  .lk-page__table-after
    > div:first-child
      max-width: 75%
      +r(767)
        max-width: 100%
  .lk-page__table-after-btns
    +r(1199)
      flex-direction: column
      align-items: flex-start
      & > *
        margin-right: 0
        margin-bottom: 10px
    +r(767)
      align-items: flex-end

.lk-page.--lk-leader-order-creat
  .lk-page__filters
    +r(1679)
      grid-template-columns: repeat(4, 1fr)
    +r(1199)
      margin-top: 20px
      grid-template-columns: repeat(3, 1fr)
    +r(767)
      grid-template-columns: repeat(1, 1fr)
      grid-gap: 15px

  .lk-page__action-btns
    +r(767)
      margin-top: 20px
      align-self: flex-end

  .lk-page__table-after
    > div:first-child
      max-width: 75%
      +r(767)
        max-width: 100%

  .lk-page__table-bonus
    +r(767)
      margin: 20px 0 0 30px

  .lk-page__table-after-btns
    +r(1199)
      margin-left: 0
      margin-top: 20px
    +r(767)
      flex-direction: column
      justify-content: flex-end
      align-items: flex-end
      & > *
        margin-right: 0
        margin-bottom: 16px