.page-footer
  background: #3C3C3B
  padding: 60px 0 45px

  font-style: normal
  font-weight: 400
  font-size: 16px
  line-height: 100%
  letter-spacing: 0.02em
  color: #FFFFFF

  +r(1359)
    padding: 30px 0 40px

  +r(767)
    padding: 17px 0 20px

  a
    display: block
    position: relative
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: #FFFFFF
    +tr(0.25s)
    +hh
      color: $accent

    +r(1359)
      font-size: 13px

  &__top
    display: flex
    justify-content: space-between
    align-items: flex-start

    +r(767)
      flex-direction: column

  &__top-left
    display: flex
    justify-content: flex-start
    align-items: flex-start

    +r(767)
      width: 100%
      justify-content: center

    .logo
      +r(767)
        margin-top: 15px

  &__qrcode
    margin-right: 20px
    +r(1199)
      margin-right: 10px
      width: 60px
      height: 60px
      flex: 0 0 60px
    +r(767)
      width: 85px
      height: 85px
      flex: 0 0 85px
      margin-right: 20px
    img
      max-width: 100%

  &__submenu
    list-style-type: none
    padding: 0
    margin: 20px 0 0 0

    +r(767)
      margin: 50px 0 0 -85px
      text-align: center

    li
      & + li
        margin-top: 10px

  &__top-center
    padding-top: 10px
    padding-right: 70px
    padding-right: vw(70px)
    +r(1199)
      padding: 0 30px 0 20px
    +r(767)
      padding: 30px 0
      width: 100%

  &__btns
    display: flex
    justify-content: space-between
    align-items: center
    flex-wrap: wrap

    +r(1199)
      flex-direction: column
    +r(767)
      justify-content: center

    .button
      display: flex
      border: 1px solid rgba(255, 255, 255, 0.5)
      box-sizing: border-box
      border-radius: 20px
      min-width: 220px
      font-size: 16px

      +r(1199)
        padding: 0 10px

      +h
        border: 1px solid $accent
        background: none
        &:before
          color: $accent

      & + .button
        margin-left: 20px

        +r(1199)
          margin-left: 0
          margin-top: 10px

  &__social
    margin-top: 30px
    +r(1199)
      margin-top: 10px
    .social
      justify-content: center
      &__item
        width: 50px
        height: 50px
        flex: 0 0 50px
        & + .social__item
          margin-left: 20px
      &__link
        display: flex
        justify-content: center
        align-items: center
        border: none
        width: 50px
        height: 50px
        flex: 0 0 50px
        font-size: 25px
        +h
          color: #3C3C3B

  &__top-right
    +r(767)
      padding: 0 30px
      width: 100%

  &__contacts
    list-style-type: none
    padding: 0
    margin: 0
    +rmin(768)
      max-width: 320px
    +r(767)
      text-align: center
    li
      & + li
        margin-top: 20px
      a
        margin-top: 5px
        +r(1199)
          font-size: 14px

  &__bottom
    display: flex
    justify-content: space-between
    align-items: center
    margin-top: 25px
    padding-top: 20px
    border-top: 1px solid rgba(255, 255, 255, 0.2)
    +r(1199)
      padding-top: 10px
    +r(767)
      margin-top: 30px
      padding-top: 16px

  &__bottom-left
    font-style: normal
    font-weight: 400
    font-size: 15px
    line-height: 100%
    letter-spacing: 0.02em
    color: rgba(255, 255, 255, 0.5)

    +r(1359)
      font-size: 12px

  &__bottom-center
    margin: 0 10px
    span
      display: inline-flex
      font-style: normal
      font-weight: 400
      font-size: 15px
      line-height: 100%
      letter-spacing: 0.02em
      color: rgba(255, 255, 255, 0.5)
      a
        display: inline-block
        margin-left: 5px
        font-style: normal
        font-weight: 400
        font-size: 15px
        line-height: 100%
        letter-spacing: 0.02em
        color: rgba(255, 255, 255, 0.5)
        +tr(0.25s)
        +h
          color: $accent

.payments
  display: flex
  justify-content: flex-end
  align-items: center
  list-style-type: none
  padding: 0
  margin: 0
  opacity: 0.4
  li
    & + li
      margin-left: 15px


.seo-links
  display: block
  position: relative
  margin: 0 0 60px
  +r(767)
    margin: 0 0 40px
  &__box
    background: #FFFFFF
    border-radius: 30px
    padding: 30px
    +r(767)
      padding: 30px 16px

    .nav-pills
      display: flex
      justify-content: flex-start
      align-items: center
      border-bottom: 1px solid #E1E3EA

    .nav-link
      padding: 0 40px 12px 1px
      background: none
      border: none
      border-radius: 0
      position: relative

      font-style: normal
      font-weight: 400
      font-size: 16px
      line-height: 100%
      letter-spacing: 0.02em
      color: #878C98
      +r(767)
        padding: 0 24px 12px 1px
      +r(374)
        padding: 0 16px 12px 1px
        font-size: 14px

      &::before
        content: ''
        display: block
        position: absolute
        left: 0
        bottom: 0
        width: 100%
        height: 1px
        background: #B4BACA

      &::after
        content: ''
        display: block
        position: absolute
        left: 0
        bottom: 0
        width: 0
        height: 4px
        background: #30393E

    .nav-link.active,
    .show>.nav-link
      background: none
      color: #30393E
      &::after
        width: 100%


    .tab-content
      margin-top: 20px

  &__list
    list-style-type: none
    padding: 0
    margin: 0
    display: grid
    grid-template-columns: repeat(4, 1fr)
    grid-gap: 8px
    +r(991)
      grid-template-columns: repeat(3, 1fr)
    +r(767)
      grid-template-columns: repeat(2, 1fr)
    +r(575)
      grid-template-columns: repeat(1, 1fr)
    li
      a
        display: inline-block
        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 100%
        letter-spacing: 0.02em
        color: #9FA4B0
        +tr(0.25s)
        +h
          color: #3C3C3B