.section-hero
  margin: 0 0 55px
  +r(1199)
    margin: 0 -10.5px 30px

.hero
  &__inner
    display: flex
    justify-content: flex-start
    align-items: stretch
    position: relative
    // background-color: #F0F0F0
    min-height: 500px
    overflow: hidden
    &::after
      width: 100vw
      height: 100vh
      position: fixed
      left: 0
      top: 0
      z-index: 99
      background: #3C3C3B
      mix-blend-mode: multiply
      opacity: 0
      visibility: hidden
      pointer-events: none
      display: block
      content: ''
      +tr(.3s)
      +r(1199)
        display: none

    &.is-fixed
      &::after
        opacity: 0.5
        pointer-events: all
        visibility: visible
  
.hero-menu-wrap
  position: absolute
  left: 50%
  transform: translateX(-50%)
  top: 0
  width: 100%
  max-width: 1727px
  width: 100%
  padding-left: 15px
  padding-right: 15px
  min-height: 500px
  z-index: 5
  pointer-events: none
  +r(1199)
    display: none

  &.is-fixed
    position: fixed
    top: 109px
    z-index: 100
    max-width: 1727px
    width: 100%
    height: calc( 100vh - 150px )
    max-height: 774px
    background: #D9E1E4
    +r(1727)
      width: calc( 100% - 22px )


    .hero-menu
      height: 100%
      &__container
        height: 100%

.hero-menu
  height: 500px
  position: absolute
  top: 0
  left: 0
  z-index: 5
  pointer-events: all
  +r(1199)
    display: none

  &__container
    border-right: 2px solid rgba(#fff, 0.3)
    width: 410px
    height: 500px
    +r(1679)
      width: 316px

  &__list
    display: block
    position: relative
    padding: 0
    margin: 0
    height: calc( 100% - 84px )
    overflow-x: hidden
    overflow-y: auto
    width: 414px
    scrollbar-color: #FFFFFF transparent
    scrollbar-width: thin!important
    -moz-appearance: none !important
    +r(1679)
      width: 320px

    &:first-child
      height: 84px
      overflow: hidden
      +r(1679)
        width: 314px
      .hero-menu__item
        &:first-child
          padding-top: 18px

        &:last-child
          padding-bottom: 0
          border-bottom: 1px solid rgba(#fff, 0.3)

        &:first-child

          &.is-active
            background: $accent
            .hero-menu__title,
            .hero-menu__icon
              color: #3C3C3B

            .hero-menu__icon
              transform: rotate(180deg)

    &::-webkit-scrollbar
      width: 6px

    &::-webkit-scrollbar-track
      background-color: transparent

    &::-webkit-scrollbar-thumb
      background: #FFFFFF

  &__item
    display: flex
    align-items: center
    width: 410px
    padding-left: 30px
    padding-right: 15px
    border-bottom: 1px solid rgba(#fff, 0.3)
    box-sizing: border-box
    pointer-events: all

    +r(1679)
      width: 315px

    &:last-child
      border-bottom: none
      padding-bottom: 18px

    &.is-active
      background: rgba(#fff, 0.3)
      .hero-menu__title,
      .hero-menu__icon
        color: #3C3C3B

      .hero-menu__icon
        transform: rotate(180deg)

  &__link
    display: flex
    justify-content: space-between
    align-items: center
    width: 100%
    min-height: 65px

  &__title
    display: inline-block
    font-style: normal
    font-weight: 700
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    +tr(0.25s)
    +line(2)
    +r(1679)
      font-size: 14px

  &__icon
    width: 20px
    height: 20px
    flex: 0 0 20px
    font-size: 22px
    display: flex
    justify-content: center
    align-items: center
    text-align: center
    color: #3C3C3B
    +tr(0.25s)

  &__submenu
    background: #D9E1E4
    position: absolute
    top: 0
    left: 410px
    z-index: 2
    width: calc(100% - 408px)
    height: 100%
    min-height: 500px
    opacity: 0
    pointer-events: none
    +tr(0.4s)
    +r(1679)
      left: 320px
      width: calc( 100% - 318px )
    &.is-active
      opacity: 1
      pointer-events: all


  &__submenu-list
    padding: 0
    overflow-y: auto
    max-height: calc(100% - 61px)
    scrollbar-color: $accent #E2EBEC
    scrollbar-width: thin!important
    -moz-appearance: none !important

    &::-webkit-scrollbar
      width: 4px

    &::-webkit-scrollbar-track
      background-color: #E2EBEC

    &::-webkit-scrollbar-thumb
      background-color: $accent


  &__submenu-header
    display: flex
    align-items: center
    padding: 0 10px 0 20px
    margin-left: 0
    font-style: normal
    font-weight: bold
    font-size: 18px
    line-height: 1.1
    color: #FFFFFF
    background-color: $accent
    border-bottom: none
    min-height: 61px
    border-bottom: 1px solid rgba(#fff, 0.3)
    span
      +line(2)
    +r(1679)
      font-size: 16px

  &__submenu-item
    display: flex
    align-items: center
    border-bottom: 1px solid rgba(#fff, 0.3)
    min-height: 61px
    +tr(0.3s)
    +h
      background-color: $accent
      .hero-menu__submenu-link
        color: #3C3C3B

  &__submenu-link
    width: 100%
    display: flex
    align-items: center
    font-style: normal
    font-weight: 700
    font-size: 16px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    padding: 0 20px
    min-height: 61px
    +line(1)
    +tr(0.3s)
    +r(1679)
      font-size: 16px

  &__submenu-columns
    display: block
    position: relative
    margin: 0 0 0 40px
    padding: 20px 40px 0 0
    box-sizing: border-box
    height: calc(100% - 92px)
    overflow-x: hidden
    overflow-y: auto
    scrollbar-color: #fff transparent
    scrollbar-width: thin!important
    -moz-appearance: none !important

    &::-webkit-scrollbar
      width: 6px

    &::-webkit-scrollbar-track
      background-color: transparent

    &::-webkit-scrollbar-thumb
      background-color: #fff

    img
      max-width: 114px
      max-height: 80px
      display: block

    & > ul
      list-style-type: none
      padding: 0
      margin: 0
      display: flex
      flex-wrap: wrap
      justify-content: flex-start
      align-items: flex-start
      & > li
        width: calc( 100% / 4 )
        flex: 0 0 25%
        margin-bottom: 30px
        +r(1439)
          width: calc( 100% / 4 )
        img
          display: block
          position: relative
          margin: 0 0 20px
          +r(1439)
            margin: 0 0 20px 0
        h5
          a
            font-style: normal
            font-weight: 700
            font-size: 18px
            line-height: 117%
            letter-spacing: 0.02em
            color: #3C3C3B
            margin: 0 0 10px 0
            +h
              text-decoration: underline
        & > ul
          list-style-type: disc

          li
            color: rgba(135, 140, 152, 1)
            +tr(0.25s)
            +h
              color: rgba(60, 60, 59, 1)
            & + li
              margin-top: 5px
            a
              display: inline-block
              font-style: normal
              font-weight: 400
              font-size: 14px
              line-height: 115%
              letter-spacing: 0.02em
              color: rgba(135, 140, 152, 1)
              +line(1)
              +tr(0.25s)
              +h
                color: rgba(60, 60, 59, 1)
                text-decoration: underline

  &__submenu-grid
    display: block
    position: relative
    padding: 20px 40px
    box-sizing: border-box
    height: calc(100% - 92px)
    overflow-x: hidden
    overflow-y: auto
    scrollbar-color: #fff transparent
    scrollbar-width: thin!important
    -moz-appearance: none !important

    &::-webkit-scrollbar
      width: 6px

    &::-webkit-scrollbar-track
      background-color: transparent

    &::-webkit-scrollbar-thumb
      background-color: #fff

    ul
      list-style-type: none
      padding: 0
      margin: 0
      display: grid
      grid-template-columns: repeat(4, 1fr)
      grid-gap: 25px
      +r(1439)
        grid-template-columns: repeat(3, 1fr)
      li
        +r(1439)
          &:nth-child(5n + 1)
            display: none
        a
          display: flex
          justify-content: flex-start
          align-items: center
          img
            max-width: 90px
            max-height: 65px
            margin-right: 5px
          h5
            font-style: normal
            font-weight: 400
            font-size: 16px
            line-height: 117%
            color: #3C3C3B
            +line(1)

          +h
            h5
              text-decoration: underline


  &__submenu-brands
    border-top: 1px solid rgba(#fff, 0.3)
    max-width: 100%
    scrollbar-color: #fff transparent
    scrollbar-width: thin!important
    -moz-appearance: none !important
    overflow-x: auto
    overflow-y: hidden

    &::-webkit-scrollbar
      height: 6px

    &::-webkit-scrollbar-track
      background-color: transparent

    &::-webkit-scrollbar-thumb
      background-color: #fff
  
    & > ul
      list-style-type: none
      padding: 0
      margin: 0
      display: flex
      justify-content: start
      align-items: center
      & > li
        display: flex
        justify-content: center
        align-items: center
        width: 164px
        flex: 0 0 164px
        border-right: 1px solid rgba(#fff, 0.3)

        +h
          background: rgba(255, 255, 255, 0.3)

        a
          display: flex
          justify-content: center
          align-items: center
          flex-wrap: wrap
          height: 85px

          img
            display: block
            max-width: 117px
            max-height: 100%

.hero-slider
  display: block
  position: relative
  width: 100%
  background-color: #dadfe3

  .swiper-nav
    position: absolute
    top: 90px
    left: 550px
    +r(1439)
      left: 370px
    +r(1199)
      left: 30px
    +r(767)
      top: 45px

    .swiper-button-prev,
    .swiper-button-next
      border-color: #fff
      +h
        border-color: #000


.hero-banner
  display: flex
  align-items: center
  height: 500px
  +r(767)
    align-items: flex-start
    height: 568px
  &__desc
    display: flex
    justify-content: center
    align-items: flex-start
    flex-direction: column
    position: relative
    width: 100%
    color: #fff
    padding-left: 530px
    margin-top: 45px
    z-index: 3
    +r(1439)
      padding-left: 350px
    +r(1199)
      width: 50%
      padding-left: 15px
      margin-top: 0
    +r(767)
      width: 100%
      margin-top: 90px

  &__title
    display: block
    position: relative
    font-style: normal
    font-weight: 700
    font-size: 46px
    line-height: 120%
    color: #3C3C3B
    margin: 0 0 15px 0
    max-width: 550px
    +r(1439)
      font-size: 40px
    +r(1365)
      font-size: 36px
    +r(1199)
      font-size: 30px
    +r(767)
      font-size: 24px
      margin: 0 0 10px 0
  &__text
    font-style: normal
    font-weight: 400
    font-size: 16px
    line-height: 140%
    color: #3C3C3B
    max-width: 550px
    margin: 0
    +r(1365)
      max-width: 50%
    +r(1199)
      font-size: 14px
    +r(767)
      font-size: 13px
      max-width: 100%
  &__more
    display: block
    position: relative
    margin-top: 25px
    .button
      padding: 0 30px
  &__img
    display: block
    position: absolute
    left: 0
    right: 0
    width: 100%
    height: 100%
    background-size: cover
    background-position: right bottom
    background-repeat: no-repeat
    +r(1679)
      background-position: right 20% bottom
    +r(1359)
      background-position: right 40% bottom
    +r(1199)
      background-position: right 25% bottom
    +r(767)
      background-position: right bottom
      background-size: 861px 250px