.lk-menu
  display: block
  position: relative
  width: 262px
  +r(1365)
    width: 100%
    display: flex
    justify-content: center
    align-items: center
    margin: 20px 0 30px
  +r(767)
    margin-bottom: 20px
  &::before
    content: ''
    display: block
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: rgba(#000, 0.8)
    z-index: 1
    opacity: 0
    pointer-events: none
    +tr(0.25s)
  &.is-active
    &::before
      opacity: 1
    & > *
      z-index: 2
  &__box
    display: block
    position: relative
    background: #FFFFFF
    border-radius: 30px
    padding: 21px 0

    +r(1365)
      border-radius: 10px

      position: absolute
      left: 50%
      top: calc(100% + 100px)
      opacity: 0
      pointer-events: none
      transform: translateX(-50%)
      z-index: 5
      width: 260px
      box-shadow: 0px 9px 20px 0px #00000017
      +tr(0.35s)

    +r(414)
      width: 290px

    &.is-show
      top: calc(100% + 10px)
      opacity: 1
      pointer-events: all
      +tr(0.35s)

  &__message-counter
    font-weight: 700
    font-size: 10px
    line-height: 100%
    letter-spacing: 0.04em
    color: #FFAE00
    display: flex
    align-items: center
    justify-content: center
    border: 1px solid #FEE5B4
    border-radius: 50%
    width: 22px
    height: 22px
    margin-right: auto
    margin-left: 8px
    

  &__btn
    display: none
    justify-content: space-between
    align-items: center
    position: relative
    height: 50px
    background: #fff
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #3C3C3B
    border: none
    border-radius: 10px
    width: 260px
    padding: 0 17px 0 30px
    +r(414)
      width: 290px
    +ico()
      color: #3C3C3B
      font-size: 8px
    &::before
      content: ''
      display: block
      position: absolute
      top: 50%
      left: -1px
      transform: translateY(-50%)
      width: 4px
      height: 20px
      background: $accent
      box-shadow: 1px 0px 4px 1px rgba(#FFD600, 0.3)
    &.is-active
      border-color: $accent
      +ico()
        transform: rotate(180deg)
    +r(1365)
      display: flex
  &__list
    list-style-type: none
    padding: 0
    margin: 0
    li
      display: block
      position: relative
      border-bottom: 1px solid #F8F9FA
      &:last-child
        border-bottom: 0
      &::before
        content: ''
        display: block
        position: absolute
        top: 50%
        left: 0
        transform: translateY(-50%)
        width: 6px
        height: calc( 100% - 10px )
        background: $accent
        box-shadow: 1px 0px 4px 1px rgba(#FFD600, 0.3)
        opacity: 0
        +tr(0.25s)
        +r(1365)
          display: none
      +h
        &::before
          opacity: 1
      &.is-active
        &::before
          opacity: 1
        a
          font-style: normal
          font-weight: 400
          font-size: 14px
          line-height: 100%
          letter-spacing: 0.02em
          color: #3C3C3B
          +ico()
            color: #3C3C3B
      a
        display: flex
        justify-content: space-between
        align-items: center
        height: 45px
        padding: 0 0 0 32px

        font-style: normal
        font-weight: 400
        font-size: 14px
        line-height: 100%
        color: #3C3C3B
        +tr(0.25s)

        +ico()
          width: 40px
          height: 45px
          flex: 0 0 40px
          display: flex
          justify-content: center
          align-items: center
          color: #E1E3EA
          font-size: 11px
          +tr(0.25s)