.container-xl
  max-width: 1746px

.container-lg
  max-width: 1430px

.container-text
  max-width: 1640px

.mt-1
  margin-top: 10px!important

.mb-5
  margin-bottom: 30px!important

.g-5, .gx-5
  --bs-gutter-x: 30px
  +r(1199)
    --bs-gutter-x: 20px
  +r(767)
    --bs-gutter-x: 15px

.g-5, .gy-5
  --bs-gutter-y: 30px
  +r(1199)
    --bs-gutter-y: 20px
  +r(767)
    --bs-gutter-y: 15px

.invalid-feedback
  font-style: normal
  font-weight: 400
  font-size: 10px
  line-height: 13px
  color: #FA8A96

.form-group
  position: relative

.form-control
  height: 45px
  padding-left: 15px
  border: 1.5px solid #E1E3EA
  border-radius: 30px

  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 18px
  color: #3C3C3B

  background: none

  +tr(0.25s)

  +placeholder()
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 18px
    color: #9FA4B0
    +tr(0.25s)

  &:focus
    border: 1.5px solid #D2D6DF
    +placeholder()
      opacity: 0

  +hh
    border: 1.5px solid #D2D6DF
    background-color: #fff

textarea.form-control
  height: 90px
  resize: none
  padding-top: 15px
  border-radius: 20px

.form-control.is-valid,
.was-validated .form-control:valid
  border: 1.5px solid #E1E3EA

.form-control.is-invalid,
.was-validated .form-control:invalid,
.form-control.is-invalid:focus,
.was-validated .form-control:invalid:focus
  border-color: #FA8A96