[class^="ico_"],
[class*=" ico_"]
  display: inline-flex
  align-items: center
  line-height: 1em

html,
body
  font-family: $font-base
  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 140%
  color: #3C3C3B


h1, h2, h3, h4, h5, h6
  font-family: $font-head
  font-weight: 700
  color: #3C3C3B

.section-title
  font-style: normal
  font-weight: 700
  font-size: 28px
  line-height: 120%
  letter-spacing: 0.02em
  text-transform: uppercase
  color: #3C3C3B
  margin: 0 0 20px 0
  +r(767)
    font-size: 20px

.title
  display: block
  position: relative
  font-style: normal
  font-weight: 700
  font-size: 26px
  line-height: 130%
  letter-spacing: 0.02em
  color: #3C3C3B
  margin: 0 0 20px 0

.nowrap
  white-space: nowrap